import React from 'react'
import {Routes, Route} from 'react-router-dom';
import { CharLanding } from './wikiPageContent/CharLanding';
import { YuFamily } from './wikiPageContent/YuFamily';
import { MorganYu } from './wikiPageContent/MorganYu';
import { AlexYu } from './wikiPageContent/AlexYu';
import { CathYu } from './wikiPageContent/CathYu';
import { WilliamYu } from './wikiPageContent/WilliamYu';
import { RileyYu } from './wikiPageContent/RileyYu';
import { GarfieldL } from './wikiPageContent/GarfieldL';
import { GrantLockwood } from './wikiPageContent/GrantLockwood';
import { Mikhaila } from './wikiPageContent/Mikhaila';
import { DanielleSho } from './wikiPageContent/DanielleSho';
import { TrevorY } from './wikiPageContent/TrevorY';
import { LukaGolubkin } from './wikiPageContent/LukaGolubkin';
import { AbigailFoy } from './wikiPageContent/AbigailFoy';
import { DayoIgwe } from './wikiPageContent/DayoIgwe';
import { December } from './wikiPageContent/December';
import { January } from './wikiPageContent/January';
import { MathiasKohl } from './wikiPageContent/MathiasKohl';
import { WaltherDahl } from './wikiPageContent/WaltherDahl';
import { JorgenThorstein } from './wikiPageContent/JorgenThornstein';
import { Kennedy } from './wikiPageContent/Kennedy';
import { HansKelstrup } from './wikiPageContent/HansKelstrup';
import { Peter } from './wikiPageContent/Peter';
import { AaronIngram } from './wikiPageContent/AaronIngram';
import { WilliamMitchell } from './wikiPageContent/WilliamMitchell';



const Description = () => {
  return (
    <>
      <Routes>

        <Route path='/'>
          
          <Route index={true} element={<CharLanding/>}/>

          <Route path="yu_family">
            <Route index={true} element={<YuFamily/>} />
            <Route path='morgan_yu' element={<MorganYu/>} />
            <Route path='alex_yu' element={<AlexYu/>} />
            <Route path='catherine_yu' element={<CathYu/>} />
            <Route path='william_yu' element={<WilliamYu/>} />
            <Route path='riley_yu' element={<RileyYu/>} />
          </Route>

          <Route path='garfield_langly' element={<GarfieldL/>} />
          <Route path='grant_lockwood' element={<GrantLockwood/>} />
          <Route path='mikhaila_ilyushin' element={<Mikhaila/>} />
          <Route path='danielle_sho' element={<DanielleSho/>} />
          <Route path='trevor_j_young' element={<TrevorY/>} />
          <Route path='luka_golubkin' element={<LukaGolubkin/>} />
          <Route path='abigail_foy' element={<AbigailFoy/>} />
          <Route path='dayo_igwe' element={<DayoIgwe/>} />
          <Route path='december' element={<December/>} />
          <Route path='january' element={<January/>} />
          <Route path='mathias_kohl' element={<MathiasKohl/>} />
          <Route path='walther_dahl' element={<WaltherDahl/>} />
          <Route path='jorgen_thorstein' element={<JorgenThorstein/>} />
          <Route path='john_f_kennedy' element={<Kennedy/>} />
          <Route path='hans_kelstrup' element={<HansKelstrup/>} />
          <Route path='peter' element={<Peter/>} />
          <Route path='aaron_ingram' element={<AaronIngram/>} />
          <Route path='will_mitchell' element={<WilliamMitchell/>} />

        </Route>

      </Routes>
    </>
  )
}

export default Description