import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Cargo = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/cargoBay.gif')} alt="Cargo" />
        </div>
        <h2 className={styles.header}>
            Omówienie
        </h2>
        <p>
          Cargo Bay to miejsce, w którym wszystkie przesyłki przychodzą i odpływają na pokładzie Talosa I w Prey (2017). <br /> <br />
          Wszystkie przesyłki przychodzą i przechodzą przez ładownię i jest to punkt zborny funkcjonariuszy ochrony pod dowództwem Sarah Elazar.
        </p>
        <div className={styles.imageBox} >
            <img className={styles.image} src={require('../../../../../../grafika/cargoEx.png')} alt="Cargo" />
        </div>
    </>
  )
}
