import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const EthericMimic = () => {
  return (
    <>
        <h2 className={styles.header}>
          Omówienie
        </h2>
        <p>
          Eteryczne mimiki to, podobnie jak ich bardziej pospolici mimiczni kuzyni, małe, czworonożne, przypominające pająki organizmy Tyfon, które mogą kamuflować się, by wyglądać jak obiekty w ich otoczeniu, takie jak krzesła biurowe lub kosze na śmieci, wyskakując na gracza, gdy wędruje zbyt blisko. Eteryczny mimik ma dodatkową właściwość emitowania szkodliwych chmur eterycznych po śmierci, potencjalnie zadając obrażenia od korozji graczowi i jego wyposażeniu, a także innym bytom.
        </p>
    </>
  )
}
