import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Bridge = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/bridge.gif')} alt="Mostek" />
        </div>
        <h2 className={styles.header}>
            Omówienie
        </h2>
        <p>
          Mostek Talosa I to główne centrum kontroli na pokładzie Talosa I podczas Prey (2017).

 <br /> <br />
          Znajduje się pod Arboretum. Jego głównym priorytetem jest upewnienie się, że Talos I pozostaje na kursie wokół orbity księżyca. Działa również jako wieża kontrolna dla przychodzących i odlatujących wahadłowców i ładunków. Mostem dowodził Jada Marks jako kapitan Talosa I. To tutaj gracz zmierzy się z zakończeniem samej gry, zainicjuje sekwencję samozniszczenia lub rozstawi Prototypowy Nadajnik Nullwave. Alex Yu i January będą tu na ostatniej misji.


        </p>
        <div className={styles.imageBox} >
            <img className={styles.image} src={require('../../../../../../prey-graphics/bridge1.png')} alt="Mostek" />
        </div>
    </>
  )
}
