import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Talos = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/Talos_I_Station_Logo.gif')} alt="Talos I" />
        </div>
        <div className={styles.imageBox} style={{width: "64%"}}>
            <img className={styles.image} src={require('../../../../../../grafika/taloscon.png')} alt="Talos I" />
        </div>
        <h2 className={styles.header}>
            Omówienie
        </h2>
        <p>
          Talos I to zaawansowana stacja kosmiczna i laboratorium krążące wokół Księżyca w roku 2035 i jest głównym miejscem wydarzeń z Prey (2017). <br /><br />
          Talos I składa się z różnych sekcji, które powstawały przez lata od Programu Kletka w latach 60-tych. Różnią się zarówno konstrukcją, jak i użytecznością, służąc do badań, produkcji, logistyki czy obsługi załogi. Retrofuturyzm i Art Deco to najbardziej widoczne style wystroju na stacji. Komputery szpulowe i podobne przestarzałe technologie nadal znajdują się na stacji, ale od tego czasu zostały odnowione, aby pasowały do ​​​​nowoczesnych standardów lub zostały wyczyszczone, aby służyły jako dekoracja. <br /> <br />

Sama stacja została zakotwiczona wokół Jednostki Przechowawczej Kletka, zanim została rozbudowana najpierw w pionie, a następnie w poziomie za pomocą modułowych pokładów. W swojej ostatecznej formie Talos I składa się z trzech poziomów, każdy z zatoką kapsuł ratunkowych oraz pokładem lub sektorem połączonym z windą równolegle do systemu użyteczności publicznej.
        </p>
        <h2 className={styles.header}>
          Historia
        </h2>
        <h2 className={styles.smallHeader}>
          Wczesny etap
        </h2>
        <p>
          W 1958 roku Związek Radziecki wystrzelił satelitę Vorona-1, który wszedł na orbitę w LaGrange Point-2 między Ziemią a Księżycem. Po awarii komunikacji z satelity szefowie radzieckiego programu kosmicznego wysłali kilku kosmonautów w celu zbadania sprawy. Tam napotkali wrogie pozaziemskie formy życia, które przystąpiły do ​​​​zabicia kosmonautów. Vorona 1 została zatopiona, a nagranie z incydentu zostało oznaczone jako tajemnica państwowa i zakopane. <br /> <br />

W 1963 r. radziecki premier Nikita Chruszczow ogłosił program współpracy z amerykańskim prezydentem Johnem F. Kennedym w celu powstrzymania istot pozaziemskich, które nazwano Tyfonem. Doprowadziło to do powstania programu Kletka, w ramach którego Stany Zjednoczone i Związek Radziecki zbudowały stację kosmiczną wokół organizmów Typhon w celu ich zbadania i powstrzymania. Jednak nieudana próba zamachu w Dallas w Teksasie przekonałaby Kennedy'ego do przejęcia programu Kletka, odcinając Sowietów. W następnych dziesięcioleciach Amerykanie rozbudowali stację jako ośrodek badawczo-rozwojowy, aby uzyskać wgląd w organizmy Tyfona. Wiele z tego zostało podjętych w ramach Projektu Axiom, a Stany Zjednoczone utrzymywały stację kosmiczną aż do katastrofalnego incydentu w Pobeg w 1980 roku, kiedy kilka Tyfonów przedarło się przez zabezpieczenia i zabiło zespół badawczy. Konsekwencje tej tragedii, w połączeniu z dalszymi cięciami budżetowymi amerykańskiego programu kosmicznego pod rządami Reagana, spowodowały, że stacja została opuszczona.


        </p>
        <h2 className={styles.smallHeader}>
          TranStar
        </h2>
        <p>
          W 2030 r. Korporacji TranStar udało się przejąć własność opuszczonej stacji kosmicznej i rozpocząć przekształcanie jej w wysokiej klasy centrum technologii informacyjnej oraz ośrodek badawczo-rozwojowy, przemianowany na Talos I. Chociaż pozornie jest to centrum korporacyjne i laboratorium, prawdziwa misja TranStar miał wznowić badania nad organizmami Typhon w celu stworzenia różnych urządzeń psychotronicznych, w tym Typhon Neuromods, wyspecjalizowanych optogenetycznych modyfikacji neuronów, które pozwalają ludziom posiadać i używać zdolności przeczących fizyce, takich jak sami kosmici. <br /><br />
          Pomimo niebezpieczeństw związanych z badaniami nad organizmami Typhon, TranStar kontynuował swoje wysoce nieetyczne badania nad organizmami, wykorzystując ludzi do testów za pośrednictwem swojego Wydziału Psychotroniki - w celu osiągnięcia zysku z Neuromodów, a jednocześnie „przedefiniowania co to znaczy być człowiekiem”.
        </p>
    </>
  )
}
