import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Operators = () => {
  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../grafika/operatorsDesign.png')} alt="Schemat" />
        </div>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../grafika/operators.png')} alt="Schemat" />
        </div>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../grafika/chassis.png')} alt="Schemat" />
        </div>
        <h3 className={styles.smallHeader}>
          Operatorzy
        </h3>
        <p>
          Operatorzy to roboty spotykane na Talosie I i Pyteasie podczas gry Prey and Prey: Mooncrash. <br /><br />
          Obsługują Talosa I w bardziej żmudnych zadaniach: lekarze, inżynierowie, naukowcy, sekretarki itp. Jednak od czasu wybuchu obcych Tyfonów na stacji oni, podobnie jak wielu innych mieszkańców stacji, padli ofiarą wynikającego z tego chaosu z niektóre z nich zostały uszkodzone lub opętane przez Tyfona.<br /><br />
        </p>
        <h3 className={styles.smallHeader}>
          Wygląd
        </h3>
        <p>
          Operatorzy to roboty o kształcie zbliżonym do trapezu, z których wychodzą małe roboty, które mogą lewitować dzięki technologii opartej na teorii pola Reyesa. Zostały one zaprojektowane do wykonywania podrzędnych zadań na stacji, takich jak naprawa kombinezonów załogi stacji i przywracanie jej zasobów umysłowych poprzez wstrzykiwanie im środków nootropowych, a także diagnozowanie i leczenie ich obrażeń. Operatorzy wojskowi są wyposażeni w ofensywną broń laserową.
        </p>
    </>
  )
}
