import React, { useRef, useState } from 'react';
import { NavLink } from 'react-router-dom';
import styles from './SubMenu.module.scss'
import DropdownSubmenu from './subcomponents/DropdownSubmenu';

import interfaceAudio from '../../../../../sounds/interface.wav' 
const audio = new Audio(interfaceAudio)

const SubMenu = ({ item }) => {
    const [subnav, setSubnav] = useState(false)
    const listRef = useRef()

    // const removeDropdowns = () =>{
    //     const listElems = listRef.current.parentElement.children;
    //     for (let elem of listElems){
    //         if(elem.hasAttribute("data-dropdown")){
    //             console.log(elem);
    //             elem.remove()
    //         }
    //     }
    // }

    const handleClick = () => {
        // removeDropdowns()
        item.subNav && setSubnav(!subnav)
        audio.play()
    }

  return (
    <>
        <NavLink ref={listRef} className={styles.mainNavElem} onClick={handleClick} to={item.path}>
            {item.title}
        </NavLink>
        {subnav && item.subNav.map((item, index) => {
            return (
                <DropdownSubmenu item={item} key={index}></DropdownSubmenu>
            )
        })}
    </>
  )
}

export default SubMenu