import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const ApexTendril = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "35%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/tendril.png')} alt="Wić" />
        </div>
        <h2 className={styles.header}>
            Omówienie
        </h2>
        <p>
          Wić apex jest częścią większego gatunku Apex w Prey (2017). <br /><br />
          Wici Apex to wrogie wyrostki Apex, które pojawiają się na stacji Talos I pod koniec Prey. Jeśli Morgan spróbuje zbliżyć się do wici, natychmiast spróbują je zaatakować, uderzając je. Morgan może chwilowo zniszczyć wici, ale wkrótce powrócą one z pełnym zdrowiem.
        </p>
        <h2 className={styles.header}>
            Strategia
        </h2>
        <p>
          Wić można łatwo zniszczyć jednym strzałem z dowolnej broni. Z tego powodu wąsy stanowią niewielkie zagrożenie dla Morgana i można je łatwo ominąć.
        </p>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/tendrilConc.png')} alt="Wić" />
        </div>
    </>
  )
}
