import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Nightmare = () => {
  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/nightmareConc.png')} alt="Cystoid" />
        </div>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/nightmare.png')} alt="Cystoid" />
        </div>
        <h2 className={styles.header}>
          Omówienie
        </h2>
        <p>
          Koszmar to wrogi organizm Typhon, który można spotkać na Talosie I i Pyteasie podczas wydarzeń w Prey (2017) i Prey: Mooncrash. <br /> <br />
          Koszmary są największymi, najsilniejszymi i najniebezpieczniejszymi ze wszystkich tyfonów, poza wierzchołkiem. Stworzone przez Tkaczy Koszmary to wyspecjalizowane Upiory zaprojektowane wyłącznie w celu polowania i eksterminacji Morgan Yu. Pierwszy Koszmar zacznie śledzić gracza po uzyskaniu dostępu do Arboretum, a inne będą pojawiać się losowo przez całą grę. Gdy Koszmar zostanie powiadomiony o obecności gracza, rozpocznie się zadanie, w którym gracz musi zabić Koszmar lub unikać go przez 3 minuty. Unikanie Koszmaru zapobiegnie jego ponownemu pojawieniu się przez 20 minut, a zabicie go przedłuży czas potrzebny do pojawienia się Koszmaru do 30 minut. Gracz musi przerwać bezpośrednią linię wzroku, aby skutecznie uniknąć Koszmaru, a po udanym uniknięciu Koszmaru po raz pierwszy, January wspomni o prototypie satelity używanego w Psychotronice, rozpoczynając zadanie Mieszane sygnały. Koszmar upuści 30 Organów Tyfona i 1 Rdzeń Tyfona za każdym razem, gdy zostanie zabity, przy czym ten ostatni wymaga sekcji zwłok, aby go zabrać. <br /> <br />
        </p>
        <h3 className={styles.smallHeader}>
          Agresja i warunki spotkania
        </h3>
        <p>
          Po uzyskaniu dostępu do Arboretum Koszmar może pojawiać się w dowolnym miejscu poza Zewnętrzem Talosa I i G.U.T.S. Jeśli gracz zainstalował jakieś neuromody związane z Tyfonem, January wskaże je jako przyczynę nagłego pojawienia się stwora. Pomimo stwierdzenia w styczniu, że przyczyną pojawienia się Koszmaru jest instalacja Neuromodu, sama instalacja Neuromodu nie ma możliwości kontrolowania, czy Koszmar będzie cię ścigał podczas gry, a zamiast tego ma dramatyczny wpływ na to, kiedy Koszmar się pojawi. <br /> <br />

Koszmary będą się pojawiać coraz bardziej agresywnie, w zależności od tego, czy gracz zainstaluje neuromody oparte na Typhonie podczas rozgrywki. W środowisku, w którym gracz instaluje minimalne ulepszenia Typhona lub nie instaluje ich wcale, Nightmare zazwyczaj czeka, aż przejdziesz na inny poziom, i od czasu do czasu da znać o swoim wyglądzie spostrzegawczemu graczowi, lekko potrząsając stacją i powodując słaby, ale słyszalny ryk. Jeśli jednak gracz wyda zbyt wiele neuromodów na ulepszenia Tyfona w zbyt krótkim czasie, Koszmar z dużym prawdopodobieństwem pojawi się na tym samym poziomie, odradzając się bez ostrzeżenia i ścigając gracza niemal natychmiast. Zaleca się oszczędne kupowanie zdolności Neuromod opartych na Typhonie i bycie gotowym na spotkanie z Koszmarem za każdym razem, gdy umieszczasz punkty w drzewku ulepszeń. <br /> <br />

W Prey: Mooncrash Koszmary pojawią się również na Pyteasie, gdy Peter wystarczająco rozwinie się w swojej pracy. W przeciwieństwie do Koszmarów napotkanych na Talosie I, te Koszmary będą nieustannie wędrować po bazie, dopóki nie zostaną zniszczone, bez limitu czasowego na zniknięcie. Co dziwne, istnieje również Koszmar zamknięty w jednostce przechowawczej w Pyteas Labs, którego nie można uwolnić, ale można go przeskanować za pomocą Psychoskopu. Prawdopodobnie został stworzony w niewoli przez uwięzionego Tkacza, ponieważ schwytanie Koszmaru jest praktycznie niemożliwe, ale nie wiadomo, dlaczego żadne dane na jego temat nie zostały udostępnione Psychotronice na Talosie I.


        </p>
        <h2 className={styles.header}>
          Design
        </h2>
        <p>
          Koszmary wyglądają jak znacznie większe, mniej humanoidalne wersje fantomów. Jak prawie wszystkie Tyfony, jest to masa macek i poruszającej się materii. Posiadają sześć białych plamek, które mogą łączyć się w jedną, które służą jako prowizoryczne oczy. To, co wydaje się być głową Koszmaru, to w rzeczywistości cztery do sześciu żuchw, które połączyły się, tworząc kształt dużego dzioba. Te żuchwy rozejdą się i rozejdą, gdy wyzwoli swój główny atak, potrójną salwę naprowadzanych kul, której towarzyszy głośny ryk. Jest w stanie przecisnąć się przez standardowe drzwi i wydaje się przyjmować humanoidalny kształt tylko do celów bojowych.
        </p>
    </>
  )
}
