import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Apex = () => {
  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/apexAttack.png')} alt="Apex" />
        </div>
        <h2 className={styles.header}>
            Omówienie
        </h2>
        <p>
          Apex jest największym dotychczas obserwowanym Tyfonem, który atakuje i zaczyna konsumować stację kosmiczną Talos I pod koniec Prey. Apex jest przywoływany przez strukturę znaną jako „Coral” zbudowaną przez Tyfona, która według Alexa Yu jest środkiem komunikacji dla Tyfona. Apex nie może zostać zabity bezpośrednio przez Morgana, ale jego macki można tymczasowo odeprzeć. <br /><br />

Istnieją tylko dwa sposoby na pokonanie Apex: zniszczenie Talosa I poprzez samozniszczenie, co skutkuje zniszczeniem również Apex, lub zainstalowanie prototypowego nadajnika fal zerowych na serce koralu i zdetonowanie go z mostu, aby pokonać Apex bez niszczenia Talos I.


        </p>
    </>
  )
}
