import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const WaltherDahl = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "40%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/dahl.png')} alt="Walther Dahl" />
        </div>
        <div className={styles.imageBox} style={{width: "20%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/dahl2.png')} alt="Walther Dahl" />
        </div>

        <h2 className={styles.header}>
          Walther Dahl
        </h2>
        <p>
          Walther Dahl jest liderem zespołu naprawczego na Talos I i pojawia się w Prey (2017) jako główny antagonista.
        </p>

        <h2 className={styles.header}>
          Historia
        </h2>
        <h3 className={styles.smallHeader}>
          Tło
        </h3>
        <p>
          Dahl stacjonował na orbicie Ziemi. William Yu nakazał Dahlowi zabezpieczyć całą „technologię i badania na poziomie usuwania kobaltu” z Talosa I i eksterminować cały personel ludzki.
        </p>
        <h3 className={styles.smallHeader}>
          Prey (2017)
        </h3>
        <p>
          Agenci Dahla pojawią się jako pierwsi w Arboretum, gdy Kaspar przechwyci transmisję z Morgan Yu do Alexa Yu. Podczas The Repo Man Morgan będzie następnie szukał miejsca pobytu Dahla i jego partnera Kaspara w zatoce wahadłowej, zanim zdecydują się stawić mu czoła w sprawie podtrzymywania życia z powodu jego ultimatum poprzez odcięcie tlenu w jednej z trzech lokalizacji (Psychotronics, Cargo Bay lub Arboretum). <br /> <br />

Jeśli Dayo Igwe zostanie uratowany, chce, aby Morgan pozbawił Dahla przytomności za pomocą ogłuszającego pistoletu podtrzymującego życie, gdzie następnie Morgan i Igwe przeprowadzą operację na Dahlu w Oddziale Neuromodów, aby usunąć jego wspomnienia o otrzymaniu rozkazów Williama Yu, co doprowadzi Dahla i innych ocalałych na Talosie I, aby razem uciekli w zatoce wahadłowców. Jeśli Morgan nie uratował Igwe i pozbawił Dahla przytomności, zmierzą się z nim ponownie w Arboretum, gdzie będzie próbował zhakować kod Alexa Yu (Stanie się tak również, jeśli Morgan najpierw zaatakuje Kaspara i zniszczy go lub zhakuje, zamiast iść do podtrzymywania życia skonfrontować się z Dahlem). Morgan może albo ponownie pozbawić go przytomności, albo go zabić. Dopóki Dahl i Igwe przeżyją, opcja ucieczki przez Shuttle Bay będzie dostępna, ale jeśli jeden z nich umrze przed zabiegiem neurotomii, ucieczka przez Shuttle Bay nie będzie dostępna.


        </p>
        <h2 className={styles.header}>
          Kooperacja z Dahlem
        </h2>
        <p>
          Jeśli Morgan zabija innych pracowników stacji (dokładnie 36), po przybyciu Dahla obaj mogą zawrzeć umowę. Dahl proponuje, że jeśli Morgan zabije pozostałych pracowników stacji, bezpiecznie zabierze Morgana z powrotem na Ziemię. W rezultacie będzie znacznie bardziej przyjazny dla Morgana, często chwaląc ich za „inicjatywę” usunięcia personelu stacji i „ułatwienia mi pracy”. <br /> <br />

Po tym, jak Morgan wykona zadanie, Dahl poprosi ich, aby przybyli do zatoki wahadłowców, aby mogli wyjść. Po wejściu do zatoki Morgan znajduje grupę przyjaznych operatorów wojskowych krążących po okolicy. Następnie Dahl ujawnia, że ​​kłamał na temat ratowania Morgana i nakazuje Operatorom atak. Morgan jest zmuszony zaangażować wszystkich Operatorów, a także Dahla.


        </p>
        <h2 className={styles.header}>
          Osobowość i umiejętności
        </h2>
        <p>
          Doświadczenie Dahla w walce elektronicznej i infiltracji w połączeniu z bezwzględnym pragmatyzmem czyni go idealnym narzędziem do rozwiązywania trudnych problemów. Dahl preferuje towarzystwo Operatorów, którzy nie kwestionują poleceń. Jego celność jest taka, że ​​​​jest w stanie zabić stycznie biegnącego Morgana jednym strzałem z niezmodyfikowanego pistoletu z tłumikiem, niezależnie od integralności lub zdrowia tego ostatniego.
        </p>
    </>
  )
}
