import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Exterior = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/exteriorBanner.png')} alt="Zewmątrz Talosa I" />
        </div>
        <h2 className={styles.header}>
            Omówienie
        </h2>
        <p>
          Zewnątrz Talosa I to lokacja występująca w grze Prey (2017).

<br /> <br />
          To ogromna, szeroko otwarta przestrzeń, którą Morgan musi przemierzać w celu zaliczania różnych pobocznych questów lub szybkiego przedostawania się do innych części stacji. Jeśli Morgan wyleci zbyt daleko poza pierścienie orbitujące stację, promieniowanie zacznie się gromadzić i ostatecznie zabije gracza. W czasie Prey (2017) kilka miejsc na stacji zostało naruszonych, co spowodowało ich całkowitą ekspozycję na kosmos.<br /> <br />

Jeden z gigantycznych billboardów krążących wokół Talosa I może zostać zhakowany, aby ostrzec ludzi, że kapsuły ratunkowe nie działają.<br /> <br />

Ponieważ tkacze unikają tworzenia w mikrograwitacji istot z nogami, takich jak zjawy i koszmary, tacy wrogowie nie pojawiają się w tym środowisku. Wydaje się, że nie ma tu również mimików i poltergeistów.<br /> <br />

Po przybyciu Apex zewnętrzna część Talosa I będzie na stałe niedostępna, a wszystkie misje, które wymagały udania się na zewnętrzną część, natychmiast zakończą się niepowodzeniem.


        </p>
        <div className={styles.imageBox} >
            <img className={styles.image} src={require('../../../../../../prey-graphics/exterior1.png')} alt="Zewmątrz Talosa I" />
        </div>
    </>
  )
}
