import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Weaver = () => {
  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/weaverConc1.png')} alt="Tkacz" />
        </div>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/weaverConc2.png')} alt="Tkacz" />
        </div>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/weaver.png')} alt="Tkacz" />
        </div>
        <h2 className={styles.header}>
          Omówienie
        </h2>
        <p>
          Tkacze to zaawansowana forma Mimika Tyfona, która w nieznany sposób została wybrana do przekształcenia w Tkacza. Nawet przy normalnej grawitacji Tkacze posiadają moc unoszenia się, prawdopodobnie za pomocą środków psychicznych. <br /> <br />

Tkacze wzięli swoją nazwę od produkcji Koralu, świecącej substancji podobnej do pajęczyny, która wisi w powietrzu wszędzie tam, gdzie znajdują się Tkacze. Tkacze Talosa I są chronione przez luminescencyjną żółtą zasłonę. Tak długo, jak Tkacz jest przez nią chroniony, ataki fizyczne będą blokowane, a w umyśle atakującego wywołana zostanie psychiczna fala strachu, efekt znany jako Psychiczna Reakcja. Tkacze Pyteasa, którym brakuje produkujących korale „przędzi” ich kuzynów z Talosa I, nie posiadają tej zasłony. Teoretyzuje się zatem, że zdolność Tkaczy Backlash jest powiązana z ich zdolnością do syntezy korala.<br /> <br />

Tkacze są w stanie ożywić łuski zabitych ludzi, zamieniając je w Upiory. Podczas gdy ludzie zabici i wyczerpani przez Mimiki są zazwyczaj wybierani do tego celu, każde ciało może wystarczyć, czego dowodem jest Ładownia, kiedy Tkacz przekształca szczątki Enocha Kounevy w Upiora. Tkacze mogą wybrać, czy fantomizować zwłoki, czy nie, chociaż nie wiadomo, jakie czynniki skłaniają ich do powstrzymania się od fantomizacji. W rzadkich przypadkach próba stworzenia Upiora przez Tkacza może zakończyć się niepowodzeniem, co skutkuje mutacją znaną jako Poltergeist.<br /> <br />

Tkacze są znani (ale nie pokazani) z tworzenia innych organizmów Tyfonów, takich jak telepaci, technopaci i koszmary, aby chronić ekologię Tyfonów i realizować swoje cele. Jeśli zostaną zaatakowani, Tkacze tworzą gniazda cystoidów jako pasywną obronę lub pułapki i odradzają cystoidy bezpośrednio w celu obrony osobistej. Tkacze nie przekształcają jednak normalnych mimików w większe mimiki, które dostarczają im energii potrzebnej do stworzenia wyżej wymienionych bytów.


        </p>
        <h2 className={styles.header}>
          Design
        </h2>
        <p>
          Tkacze przypominają trochę Mimików, z których się przeobrażają. Tkacze wyglądają jak skupione macki, 3 na górze i kilka na spodzie. Zwykle są pokryte żółtą powłoką energii psychicznej. „Głowa” tkacza, którą można zlokalizować, lokalizując fioletową poświatę emitowaną przez nią, gdy jest pod wpływem umysłu, podobno zawiera narząd przędzalniczy (nie można go zebrać), którego używa do tkania koralowców i wytwarzania tarczy luzowej. Ta dysza przędzalnicza nie jest zaangażowana w genezę Tyfona.
        </p>
        <h2 className={styles.header}>
          Strategie
        </h2>
        <p>
          Wydaje się, że złota tarcza Tkacza łagodzi większość obrażeń zadanych mu przy pierwszym trafieniu. Następnie tarcza zostanie złamana tylko na krótkie okno, podczas którego Weaver będzie próbował uciec, odlatując od gracza. W połączeniu z umiejętnością Backlash, która zaburza kontrolę nad kamerą gracza, oraz niezliczonymi Cystoidami, które Tkacz jest w stanie odrodzić, sprawia, że ​​jest to niezwykle trudny przeciwnik do przygwożdżenia. <br /> <br />

Jeśli jednak gracz ma aktywowany chipset lunety osłaniającej strach, zostanie on osłonięty przed efektem strachu zastosowanym po uruchomieniu jego zdolności Backlash. W rezultacie sprawia to, że Tkacze są śmiesznie łatwi do pokonania, ponieważ nie mają dużo zdrowia, gdy ich tarcza jest opuszczona. Kilka strzałów z pistoletu zwykle może ich zniszczyć. Jeśli Tarcza Strachu nie jest dostępna do dodania, picie jakiegokolwiek alkoholu również usunie efekt strachu, kosztem potykania się i niewyraźnego widzenia, chyba że zainstalowany jest Chipset Psychoaktywnego Wzmacniacza, który wyłącza negatywne skutki alkoholu w bezruchu pozwalając mu zanegować strach, a także dać dodatkowe obrażenia w walce wręcz. W związku z tym dobrym pomysłem jest trzymanie kilku butelek w ekwipunku na takie przypadki.<br /> <br />

Wiązka Q, nawet minimalnie ulepszona, jest bardzo skuteczna przeciwko nim, ponieważ jej brak bezpośrednich obrażeń nie rozerwie tarczy Tkacza i nie spowoduje, że zada on Backlash, co również zachęci go do pozostania i walki zamiast ucieczki, czyniąc go o wiele łatwiejszy cel. Uważajcie tylko na mnóstwo Cystoidów, które tworzy Tkacz, dzięki czemu najlepiej walczyć z dystansu, najlepiej z wysokiego punktu obserwacyjnego. Q-beam to jedyna broń, za pomocą której można zabić tkacza bez wywoływania luzów. Paralizator może unieruchomić tkacza i nie wywołuje luzu, ponieważ nie zadaje tkaczowi obrażeń.<br /> <br />
Walka wręcz wydaje się być wyjątkowo nieskuteczną strategią bez pomocy psychicznego inhibitora, takiego jak Psychoshock lub Nullwave. Tarcza Tkacza, jeśli graczowi uda się zbliżyć do niego niezauważonym i nieskrępowanym przez Cystoidy, zniweczy każde pierwsze trafienie wymierzone w niego, co sprawi, że ataki z ukrycia będą niezwykle trudne do wykonania. To pozostawia ich bezbronnych tylko w krótkim okresie, podczas którego gracz nadal będzie musiał radzić sobie z Cystoidami, które spawnuje, i mieć nadzieję, że tkacz po prostu nie unosi się poza zasięgiem, co jest całkiem w stanie zrobić.<br /> <br />

Tkacze są w stanie w każdej chwili stworzyć Upiory z ciał. Jako środek ostrożności przenieś wszelkie możliwe ciała z wyprzedzeniem, usuń je z drogi, aby zmniejszyć szansę na utworzenie kopii zapasowej. Jeśli zobaczysz Tkacza w trakcie tworzenia Upiora z ciała, możesz strzelić do Tkacza. Nawet jeśli nie trafisz go bezpośrednio, uderzenie w punkt wystarczająco blisko przyciągnie jego uwagę i sprawi, że ponownie upuści ciało. Chociaż tracisz element zaskoczenia, oznacza to, że Tkacz nie będzie miał dodatkowego wspierającego go Upiora. Ponadto w tradycji mówi się, że Tkacze są w stanie zainwestować dodatkową energię w tworzenie potężniejszych fantomów, takich jak eteryczne, termiczne i woltaiczne odmiany fantomów.<br /> <br />

Tkacze są nieco trudni do walki w stanie nieważkości, ponieważ cystoidy, które odradzają, są znacznie bardziej zwrotne, a przez to niebezpieczne.<br /> <br />

W Prey: Mooncrash Tkacze nie mają zwykłej tarczy, co znacznie ułatwia ich pokonanie i zabicie. Są wyjątkowo podatni na obrażenia zadawane przez Shotguna lub Psychostatic Cutter. Jeśli graczowi uda się zakraść za Tkacza i zbliżyć się do niego, zabicie Tkacza zajmie tylko kilka ataków, zanim będzie mógł zareagować i uwolnić Cystoidy lub uciec.




        </p>
    </>
  )
}
