import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const VoltaicPhantom = () => {
  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/voltPhan.png')} alt="Fantom" />
        </div>
        <h2 className={styles.header}>
          Omówienie
        </h2>
        <p>
          Fantom woltaiczny to znacznie silniejsza i bardziej niebezpieczna wersja zwykłego Phantoma. Może strzelać piorunami elektrycznymi w swoje cele i może porazić prądem wszystko, co się do niego zbliży, a także wyłączając wszystkie niestandardowe bronie, których używa Morgan (pistolet z tłumikiem, klucz francuski i strzelba nie są naruszone). Te fantomy mogą również przywoływać i ładować elektrycznie pobliskich Mimów przez około osiem sekund, dzięki czemu są w stanie wyłączyć elektronikę na czas ich szarży. Pierwszy Voltaic Phantom spotyka się w komorze Psychotronics Live Exam. Co ciekawe, są jedynym typem fantomu, który nie może się wypaczać (poruszać się bardzo szybko w krótkich seriach); poruszają się tylko chodząc.
        </p>
        <h2 className={styles.header}>
          Design
        </h2>
        <p>
          W większości wyglądają identycznie jak normalny Phantom, z wyjątkiem tego, że mają dodatkowe kryształy wystające z ich pleców, które emitują elektryczność.
        </p>
        <h2 className={styles.header}>
          Strategia
        </h2>
        <p>
          Gracz powinien uważać na Voltaic Phantoms wokół wybuchowych kanistrów, ponieważ ich pasywne wytwarzanie energii elektrycznej spowoduje eksplozję kanistrów. Wersja Voltaic jest również odporna na Paralizator zakłócający. Należy również pamiętać o powierzchniach przewodzących, takich jak woda lub metal, gdy stykają się z nimi fantomy galwaniczne. Nadajnik fali zerowej wyłącza aktywne i quasi-aktywne zdolności elektryczne fantomu galwanicznego na około 20 sekund, a ładunek EMP na około 10 sekund. Pamiętaj, aby zachować dystans podczas skradania się do nich. Ich niszczycielska aura galwaniczna jest aktywna nawet wtedy, gdy nie są świadomi twojej obecności. <br /><br />

Ze względu na ich niezdolność do zmiany warp/phantom, utrzymywanie odpowiedniej odległości powinno być dość łatwe, a walka w zwarciu jest niewskazana, chyba że wcześniej wyłączy się elektryczną aurę.<br /><br />

Ich elektryczne podmuchy dystansowe mają zdolność naprowadzania (w porównaniu z żadnym podmuchem kinetycznym), więc unikanie ich podmuchów dystansowych wymaga większej ostrożności.
        </p>
    </>
  )
}
