import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Mimic = () => {
  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/mim.png')} alt="Mimik" />
        </div>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/mim2.png')} alt="Mimik" />
        </div>
        <h2 className={styles.header}>
          Omówienie
        </h2>
        <p>
          Są bardzo zwinne, potrafią czołgać się, skakać, robić salta, krótko przywierać do ścian, a także zręcznie unikają ciosów kluczem francuskim. Żywią się, ciasno owijając swoje wyrostki wokół swoich ofiar i wbijając jeden wyrostek w gardło ofiary, szybko pożerając ich składniki odżywcze i świadomość, a następnie ulegają rozszczepieniu, dzieląc się na czterech nowych Mimów. Mogą jednak zabić Morgana lub inne postacie graczy, żądląc ich, i są znani z tradycji, aby użądlić niektórych innych naśladowców, co prowadzi do przemiany ofiar w tkaczy. Morgan może uzyskać moc naśladowania Mimika za pomocą neuromodów. W grze mimiki stwarzają podejrzenie co do dowolnego obiektu w pokoju.
        </p>
        <h2 className={styles.header}>
          Strategia
        </h2>
        <p>
          Istnieje chipset (Wykrywanie mimiki 1), który pozwala Psychoskopowi wykryć mimikę, gdy przybierze ona formę przedmiotu. Użyj tego, aby sprawdzić obszar pod kątem mimików przed przejściem. Pamiętaj jednak, że pierwsza wersja chipa detekcyjnego nie wykrywa Greater Mimics. Jeśli nie masz chipsetu, najlepszym sposobem na znalezienie ukrytych Mimów jest przeszukanie pokoju w poszukiwaniu małych i średnich duplikatów lub przedmiotów, które wydają się być nie na miejscu (co ciekawe, Mimiki przebrane za apteczki zwykle nie mieć wokół siebie apteczki). Pamiętaj, że jeśli do podniesienia wymaga użycia dźwigni, prawdopodobnie nie jest to Mimik. Jednak oskryptowany mimik w Psychotronice zamienia się w skrzynię Leverage I zaraz po zdobyciu psychoskopu, zaobserwowano innego mimika powielającego wózek narzędziowy, który wymaga dźwigni 2 do poruszania się, a większy mimik najwyraźniej naśladował ciężki przedmiot również. Kręć się po okolicy i uderzaj kluczem francuskim we wspomniane obiekty. Spróbuj naładować atak za pomocą klucza, aby skoczyć na każdego mimika, którego znajdziesz, ponieważ w pełni naładowany atak kluczem może zwykle wystarczyć do jednego strzału Mimika. <br /> <br />

Strategia mimika opiera się na zasadzce i sile w liczebności, jako że jest to najliczniejszy typ Tyfona, nierzadko można znaleźć 2-4 mimików na jednym obszarze. W przypadku braku żywego celu wolą przybrać postać nieożywionego obiektu, aby zaczaić się na jego pojawienie się. Pierwszą rzeczą, o której Morgan musi pamiętać, jest to, że Mimicy mogą przybierać postać tylko obiektu, który już jest obecny, więc jeśli zobaczą więcej niż jeden taki sam obiekt w dziwnym miejscu, najlepszą opcją jest próba wyeliminowania obu obiektów natychmiast.<br /> <br />

Mimic spróbuje przestraszyć Morgana, powodując, że w panice marnują cenną amunicję lub wytrzymałość, ale jeśli są w stanie zachować spokój, nie stanowią większego zagrożenia.<br /> <br />

Jeśli jednak naśladowcy mogą żywić się zwłokami i rozmnażać się z tego, co z nich uzyskali, to nie ma sensu, aby TranStar karmił ich żywymi ochotnikami, chyba że żywi ludzie są bardziej pożywni dla naśladowców niż martwi. Ponadto nie odnotowano, aby naśladowcy żywili się jakimikolwiek nieożywionymi, nieludzkimi organizmami, a January twierdzi, że Tyfon żywi się świadomością, której nie posiadają zwłoki ani wiele organizmów innych niż ludzie znalezionych na Talosie I lub Pyteaszu . Powyższe roszczenie jest obecnie badane poprzez zamknięcie jednego mimika z dwoma świeżymi zwłokami w pustej kabinie bezpieczeństwa i czekanie. Poprzedni test dotyczył dwóch mimików i jednego świeżego zwłok, i, co zaskakujące, trzeci mimik pojawił się później razem z nimi, chociaż nie wiadomo, czy mimik już tam był, czy też wślizgnął się przez jedną z niezapieczętowanych dziur .<br /> <br />

Strzelby, pistolety i większość mocy są w stanie z łatwością poradzić sobie z mimikami, ponieważ nie są one szczególnie wytrzymałe w porównaniu z Widmami i innymi bardziej niebezpiecznymi wrogami, aw przeciwieństwie do tych mimików całkowicie nie mają ataku dystansowego, co oznacza, że ​​strzelba i klucz są skuteczne przeciwko tym wrogom.<br /> <br />

Jeśli gracz ma w miarę przyzwoity cel, powinien być w stanie zabić mimika jednym trafieniem ze strzelby. Mimiki są niezwykle wrażliwe, gdy przybierają formę obiektu, a w pełni naładowany atak klucza powinien zabić każdego ukrywającego się Mimika jednym strzałem. Pamiętaj, że powinno to być używane tylko przeciwko zwykłym mimikom, a jeśli wykryjesz większego mimika w ukryciu, najlepiej zamiast tego użyć strzelby, działa GLOO lub EAT.


        </p>
    </>
  )
}
