import React from 'react';

import styles from './About.module.scss'

const About = () => {
    return ( 
        <div className={styles.desc}>
            <img className={styles.gamelogo} src={require('../../../../grafika/prey-logo.png')} alt="Game logo" />
            <p>Prey to strzelanka FPS opracowana przez Arkane Studios i wydana przez Bethesda Softworks. Akcja gry toczy się w niedalekiej przyszłości alternatywnej rzeczywistości, na stacji kosmicznej Talos I najechanej opanowanej przez tajemniczego Tyfona. Gra została wydana na systemy Microsoft Windows, PlayStation 4 i Xbox One 5 maja 2017 roku.</p>
            <div className={styles.gameInfo}>
                <h2>Rozgrywka</h2>
                <br />
                <p>
                    Prey to strzelanka FPS z elementami RPG i silną narracją, inspirowaną System Shock 2. Gracz wciela się w postać Morgana Yu, człowieka na pokładzie stacji kosmicznej z licznymi wrogimi obcymi. Gracz może wybrać określone atrybuty Morgana i dokonać wyborów, które mają wpływ na elementy fabuły gry. Aby przetrwać, gracz kontroluje Morgana, który zbiera i używa broni i zasobów na pokładzie stacji, aby pokonać Tyfona. Prey nie jest grą z otwartym światem, ale oferuje rozgrywkę na otwartych poziomach, podobną do innej gry studia, Dishonored.
                </p>
                <br />
                <img src={require('../../../../grafika/mimic_swarm.jpg')} alt="Mimic attack" />
                <br />
                <h2>Fabuła</h2>
                <br />
                <p>
                    W marcu 2032 roku Morgan Yu zostaje zwerbowany przez swojego brata Alexa, aby dołączyć do zespołu badawczego TranStara na Talos I. Jednak podczas wykonywania serii testów przed wyjazdem na stację jeden z nadzorujących lekarzy zostaje zaatakowany przez Tyfona i Morgan traci przytomność. Morgan budzi się ponownie w swoim mieszkaniu, ale szybko odkrywa, że to tylko symulowane środowisko. Jest rok 2035, a oni właściwie już od trzech lat mieszkają na Talosie I. January kontaktuje się z Morganem, jest to operator, sztuczna inteligencja, która twierdzi, że została stworzona przez Morgana. January ostrzega Morgana, że Tyfon złamał zabezpieczenia i przejął stację, zabijając większość załogi. Ujawnia również Morganowi, że przez ostatnie trzy lata testowali neuromody, a Morgan nieustannie je dodawał i usuwał. Jednak efektem ubocznym usunięcia neuromodu jest to, że użytkownik traci wszystkie wspomnienia zdobyte po zainstalowaniu tego neuromodu, co wyjaśnia utratę pamięci Morgana. January następnie twierdzi, że Morgan zbudował go, aby pomóc mu zniszczyć Talosa I, niszcząc zarazem Tyfona i wszystkie jego badania. Tymczasem Alex kontaktuje się z Morganem i błaga ich, aby zamiast tego zbudowali specjalne urządzenie - emiter fali negacyjnej, które zniszczy Tyfona, ale pozostawi stację nietkniętą, powołując się na to, że ich badania są zbyt cenne, by je stracić.
                </p>
                <br />
                <img src={require('../../../../grafika/red_suit.jpg')} alt="Morgan w czerwonym kombinezonie" />
                <br />
                <p>
                    Gdy Morgan przedziera się przez stację, spotyka innych ocalałych, którym może pomóc. Następnie Alex zleca Morganowi przeskanowanie tyfonowego „Koralu” rosnącego wokół stacji i odkrywa, że buduje on jakiś rodzaj sieci neuronowej. Jednak ich próby zbadania sieci neuronowej zostają przerwane, gdy Rada Dyrektorów TranStar dowiaduje się o naruszeniu przechowalni i wysyła ekipę sprzątającą, aby wyeliminować zarówno Tyfona, jak i załogę stacji, która przeżyła. Po wyeliminowaniu ekipy sprzątającej Alex dalej analizuje dane i dochodzi do wniosku, że Tyfon wysyła sygnał w przestrzeń kosmiczną, aby coś wezwać. Następnie przy stacji pojawia się gigantyczny Tyfon (zwany Apex) i zaczyna pożerać Talosa I. Morgan ma wtedy wybór, czy aktywować sekwencję samozniszczenia stacji, czy zbudować urządzenie emiter, aby pokonać Tyfona.
                </p>
                <br />
                <p>
                    Jeśli Morgan zdecyduje się aktywować urządzenie, wszystkie Tyfony na Talosie I zostaną zniszczone, a stacja pozostanie nienaruszona, aby umożliwić dalsze badania neuromodów. Jeśli Morgan zdecyduje się aktywować samozniszczenie, cała stacja eksploduje, niszcząc przy tym cały Tyfon. Opierając się na wcześniejszych wyborach Morgana, Morgan może albo znaleźć sposób na ucieczkę ze stacji, albo też zostaje uwięziony i ginie w eksplozji.
                </p>
                <br />
                <img src={require('../../../../grafika/purple_typhon.jpg')} alt="Tyfon" />
                <br />
                <p>
                    W scenie po napisach, Morgan budzi się w laboratorium i dowiaduje się, że nie jest prawdziwym Morganem, ale schwytanym Tyfonem, któremu wszczepiono wspomnienia Morgana, aby nauczyć go ludzkich emocji i empatii. Sugeruje się, że prawdziwy Morgan nie żyje, a Tyfon już rozprzestrzenił się na Ziemi. Alex i jego asystenci Operatora, znani jako Dayo Igwe, Michaiła Iljuszyn, Danielle Sho i Sarah Elazar, następnie oceniają „Morgana” na podstawie wyborów, jakich dokonał w trakcie gry. Jeśli Igwe stwierdził, że „Morgan” nie okazał ludzkiej empatii, Alex niszczy go i zaczyna eksperyment od nowa. Jeśli Igwe stwierdzi, że „Morgan” rzeczywiście okazywał ludzką empatię, niezależnie od tego, czy był średni, czy wysoki, Alex odpuszcza, po czym może przyjąć jego ofertę zostania ambasadorem między gatunkami lub zabić go i dołączyć do reszty swego gatunku niszcząc ludzkość.
                </p>
                <h2>Rozwój</h2>
                <br />
                <p>
                    Pierwotnie, Human Head Studios tworzyło Prey 2, kontynuację gry wideo Prey z 2006 roku, ale prace nad nią zostały ostatecznie anulowane. Bethesda Softworks, która nadal była właścicielem własności intelektualnej, zaoferowała Arkane Studios pracę nad tytułem. Zaproponowali, że nie będą pracować nad prawdziwą kontynuacją, ale nad „przeobrażeniem gry”.
                </p>
                <br />
                <p>
                    Kolejna gra Prey została ogłoszona 12 czerwca 2016 roku przez Bethesdę na konferencji prasowej E3. Rozwój był prowadzony przez dyrektora generalnego i dyrektora Arkane Studios, Raphaela Colantonio, i jego zespół w Austin w Teksasie. Nad projektem pracował również doświadczony programista Chris Avellone. Więcej informacji o grze, w tym fabuła i rozgrywka, zostało ujawnionych na QuakeCon 2016.
                </p>
                <br />
                <p>
                    Według Seamusa Byrne'a z CNet, zwiastun zapowiedzi pokazywał bohatera gry w czymś w rodzaju „kosmicznej wersji horroru Świstaka”. Wiceprezes Bethesdy ds. marketingu, Pete Hines, wyjaśnił, że nowa gra nie zawiera żadnych elementów z anulowanej Prey 2 poza graczem walczącym z kosmitami. Hines wyjaśnił, że Arkane dogłębnie ocenił tytuł i zbudował wokół niego nową grę, nazywając ją bardziej grą psychologiczną niż horrorem.
                </p>
                <br />
                <p>
                    W czerwcu 2018 roku gra otrzymała tryb przetrwania, nową grę plus oraz DLC Mooncrash i Typhon Hunter.
                </p>
            </div>
        </div>
    );
}

export default About;