import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Mikhaila = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/mikhaila.png')} alt="Mikhaila" />
        </div>
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/mikhaila2.png')} alt="Mikhaila" />
        </div>

        <h2 className={styles.header}>
          Mikhaila Ilyushin
        </h2>
        <p>
          Mikhaila Ilyushin jest głównym inżynierem systemowym pracującym na pokładzie Talosa I. Pojawia się w Prey (2017).
        </p>

        <h2 className={styles.header}>
          Historia
        </h2>
        <h3 className={styles.smallHeader}>
          Tło
        </h3>
        <p>
          Mikhaila marzyła o eksploracji gwiazd, tak jak jej ojciec kosmonauta przed nią, ale musiała porzucić te marzenia, gdy zdiagnozowano u niej rzadką chorobę neurologiczną znaną jako parapleks. Kiedy jej ojciec zniknął w tajemniczy sposób, zrobiła wszystko, aby go odnaleźć, a jej śledztwo ostatecznie doprowadziło ją do operacji TranStar na Talosie I. Mając nadzieję, że dowie się, co stało się z jej zaginionym rodzicem, zinfiltrowała TranStar, przyjmując stanowisko Głównego Inżyniera Systemów. Musiała sfałszować swoją dokumentację medyczną, ponieważ jej stan wykluczyłby Michajłę z pracy na pokładzie stacji kosmicznej. <br /> <br />

Po dołączeniu do Talosa I Iljuszyn nawiązał krótki związek z Morganem Yu. W pewnym momencie Morgan dowiedział się o stanie Michaiła, chociaż nie ujawnili jej stanu zdrowia administratorom stacji. Mikhaila założyła, że ​​Morgan zerwał z nią z powodu jej stanu, co było fałszywym, ale wygodnym założeniem z perspektywy Morgana. W rzeczywistości Morgan uznał, że rozsądnie będzie zakończyć sprawy, zanim nadchodzące próby Neuromodów wymażą ich wspomnienia, powodując, że całkowicie zapomną o Michaiły i swoich uczuciach do niej.


        </p>
        <h3 className={styles.smallHeader}>
          Prey (2017)
        </h3>
        <p>
          Na początku gry na terminalu Morgana pojawia się wiadomość e-mail od Mikhaili, która jest zirytowana faktem, że Morgan wydaje się ją ignorować, odkąd się rozstali.<br /> <br />

Mikhaila po raz pierwszy kontaktuje się bezpośrednio z Morganem, gdy wchodzą do elektrowni. Została znaleziona trzymająca się życia na poziomie reaktora, jej parapleksa zajęła jej nogi i płuca. Wyłom w kadłubie w jej biurze odciął ją od leków, ale mówi Morganowi, że mogą znieść trwającą blokadę stacji, wykonując twardy restart rdzenia reaktora. Po tym, jak Morgan przeprowadzi reset, drzwi i szyby grawitacyjne ponownie zaczną działać, a Morgan może EVA do biura Mikhaili i odzyskać jej lekarstwo, jeśli tak zechcą.<br /> <br />

Jeśli zostanie uratowana, Mikhaila uda się do biura Morgana. Porozmawia z January i Dayo Igwe (jeśli żyje) i przyniesie zapasy dla Morgana. Później mówi Morganowi, że dowiedziała się, że jej ojciec był jednym z rosyjskich „ochotników” przywiezionych do Talosa I na próby badawcze na ludziach. Daje Morganowi numer identyfikacyjny swojego ojca i błaga ich, aby udali się do Deep Storage, gdzie, jak wierzy, można znaleźć dowody na to, co się z nim stało. Dr Igwe prywatnie skontaktuje się z Morganem przez TranScribe i doradzi im, że najlepiej dla wszystkich będzie, jeśli dziennik audio nie dotrze do Mikhaili. Po wysłuchaniu akt dowiadują się, dlaczego: ojciec Michajły został zabity za eksperyment z Tyfonem, a Morgan osobiście zezwolił na jego egzekucję. Morgan musi wtedy zdecydować, czy zniszczyć dowody, czy przesłać je Michaiły. Po otrzymaniu danych Michaiła jest wściekły, ale z czasem uspokaja się i zdaje sobie sprawę, że Morgan nie jest tą samą osobą, co kiedyś. Jest zdecydowana wydostać ich wszystkich żywych ze stacji, chociaż nalega, aby dane wracały z nią na Ziemię bez względu na koszty dla Morgana lub TranStar.<br /> <br />

Pod warunkiem, że Morgan żyje, a Morgan wykonał cel Ubezwłasnowolnienie Dahla przed przygotowaniem samozniszczenia stacji, Mikhaila wezwie ich do zatoki wahadłowców w celu ewakuacji. Jeśli January nadal działa, udaje Morgana na TranScribe i mówi jej i innym ocalałym, aby odeszli bez nich. Mikhaila waha się, podejrzewając, że coś jest nie tak, ale January kłamie i zapewnia ją, że istnieje działający prom ewakuacyjny, którego Morgan zamierza użyć.


        </p>
        <h2 className={styles.header}>
          Osobowość i umiejętności
        </h2>
        <p>
          Ilyushin jest wykwalifikowanym inżynierem systemowym, co zapewniło jej pozycję na Talosie I. Była w stanie ukryć urządzenie podsłuchowe w czujniku drzwi. Iljuszyn bardzo troszczyła się o swojego ojca i była gotów zaryzykować własne życie, aby dowiedzieć się, co się z nim stało.
        </p>
    </>
  )
}
