import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const SzybGraw = () => {
  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../grafika/gravshaft.png')} alt="Szyb" />
        </div>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../grafika/gravshaft2.png')} alt="Szyb" />
        </div>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../grafika/gravshaft3.png')} alt="Szyb" />
        </div>
        <h2 className={styles.header}>
            Omówienie
        </h2>
        <p>
          Wały grawitacyjne to windy grawitacyjne używane do przemierzania Talosa I w Prey (2017). <br /> <br />
          Wały grawitacyjne to windy, które mogą kierować grawitację w górę iw dół, aby umożliwić użytkownikom szybką zmianę poziomów. Pojawiają się w sąsiednich parach skierowanych w górę iw dół. Pozwalają personelowi Talos I i innym podmiotom (np. mimikom, fantomom, operatorom itp.) na bezpieczne podróżowanie w pionie w większości obszarów, ponieważ jedyna tradycyjna winda na Talos I łączy tylko Lobby Talos I, Arboretum i podtrzymywanie życia. <br /> <br />

Jest prawdopodobne, że technologia wałów grawitacyjnych opiera się na tym samym przełomie fizycznym, który „utrzymuje buty mocno osadzone na włoskim marmurze holu Talosa I”, ponieważ zasadniczo manipulują lokalną sztuczną grawitacją.
        </p>
        <h2 className={styles.header}>
            Działanie
        </h2>
        <p>
          W obrębie pary szybów grawitacyjnych dostępny szyb jest pokryty pulsującymi zielonymi światłami, podczas gdy niedostępny jest pokryty pulsującymi czerwonymi światłami i znakiem „Nie wchodzić”. Wejście na czerwoną stronę skutkuje wycofaniem się. Dezaktywowane pary szybów grawitacyjnych są albo zepsute, jak ten w pobliżu holu Oddziału Neuromodów, zablokowane, jak te, które zaprowadzą cię do apartamentów kierowniczych w kwaterach załogi lub biura Alexa Yu w Arboretum, albo pozbawione zasilania z powodu lokalnych przerw w dostawie prądu. <br /> <br />

Zepsute wały grawitacyjne reaktywują się po naprawie, zablokowane po odblokowaniu ich kodami dostępu, a niezasilane po przywróceniu lokalnego zasilania. Alternatywnie możesz użyć działa GLOO, aby zbudować platformy do skalowania wewnątrz nieaktywnych szybów grawitacyjnych lub naśladować operatora i lewitować przez szyby. Jeśli nie zostaną usunięte przed reaktywacją szybu grawitacyjnego, platformy GLOO pozostaną tam, gdzie wylądowały i mogą stanowić przeszkodę podczas transportu. Chociaż można je usunąć, strzelając do nich z aktywowanych szybów z zewnątrz, najłatwiej jest je usunąć, gdy wały są dezaktywowane. <br /> <br />

Co ciekawe, fantomy galwaniczne, pomimo ich tendencji do powodowania, że ​​elektronika staje się i pozostaje niesprawna, gdy znajdują się w ich pobliżu, nie powodują awarii szybów grawitacyjnych.
        </p>
    </>
  )
}
