import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const EngineerOper = () => {
  return (
    <>
      <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/112 - GzI4CVm.png')} alt="Inżynier" />
        </div>
        <div className={styles.imageBox} style={{width: "39%"}}>
            <img className={styles.image} src={require('../../../../../../grafika/enginOper.png')} alt="Inżynier" />
        </div>
        <h2 className={styles.header}>
            Omówienie
        </h2>
        <p>
          Kobold 410 Inżynieria klasy Operator jest odmianą Operatorów, których można znaleźć na Talosie I. <br /> <br />
          Operator inżynieryjny przywraca pełną integralność skafandra gracza i jest odpowiednią alternatywą dla zestawu naprawczego skafandra. Podobnie jak inni operatorzy, dozowniki dla operatorów inżynieryjnych znajdują się w całym Talosie I, zwłaszcza na dolnych pokładach.
        </p>
        <h3 className={styles.smallHeader}>
          Opis (z broszury TranStaru)
        </h3>
        <p>
          Zaprojektowane z precyzją dla niebezpiecznej próżni kosmicznej, niewiele robotów użytkowych może dorównać operatorowi klasy inżynieryjnej TranStar Kobold 410. Wyposażony w dwa manipulatory GenSENSE i wymienne ramię narzędziowe z pełną gamą narzędzi, niewiele jest rzeczy, których operator inżynieryjny nie byłby w stanie naprawić. <br /> <br />

W przypadku cięższych prac każdy operator inżynieryjny jest standardowo wyposażony w systemy nawigacyjne OMNI klasy przemysłowej, zdolne do sprawnego manewrowania kontenerami ładunkowymi w warunkach zerowej grawitacji.
        </p>
    </>
  )
}
