import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const WilliamYu = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/williamYu.png')} alt="William Yu" />
        </div>
        <h2 className={styles.header}>
          William Yu
        </h2>
        <p>William Yu jest ojcem Alexa i Morgana Yu oraz przewodniczącym rady dyrektorów TranStar.</p>
        <h2 className={styles.header}>
          Osobowość i umiejętności
        </h2>
        <p>Nie pojawia się fizycznie w Prey (2017), ale istnieją dzienniki audio, w których dokłada wszelkich starań, aby chronić firmę i pośrednio nakazuje śmierć własnych dzieci.</p>
        <h2 className={styles.header}>
          Historia
        </h2>
        <h3 className={styles.smallHeader}>
          Tło
        </h3>
        <p>William był znakomitym chińskim neurobiologiem, który poślubił niemiecką bizneswoman, Catherine Yu. Oboje poczęli dwoje dzieci, Alexa i Morgana. W 2025 roku William założył firmę technologiczną o nazwie TranStar.</p>
        <h3 className={styles.smallHeader}>
          Prey (2017)
        </h3>
        <p>
          Gdy wiadomość o wybuchu tyfona dotarła do zarządu TranStar, William nakazał Waltherowi Dahlowi zabezpieczyć całą „technologię i badania na poziomie zezwolenia na kobalt” z Talosa I i eksterminować cały personel ludzki.
        </p>
        <h3 className={styles.smallHeader}>
          Prey: Mooncrash
        </h3>
        <p>
          Słychać go w głośniku w salonie.
        </p>
    </>
  )
}
