import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const TechLanding = () => {
  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/5 - VePcdoD.png')} alt="Schemat" />
        </div>
        <h2 className={styles.header}>
          Technologia
        </h2>
        <p>W grze technologia pełni kluczową rolę, wpływając zarówno na świat gry, jak i na samą rozgrywkę. Zaawansowane technologicznie urządzenia i systemy są integralną częścią świata gry, tworząc atmosferę futurystycznego otoczenia. Gracz musi wykorzystywać różne technologiczne narzędzia, takie jak drony, roboty i zaawansowane systemy bezpieczeństwa, aby rozwiązywać zagadki, pokonywać przeszkody i odkrywać tajemnice stacji kosmicznej, na której toczy się akcja.</p>
    </>
  )
}
