import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const ScienceOper = () => {
  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../grafika/scienceOper.png')} alt="Operator naukowy" />
        </div>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../grafika/scienceOper2.png')} alt="Operator naukowy" />
        </div>
        <div className={styles.imageBox} style={{width: "20%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/113 - 8Za1pxE.png')} alt="Operator naukowy" />
        </div>
        <h2 className={styles.header}>
            Omówienie
        </h2>
        <p>
          Operator klasy Nauka Sybil 495 to odmiana Operatorów, których można znaleźć na Talosie I. <br /><br />
          Operator naukowy przywraca graczowi pełne punkty Psi, ale tylko wtedy, gdy zostaną one wyczerpane poniżej 75%. Jest to alternatywa dla Psi Hypo.
        </p>
        <h2 className={styles.header}>
            Opis (Z broszury TranStaru)
        </h2>
        <p>
          W świecie postępu naukowego nic nie jest ważniejsze niż dokładność i bezpieczeństwo. Dlatego wszystkie laboratoria TranStar są wyposażone w operatorów klasy naukowej Sybil 495. <br /><br />
          Najnowszy w linii operatorów TranStar, operator naukowy łączy bezprecedensowy dostęp do naukowych baz danych TranStar z dwoma wysoce konfigurowalnymi ramionami użytkowymi, tworząc niezrównanego asystenta laboratoryjnego.
        </p>
    </>
  )
}
