import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const GreaterMimic = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/greaterMimic.png')} alt="Mimik" />
        </div>
        <h2 className={styles.header}>
          Omówienie
        </h2>
        <p>
          Większe mimiki to zmutowane mimiki, które rodzą się w taki sposób, w jaki są, z typowego procesu pączkowania mimicznego i dostarczają tkaczom energii potrzebnej do tworzenia różnych innych bytów tyfonów. Według badań TranStar mają zdolność przekształcania się w bardziej złożone obiekty, takie jak EAT i Operatorzy, ale nie zaobserwowano ich jeszcze w symulacjach.
<br /> <br />
Na Talosie I lepsi mimicy są bardziej odporni na traumę niż normalni mimicy i są jedynymi mimikami, które są w stanie zabić Morgana, wskakując mu na twarz i pożywiając się nim. Chociaż na Pyteas nie można znaleźć większych mimików dokładnie takich, jak te wspomniane wcześniej, niektóre z nich mutują i mogą aktywnie emitować płomienie lub łuki elektryczne lub biernie pozostawiać eteryczne chmury po śmierci. Te warianty księżycowe są również klasyfikowane jako większe mimiki, ponieważ mają z nimi więcej wspólnego niż typowe mimiki.


        </p>
        <h2 className={styles.header}>
          Strategia
        </h2>
        <p>
          Oprócz prymitywnych metod wykrywania mimiki, ukryte większe mimiki można zidentyfikować tylko po zainstalowaniu chipsetu psychoskopu Mimic Detection 2. Dobrą praktyką jest unieruchamianie lub zabijanie większych mimików na wczesnym etapie walki, ponieważ mogą one wskoczyć na twarz Morgana podczas bitwy, czyniąc go bezradnym wobec innych wrogów w okolicy, dopóki nie zostaną oszukani. Jeśli zaczepi się o twoją twarz, szybko i wielokrotnie klikaj [przycisk ataku], aż zostanie oderwany od twojej twarzy i wyrzucony. Dobrze wycelowany strzał ze strzelby zwykle wystarcza, aby zająć się tymi potężnymi mimikami.
        </p>
    </>
  )
}
