import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Chipsets = () => {
  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../grafika/chipsets.png')} alt="Chipsety" />
        </div>
        <h2 className={styles.header}>
            Omówienie
        </h2>
        <p>
          Chipsety to specjalne modyfikacje znalezione w Talosie I podczas Prey (2017) i symulacji Pyteasa podczas Prey: Mooncrash. <br /> <br />
          Są to urządzenia, które modyfikują mundur Morgana TranStar lub Psychoscope i pełnią różne funkcje. Morgan ma tylko określoną liczbę miejsc do wypełnienia chipsetami dla swojego ciała lub umysłu, a więcej miejsc można odblokować odpowiednio poprzez modyfikację skafandra i zdolności neuromodów psychotronicznych. <br /> <br />
          Nadal będziesz musiał ustalić priorytety, które z nich uważasz za bardziej wartościowe od innych. Ponadto chipsety są wybierane losowo w każdej iteracji, co oznacza, że ​​żaden żeton nie znajdzie się w tym samym miejscu dla dwóch graczy. Możliwe jest wielokrotne nabycie tego samego chipa, chociaż prawdopodobnie nie jest to zamierzone, ponieważ wiele kopii tego samego chipa nie może być wyposażonych w ten sam chipset. W grze Mooncrash nabywanie chipsetów, które już znajdują się na twojej obecnej postaci, doda Sim Points do wydania przed przystąpieniem do symulacji w przyszłych próbach. Chipsetów w symulacjach Petera nie można odzyskać ze zwłok ani fantomów poległych postaci graczy.
        </p>
    </>
  )
}
