import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const MimicsLanding = () => {
  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/mimicConc.png')} alt="Mimik" />
        </div>
        <h2 className={styles.header}>
          Mimiki
        </h2>
        <p>
          Mimiki (Typhon cacoplasmus) to odmiana tyfona napotkana na Talosie I w Prey (2017) i Pyteas w Prey: Mooncrash. <br /> <br />
          Mimiki to małe tyfony, które mogą kamuflować się, by wyglądać jak nieożywione obiekty w swoim otoczeniu, takie jak krzesła biurowe lub kosze na śmieci. Można je również zobaczyć, jak próbują ułożyć się w humanoidalne kształty, z ciekawości lub okazując zagrożenie. Jak wszystkie tyfony, składają się z błyszczącej, czarnej, półstałej substancji, która ma kształt centralnej masy z czterema mackami kończyn.
        </p>
    </>
  )
}
