import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Shotgun = () => {

  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../grafika/shotgun.png')} alt="S4"/>
        </div>
        <h2 className={styles.header}>Ogólne</h2>
        <p className={styles.info}>Throne S4 Tactical Combat Shotgun to broń typu pump-action do walki w zwarciu, którą można znaleźć na orbitalnej stacji badawczej Talos I podczas rozgrywki w Prey (2017) oraz na stacji Pyteas podczas Prey: Mooncrash.
        <br />
        S4 to standardowa strzelba typu pump-action z magazynkiem rurowym, który mieści 4 naboje, jeśli jest w magazynie, 10 nabojów, jeśli jest w pełni ulepszony. Naprawdę nie ma w tym nic szczególnego. Jedyną futurystyczną cechą jest licznik amunicji z boku.

Pierwsze miejsca, w których można znaleźć S4, znajdują się na ciele Nasha Underwooda w holu Talosa I w pobliżu wejścia do oddziału neuromodów (tylko wersja demonstracyjna) oraz na biurku w pobliżu pomieszczenia przetrzymywania w holu Talosa I.

</p>
        <p className={styles.info}>Nadajnik jest dobrym wyborem do walki z silniejszymi wariantami, takimi jak Telepata, Technopata czy Upiór, znacznie ułatwiając walkę z nimi. Trafienie Kontrolowanych Umysłem Ludzi Telepaty spowoduje, że stracą przytomność,

Jest również w stanie uniemożliwić Tkaczowi użycie jego zdolności Backlash, czyniąc go tym samym niezwykle wrażliwym i walczącym z nim bardzo łatwo. Wiadomo, że jest to skuteczna strategia na początku gry bez zakupionej umiejętności Psychoszok. Może również tymczasowo wyłączyć twoje własne zdolności, więc trzymaj się z dala od promienia wybuchu.</p>
<h2 className={styles.header}>
  Ulepszenia
</h2>
<p>The Throne S4 is a security weapon and therefore can only be fully upgraded via Weapon Upgrade Kits with the Gunsmith I and II Neuromods. On Pytheas, which lacks upgrade kits or their fabrication plans, Shotguns above Stock-grade will have randomized upgrades built into them.</p>
<h3 className={styles.smallHeader}>
  S4 z Pytheas
</h3>
<p>Strzelby na Pyteasie, podobnie jak większość innych broni, są podzielone według systemu poziomów rzadkości: podstawowy (biały), standardowy (zielony), zaawansowany (niebieski) i elitarny (złoty). Poziom rzadkości określa liczbę losowych ulepszeń otrzymywanych przez broń: standardowa 0, standardowa 4, zaawansowana 9 i elitarna 13. Wszystkie oprócz standardowych strzelb mogą również pojawiać się z efektami żywiołów, które zapewnią ich strzałom ogień, elektryczność, korozję lub psi -unieważniające zdolności, z szansą pozornie zwiększoną w zależności od ich poziomu rzadkości. Inną możliwością jest to, że pojawią się „ufortyfikowane” z maksymalną wytrzymałością 150%.</p>
<h2 className={styles.header}>Działanie</h2>
<p>Strzelba jest przydatna podczas konfrontacji z wrogami na małych przestrzeniach lub w starciu z trudnym przeciwnikiem. O ile nie napotkasz roju słabych wrogów, zwykle lepiej jest zarezerwować amunicję do strzelby na większe zagrożenia, takie jak Upiór, i użyć klucza lub pistoletu na mniejszych zagrożeniach, takich jak Mimik. Strzelba zadaje bardzo duże obrażenia z bliskiej odległości, ale cierpi z powodu szybkiego spadku obrażeń na dłuższych dystansach.

Przydatną kombinacją podczas używania strzelby przeciwko zagrożeniu, takiemu jak Upiór, jest unieruchomienie wroga działem GLOO, a następnie zbliżenie się na bliski dystans, aby zadać maksymalne obrażenia strzelbą.

Amunicja do strzelby jest rzadsza niż amunicja do pistoletu i droższa w produkcji za pomocą Fabrykatora. W efekcie utrzymanie strzelby w wystarczającej ilości amunicji, aby używać jej jako głównej broni, może być dość trudne i najlepiej zachować ją na potężniejszych wrogów. Pociski do strzelby zajmują jedno miejsce w ekwipunku na każde 48 przewożonych nabojów.

Strzelba może być niezwykle potężną bronią po pełnym ulepszeniu i zainstalowaniu neuromodów Firearms I i II. Zwiększa to siłę ognia aż do 98, co w większości przypadków pozwala mu zabić większość wrogów w ciągu trzech strzałów; jeśli zostanie zadane trafienie krytyczne, może nawet powalić Koszmar w zaledwie 2-3 strzałach z bliskiej odległości na normalnym poziomie trudności.

W Mooncrash możliwość zakupu naboi do strzelby za punkty symulacji przed faktycznym wejściem do gry oznacza, że ​​łatwo może ona stać się standardową bronią gracza. Zdolność do posiadania efektów elementarnych może nawet sprawić, że będzie to realna alternatywa dla posiadania mocy Tyfona, które normalnie dają te efekty: jest to szczególnie przydatne dla Vijaya Bhatii, który nie ma żadnej zdolności do odblokowywania Neuromodów Tyfona. Strzelby żywiołów nie są jednak nawet w przybliżeniu tak potężne, jak ich odpowiedniki mocy Tyfona, ponieważ ich obrażenia od żywiołów są stosowane jako dodatek do obrażeń Strzelby (na przykład +10 obrażeń od ognia), podczas gdy Supertermia zadaje wszystkie obrażenia jako ogień- pierwiastkowy.</p>
        <p>Wymagane materiały:
          <img className={styles.materialIcon} alt="Mineral icon" src={require('../../../../../../grafika/Mineral.png')} width="15" height="15"></img> 3,
          <img className={styles.materialIcon} alt="Synthetic icon" src={require('../../../../../../grafika/Synthetic.png')} width="15" height="15"></img> 2
        </p>
    </>
  )
}
