import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const ShuttleBay = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/shuttleBay.gif')} alt="ShuttleBay" />
        </div>
        <h2 className={styles.header}>
            Omówienie
        </h2>
        <p>
          Port wahadłowców to „port kosmiczny” do lądowania i odlatywania komercyjnych statków kosmicznych na pokładzie Talosa I w Prey (2017)

 <br /> <br />
          Początkowo w zatoce wahadłowej brakuje tlenu przed przybyciem Walthera Dahla, ale po przybyciu Dahla tlen powróci do normy. W głównym wątku wahadłowiec Dahla zacumuje w zatoce wahadłowców, a operatorzy wojskowi już roją się w tym miejscu. Hordy wojskowych operatorów mogą pokonać Koszmar w ciągu kilku sekund, ale mogą też zostać spaczone lub kontrolowane przez technopatę. <br /> <br />

Jeśli Morganowi uda się uratować Igwe i Dahla, wspomnienia Dahla o otrzymaniu rozkazu zabicia personelu Talosa I mogą zostać usunięte. Na obu końcach prom będzie uciekał z Talosa I, niezależnie od tego, czy zdecydujesz się rozmieścić prototypowy nadajnik fal zerowych, czy zniszczyć Talosa I. <br /> <br />

Wszyscy ocaleni zostaną uwzględnieni w Zatoce wahadłowej podczas ostatniego głównego zadania, którym jest ucieczka z Talosa I, głównie tylko Mikhaila, Igwe, Sarah i Aaron wraz z Dahlem na głównym pokładzie, podczas gdy pozostali ocaleni będą w ładunku wahadłowca. Zwykle zależy to od tego, ilu ocalałych uratujesz w trakcie gry.


        </p>
        <div className={styles.imageBox} >
            <img className={styles.image} src={require('../../../../../../grafika/shuttleBay.png')} alt="ShuttleBay" />
        </div>
    </>
  )
}
