import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Psychoscope = () => {
  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/14 - dWEssHF.png')} alt="Schemat" />
        </div>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../grafika/psart.png')} alt="Schemat" />
        </div>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../grafika/psart2.png')} alt="Schemat" />
        </div>
        <h2 className={styles.header}>
            Omówienie
        </h2>
        <p>
          "Psychoskop był przełomem. Poza neuromodami to najważniejsza technologia na Talosie I." <br /> <br />
          Psychoskop to technologia wynaleziona przez Morgana Yu w celu ułatwienia badania egzotycznych map neuronowych organizmów tyfonów, a tym samym odblokowania sekretów ich unikalnych zdolności. Tak jak rejestrator umiejętności w dziale neuromodów umożliwia skanowanie, digitalizację i udostępnianie ludzkich konektomów innym organizmom za pośrednictwem neuromodów, tak psychoskop rejestruje wzorce aktywacji nerwowej Typhona związane ze zdolnościami, przesyła je na serwery i umożliwia ich odblokowanie, pobrane i zainstalowane za pomocą neuromodów.
        </p>
        <h2 className={styles.header}>
            Wygląd
        </h2>
        <p>
          Psychoskop to podobne do hełmu urządzenie, które montuje jednorurowy czujnik z dwoma okularami, które można zsunąć, aby zakryć oczy użytkownika lub wsunąć je na czoło, gdy nie są używane. Z perspektywy użytkownika, gdy urządzenie jest w pozycji roboczej, widzi przez okrągłą soczewkę z wieloma małymi kolcami, które stale poruszają się po obwodzie, a pomiędzy nimi tworzą się laserowe linie. System oznacza zeskanowane cele i dostarcza danych dotyczących ich tożsamości, mocnych i słabych stron.
        </p>
        <h2 className={styles.header}>
            Funkcje
        </h2>
        <p>
          Psychoskop zastępuje funkcję zoomu, do której Morgan miał wcześniej dostęp: użycie przycisku zoomu po zdobyciu go spowoduje wyświetlenie nakładki psychoskopu. Tak długo, jak Morgan nosi psychoskop, każdy napotkany byt, który nie został jeszcze w pełni zbadany, jest skanowany, a dane na jego temat są dodawane do badawczej bazy danych. Skanować można tylko żywe stworzenia i nienaruszone roboty. Po zgromadzeniu wystarczającej ilości danych dla określonego organizmu, odblokowywana jest nowa zdolność neuromoda typu obcego. <br /> <br />
          Psychoskop ma również własną serię chipsetów, które głównie koncentrują się na poprawie mocy psi lub zapewnieniu odporności na żywioły, chociaż istnieją inne przydatne umiejętności, takie jak możliwość wykrywania mimików, gdy są one nadal przebrane, oraz poprawa szybkości skanowania psychoskopu. <br /> <br />
          Wyposażanie psychoskopu ma tę wadę, że uniemożliwia dostęp do koła broni, ponieważ sterowanie do jego podniesienia służy do powiększania i pomniejszania psychoskopu. uniemożliwia również użycie latarki, chociaż psychoskop ma swój własny efekt noktowizyjny, aby to zrekompensować. Ponadto, domyślnie psychoskop nie może wykryć ukrytych mimików lub poltergeistów, które aktualnie nie atakują. <br /> <br />
          Psychoskopy chronią również swoich użytkowników przed zdolnościami telepatów do kontrolowania umysłu; Brak noszenia przez Trevora Younga w Psychotronics spowodował, że uległ kontroli telepaty (i według twórców był przyczyną wybuchu tyfonu, który rozpoczyna grę). Nie wiadomo, czy Morgan chciał, aby psychoskopy zapewniały tę mentalną osłonę. Można jednak zauważyć, że drzwi do głównego laboratorium nie otwierają się przed nikim, chyba że jest on w posiadaniu psychoskopu. To oraz obecność dodatkowych oznakowań wokół wejścia do laboratorium, podkreślających znaczenie noszenia psychoskopu, sugeruje, że możliwości mentalnej osłony zostały uznane za wystarczająco ważne, aby włączyć je do bezpieczeństwa laboratorium.
        </p>
        <h2 className={styles.header}>
            Lokacje
        </h2>
        <h3 className={styles.smallHeader}>
          Prey (2017)
        </h3>
        <p>
          W Prey (2017) psychoskop po raz pierwszy znajduje się przy zwłokach na najniższym poziomie Psychotroniki. Wymagane jest wejście do głównego obszaru laboratorium.
        </p>
        <h3 className={styles.smallHeader}>
          Mooncrash
        </h3>
        <p>
          W Mooncrash psychoskop Riley Yu można najpierw znaleźć w jej biurze w Pyteas Labs, a następnie można go sfabrykować.
        </p>
        <p>Wymagane materiały:
          <img className={styles.materialIcon} alt="Mineral icon" src={require('../../../../../../grafika/Mineral.png')} width="15" height="15"></img> 2,
          <img className={styles.materialIcon} alt="Synthetic icon" src={require('../../../../../../grafika/Synthetic.png')} width="15" height="15"></img> 2
        </p>
    </>
  )
}
