import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Harvester = () => {
  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../grafika/harvesterConc.png')} alt="Harvester" />
        </div>
        <div className={styles.imageBox} style={{width: "20%"}}>
            <img className={styles.image} src={require('../../../../../../grafika/harvester.png')} alt="Harvester" />
        </div>
        <h2 className={styles.header}>
            Omówienie
        </h2>
        <p>
          Bullfrog 256-S klasy Harvester to rodzaj operatora w grze Prey: Mooncrash. <br /> <br />
          W przeciwieństwie do innych operatorów, Harvester nie wchodzi w bezpośrednią interakcję z graczem ani nie może być używany do uzupełniania obecnego statusu gracza. Zamiast tego biernie wędrują po obszarach – głównie z regolitem – i odkurzają je, używając wbudowanego recyklera do przetwarzania wszystkiego, co złapie, w materiały do ​​​​produkcji. Ta sama próżnia może również przypadkowo wciągnąć gracza i poddać go recyklingowi, więc upewnij się, że nie wchodzisz mu w drogę, gdy próżnia jest aktywna. <br /> <br />

Żniwiarze zwykle wydają ostrzegawczy klakson, gdy gracz jest blisko nich. Mogą również niechętnie korzystać z odkurzacza w takich okolicznościach, ale nadal mogą to robić od czasu do czasu. Są zasilane przez moduł kontrolny, podobnie jak główne systemy Pyteasa. Z tego powodu czasami rozsądne może być zniszczenie Żniwiarzy i zabranie ich Modułu Kontrolnego, jeśli taka alternatywa, jak zakup lub wyprodukowanie, nie jest w danym momencie możliwa. Zniszczone kombajny zawsze będą miały moduł kontrolny, a także niektóre materiały już poddane recyklingowi. Będzie też trochę części zamiennych, które będzie mogła zdobyć tylko Joan Winslow, podczas gdy Dismantle jest instalowany za pomocą neuromodów.<br /> <br />

Inną różnicą, która oddziela go od operatorów, jest to, że jeśli zostanie wystarczająco zaatakowany, może stać się wrogi i wykorzystać swoją próżnię jako środek obronny do radzenia sobie z atakującymi. Jednym z podobieństw, które dzieli z operatorami, jest to, że może zostać uszkodzony przez technopatę. W tym stanie będzie otwarcie ścigać gracza, jeśli zostanie zauważony, i spróbuje go odkurzyć podczas pościgu. Zepsuty Żniwiarz można zauważyć z daleka, ponieważ podczas poruszania się będzie dziwnie się trząsł i przechylał.<br /> <br />
        </p>
        <h3 className={styles.smallHeader}>
          Opis (z broszury TranStaru)
        </h3>
        <p>
          Ponieważ firma TranStar mocno wierzy w bezpieczeństwo pracowników, kombajn TranStar Bullfrog 256-S Regolith został stworzony do pracy w niebezpiecznych warunkach, więc człowiek nie musi tego robić. Silnie wzmocniona obudowa DuraGRADE Harvester została zaprojektowana tak, aby wytrzymać wszystko, od gorącego stopionego metalu po zimną próżnię kosmiczną, jednocześnie szeroko wykorzystując opatentowaną technologię Recycler do szybkiego przesiewania księżycowego regolitu i gruzu z wykopalisk w poszukiwaniu cennych zasobów.
        </p>
        <h3 className={styles.smallHeader}>
          Wygląd
        </h3>
        <p>
          Również w przeciwieństwie do innych operatorów, kombajn nie składa się z trapezowego korpusu z przymocowanymi do niego ramionami robota. Zamiast tego jest to duży pojazd trochę przypominający poduszkowiec. Jego przednia część jest brązowo-pomarańczowa – podobnie jak kolor Inżynierów Operatorów – z elektroniką i innymi obwodami z tyłu, które świecą na niebiesko. Ma żółte światło, które przypomina soczewkę, w przeciwieństwie do operatorów, którzy normalnie używają niebieskiego światła. Przedni korpus jest opancerzony, co pozwala mu zlekceważyć ostrzał z broni ręcznej, a nawet wyładowania elektryczne z paralizatora zakłócającego. Otwiera się, gdy Harvester używa swojej próżni.
        </p>
        <h3 className={styles.smallHeader}>
          Strategia
        </h3>
        <p>
          Ponieważ przód Żniwiarza jest opancerzony, atakowanie go bezpośrednio będzie bezcelowe i będzie stratą amunicji i/lub energii. Jeśli cię goni i nie możesz go ominąć, użyj ładunku EMP lub paralizatora zakłócającego. To go wyłączy i pozwoli ci obejść jego tyły. Kiedy strzelasz w jego plecy, jest znacznie słabszy i łatwiejszy do zniszczenia. Strzelba najlepiej się do tego nadaje, zwłaszcza jeśli możesz wykonać atak z zaskoczenia. <br /><br />

Harvester jest wrażliwy na materiały wybuchowe. Ustawienie jednego za nim może zniszczyć go za jednym zamachem. Alternatywnie, jeśli goni cię podczas odkurzania, uszkodzony lub nie, spróbuj znaleźć wybuchowy kanister leżący w pobliżu i wrzuć go do zsypu. To również całkowicie zniszczy go od wewnątrz.<br /><br />

Q-Beam może działać przeciwko Harvesterowi, ale ze względu na to, jak wolno się poruszasz podczas korzystania z niego, może być również ryzykowny w użyciu, jeśli korzysta z próżni. W rezultacie najlepiej jest używać go jako środka do wykończenia Żniwiarza, a nie jako formy ataku.<br /><br />


        </p>
    </>
  )
}
