import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const AaronIngram = () => {
  return (
    <>
        
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/aaron2.png')} alt="Aaron Ingram" />
        </div>
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/aaron.png')} alt="Aaron Ingram" />
        </div>

        <h2 className={styles.header}>
          Aaron Ingram
        </h2>
        <p>
          Aaron Ingram (identyfikator wolontariusza V-090655-13) jest członkiem programu wolontariatu TranStar na pokładzie Talosa I.
        </p>

        <h2 className={styles.header}>
          Historia
        </h2>
        <h3 className={styles.smallHeader}>
          Tło
        </h3>
        <p>
          Ingram był więźniem syberyjskiego obozu jenieckiego przed „wolontariatem”. Został oskarżony o różne podejrzane interesy, w tym sprzedaż substancji kontrolowanych, porwania, handel ludźmi, oszukańcze podszywanie się i „nagabywanie nieletnich”. . Ponieważ jego nazwisko jest zredagowane w jego rejestrze wolontariuszy, prawdopodobnie „Aaron Ingram” to pseudonim. Wydaje się jednak, że jest raczej Amerykaninem niż Rosjaninem i twierdzi, że jest więźniem politycznym, który nadepnął na palce niewłaściwym ludziom. <br /> <br />

Plakat dotyczący Programu Wolontariatu wymienia go z imienia i nazwiska oraz twierdzi, że został już zrehabilitowany i odesłany z powrotem na Ziemię.<br /> <br />

Ingram miał być użyty do wydobywania egzotycznych materiałów, ale ledwo uniknął tego losu z powodu chaosu wybuchu Tyfona i został porzucony w komorze ekstrakcyjnej. W tym czasie poznał hasło do pobliskiej Zbrojowni po tym, jak zobaczył, jak Demian Linn ucieka i chowa się w środku, a także był świadkiem, jak Luka Golubkin morduje Kristine Lloyd kluczem.


        </p>
        <h3 className={styles.smallHeader}>
          Prey (2017)
        </h3>
        <p>
          Morgan spotyka Ingrama w laboratorium ekstrakcji materiałów psychotronicznych. Jest uwięziony w szklanej komorze ekstrakcji materiałów, a gracz może go uwolnić lub użyć pobliskich elementów sterujących, aby zabić go mimikiem, a następnie przetworzyć powstałą grupę mimików w celu uzyskania zasobów. Jeśli zostanie uwolniony, Ingram z wdzięcznością dziękuje Morganowi i przekazuje im kod dostępu do zbrojowni. Jeśli gracz zdecyduje się go zabić, zdobędzie 13 zasobów materiałów egzotycznych.<br /> <br />

Jeśli zostanie uwolniony, Ingram przekaże Morganowi hasło do Zbrojowni Psychotroniki. Następnie opowie o swojej przeszłości, a także ostrzeże Morgana, że ​​nie wszyscy pozostali więźniowie są „porządnymi ludźmi” jak on, głównie odnosząc się do Luki Golubkina i jego psychopatycznych skłonności. W zależności od tego, czy Morgan włamie się do drzwi Zbrojowni lub użyje kodu i czy zostaną one otwarte przed czy po uwolnieniu, Ingram może również wejść do Zbrojowni.<br /> <br />

Jeśli Morgan uwolni Aarona i zapieczętuje wszystkie wycieki gazu na ścieżce prowadzącej do laboratorium ekstrakcji materiałów za pomocą GLOO, Aaron zostanie później zabity przez naśladowcę, ale jeśli pozostanie przynajmniej jeden wyciek gazu, mimiki nie będą mogły przejść i ucieknie z ocalałymi w zakończeniu wahadłowca. Powrót do Psychotroniki w dalszej części gry prawdopodobnie doprowadzi do odkrycia, że ​​Aaron został zabity przez Upiora, który pojawia się w Laboratorium Ekstrakcji Materiałów podczas kolejnych wizyt. Pamiętaj, że jeśli nie zostanie ponownie odwiedzony, nadal będzie oznaczony jako nominalny i nadal będzie traktowany jako żywy.<br /> <br />

Ostatecznie, pod warunkiem, że Ingramowi uda się przetrwać do końca gry, będzie widziany wraz z innymi ocalałymi członkami załogi na zakończeniu ucieczki wahadłowca, w kabinie wahadłowca.


        </p>
    </>
  )
}
