import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const ThermalMimic = () => {
  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/thermalMim.png')} alt="Mimik" />
        </div>
        <h2 className={styles.header}>
          Omówienie
        </h2>
        <p>
          Mimik termiczny to organizm Typhon w Prey: Mooncrash. Nie mylić z płonącymi mimikami, które są zwykłymi mimikami tymczasowo podpalonymi przez fantomy termiczne. <br /> <br />
          Mimiki termiczne to, podobnie jak ich bardziej pospolici kuzyni, małe, czworonożne, przypominające pająki organizmy Typhon, które potrafią kamuflować się, by wyglądać jak obiekty w ich otoczeniu, takie jak krzesła biurowe lub kosze na śmieci, wyskakując na gracza, gdy wędruje zbyt blisko. Mimik termiczny ma dodatkowe atrybuty posiadania aury psychotermicznej i potencjalnego zadawania oparzeń istotom w jego pobliżu. <br /> <br />
          Mimiki termiczne są klasyfikowane jako większe mimiki i, podobnie jak ich odpowiedniki z Talosa I, rodzą się w wyniku tego samego procesu, co inne mimiki. Biorąc to pod uwagę, ich mutacje są widoczne w ich nowych zdolnościach psionicznych i tendencji do prób chwytania się ludzkich twarzy podczas walki.
        </p>
        <h2 className={styles.header}>
          Strategia
        </h2>
        <p>
          Te same strategie, które mają zastosowanie do zwykłych Mimików, można również zastosować do łatwego radzenia sobie z mimikami termicznymi. Można ich łatwo zabić pojedynczym strzałem ze strzelby z bliska; jednak gracz musi uważać, aby nie podejść zbyt blisko lub ryzykować poparzenia. Nullwave lub psychoshock mogą wyłączyć ich aury i zakłócić ich zdolność mimikry. Podobnie jak w przypadku większych naśladowców na pokładzie Talosa I, bądź przygotowany na unikanie ich ataków na ciebie i szybkie oderwanie ich od twarzy, jeśli zdecydują się zaczepić.
        </p>
    </>
  )
}
