import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Pytheas = () => {
  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/pytheasBanner.png')} alt="Pytheas" />
        </div>
        <div className={styles.imageBox} >
            <img className={styles.image} src={require('../../../../../../prey-graphics/pytheasMine.png')} alt="Pytheas" />
        </div>
        <div className={styles.imageBox} >
            <img className={styles.image} src={require('../../../../../../prey-graphics/moonstation.png')} alt="Pytheas" />
        </div>
        <div className={styles.imageBox} >
            <img className={styles.image} src={require('../../../../../../prey-graphics/underDome.png')} alt="Pytheas" />
        </div>
        <div className={styles.imageBox} >
            <img className={styles.image} src={require('../../../../../../prey-graphics/pytheasOutside.png')} alt="Pytheas" />
        </div>
        <h2 className={styles.header}>
            Omówienie
        </h2>
        <p>
          Pyteas to tajny ośrodek wydobywczy i badawczy TranStar zbudowany na Księżycu. Baza księżycowa to główne miejsce gry Prey: Mooncrash.

<br /> <br />
          Pyteas siedzi w kraterze na powierzchni Księżyca i wokół niego. Każdy z trzech budynków jest połączony tramwajem, a wszystkie z wyjątkiem aneksu dla załogi mają zatokę kapsuł ratunkowych.

        </p>
        <h3 className={styles.smallHeader}>
          Ogólnie baza księżycowa składa się z:
        </h3>
        <ul className={styles.wikiPageList}>
          <li>
    Krater: miejsce wydobycia i testowania przykryte ochronną kopułą. Ma dostęp przez śluzy do wszystkich innych lokalizacji (2 śluzy dla Pyteas Labs i 1 dla pozostałych lokalizacji).
          </li>
          <li>
    Aneks dla załogi: biura administracyjne, główne Centrum Urazowe, mieszkania załogi i usługi rekreacyjne, a także zatoka wahadłowa bazy księżycowej do podróży na zewnątrz.

          </li>
          <li>
    MoonWorks: kopalnia helu-3 Pytheasa, rafineria i warsztat maszynowy.

          </li>
          <li>
    Pyteas Labs: ośrodek badawczy Typhon oraz komórki ochotników.

          </li>
        </ul>
        <p>
          Jest też mniejsze Centrum Urazowe, Aneks Inżynieryjny i Magazyn. Są one umieszczane losowo na początku symulacji w Aneksie załogi, Moonworks i Pyteas Labs.
        </p>
        <h2 className={styles.header}>
          Historia
        </h2>
        <h3 className={styles.smallHeader}>
          Wczesny etap
        </h3>
        <p>
          W 1964 roku Stany Zjednoczone utworzyły bazę księżycową, aby dostarczać surowce dla Projektu Axiom, w szczególności do budowy nowej stacji kosmicznej zbudowanej wokół Kletki. Pierwotny zakład wydobywczy znajdował się głęboko w kraterze na biegunie księżycowym, stale zacienionym i otoczonym kilometrami lodu. Znajdowała się w rozległej sieci ciasnych tuneli, w których wydobywano hel-3, wodór i platynę. Górników rekrutowano w ramach kampanii rządowej i pomimo strasznych warunków pracy i częstych wypadków obietnica pracy na Księżycu wciąż przyciągała górników i praca trwała prawie 15 lat. <br /> <br />

W 1978 roku operacje wydobywcze zostały ograniczone, gdy stacja kosmiczna została ukończona. Kopalnie pociemniały, a sześć lat później odwołano pozostały personel i zamknięto bazę po tym, jak rząd wstrzymał Projekt Axiom.


        </p>
        <h3 className={styles.smallHeader}>
          TranStar
        </h3>
        <p>
          W 2025 roku nowo założona firma TranStar kupiła bazę księżycową, wyremontowała ją i nazwała Pyteas. Mieli zamiar wykorzystać go do zaopatrzenia w zasoby odnowionej stacji kosmicznej, przemianowanej na Talos I. Aneks dla załogi był pierwszym nowym dodatkiem, który miał poprawić warunki życia personelu.<br /> <br />

Pięć lat później osiągnięto Talos I, a operacje wydobywcze zostały ograniczone. Kopalnie nadal dostarczały hel-3 do superkuźni reaktora syntezy jądrowej na stacji kosmicznej, a także wysyłały zasoby na Ziemię za pomocą Mass Drivera. W międzyczasie Pyteas Labs zostały zbudowane, aby służyć jako aneks badawczy do Talosa I w badaniach nad Tyfonem. Do krateru dodano również kopułę, aby ułatwić spacery między obiektami bazy i przekształcić ją w pole testowe dla laboratoriów. Aby zachować tajemnicę, TranStar wprowadził ograniczenia w komunikacji poza Pyteasem. Wyprowadzającym się pracownikom usunięto również neuromody i wyczyszczono pamięć z tajemnic bazy.<br /> <br />

W 2032 roku prezes TranStar, Alex Yu, mianował swojego kuzyna Rileya dyrektorem Pyteas, aby nadzorował tamtejszą rafinerię helu-3 i badania nad tyfonem, co jest niezbędne dla Talosa I. Trzy lata później nienaturalne trzęsienia księżyca zaczęły destabilizować operacje wydobywcze. W międzyczasie laboratoria, pod nowym nadzorem Rileya, kończyły prace nad kilkoma projektami, zwłaszcza Typhon Gates, Typhon Towers i Project Genesis.<br /> <br />

23 lutego 2035 roku Typhon przedarł się przez zabezpieczenia i prawie zmiótł z powierzchni ziemi personel bazy księżycowej, podczas gdy Talosa I spotkał ten sam los. Riley Yu zdołał wysłać dane bazy w Vault Operator, które zostały przechwycone przez KASMA i wysłane do ich agenta Petera w satelicie szpiegującym Pytheasa. Odzyskał jego dane poprzez symulację ostatnich chwil Pyteasa, a także klucz poleceń KASMA Claire Whitten, aby rozbić swojego satelitę w pobliżu bazy księżycowej i wejść na pokład jego wahadłowca na Ziemię, nieświadomie zabierając ze sobą Mimika.<br /> <br />


        </p>
    </>
  )
}
