import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Poltergeist = () => {
  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/poltergeist.png')} alt="Poltergeist" />
        </div>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/poltergeists.png')} alt="Poltergeist" />
        </div>
        <h2 className={styles.header}>
          Omówienie
        </h2>
        <p>
          Czasami tkacze nie odnoszą pełnego sukcesu w próbie ożywienia widma ze zwłok, zamiast tego tworzą poltergeista. W przeciwieństwie do zjaw, które otwarcie patrolują korytarze Talosa I w poszukiwaniu zdobyczy, Poltergeisty czają się poza zasięgiem wzroku, czekając na odpowiedni moment, by rzucić przedmiotami lub wyrzucić swoje ofiary w powietrze. <br /> <br />

Tak więc poltergeisty są drapieżnikami z zasadzki. Jednak w przeciwieństwie do mimików, które przebierają się za zwykłe przedmioty umieszczone strategicznie w zasięgu ręki w środowisku, poltergeisty mają wyjątkową zdolność stawania się niewidzialnym, której używają do atakowania z daleka w niewidocznych miejscach. Nierzadko można zauważyć obecność poltergeista dopiero wtedy, gdy ten już rozpoczął atak. Może zdecydować się na telekinetyczne rzucanie przedmiotami przez pokój lub generowanie pola nośnego, czyniąc swój cel łatwym łupem dla innych kosmitów lub miażdżąc ich o niski sufit.<br /> <br />

Kiedy mówią, powtarzają wiele zwrotów z innych typów fantomów, ale ich głosy brzmią bezcielesnie, emitowane przez system głośników. Należy zauważyć, że pomimo zdolności poltergeistów do odtwarzania ludzkiej mowy i ich podobieństwa do zjaw, żaden z nich nie ma imienia ani nie ma bransoletki śledzącej na sobie lub w środku, być może z powodów podobnych do istnienia nienazwanych zjaw.<br /> <br />

Moc pola podnoszenia jest odblokowywana poprzez zeskanowanie tego wroga. Alternatywnie, moc pola nośnego i dane badawcze poltergeista można uzyskać, przesyłając plik ze stacji roboczej Sylvaina Bellamy'ego. Na Pyteaszu poltergeisty są w stanie nie tylko generować pola nośne i rzucać przedmiotami w swój cel, ale są również w stanie próbować chwycić dowolny przedmiot trzymany w rękach celu, tym samym go rozbrajając.


        </p>
        <h2 className={styles.header}>
          Design
        </h2>
        <p>
          Wyglądają jak zdeformowane zjawy z cienkimi sprężystymi ramionami, których używają do ciągnięcia się lub czołgania, i pozornie bezużytecznymi nogami i dwojgiem oczu zamiast dwunastu. Każda z dwóch rąk poltergeista ma pięć cyfr, z których jedna jest przeciwstawna. Wygląda na to, że poltergeisty mają skoliozę.
        </p>
        <h2 className={styles.header}>
          Strategia
        </h2>
        <p>
          Pierwszą oznaką, że w pobliżu jest poltergeist, jest zmiana na ostrą muzykę otoczenia, która brzmi jak ciągły dźwięk dzwonka. Elektronicznie brzmiące odgłosy zmiany biegów można również usłyszeć wkrótce po wejściu na swoją domenę. Obiekty mogą zacząć się poruszać, a przez pobliskie głośniki można również usłyszeć paplaninę. Poltergeisty staną się tymczasowo widoczne po uderzeniu, w szczególności GLOO może sprawić, że pozostaną w tym stanie dłużej, jeśli zostaną całkowicie otoczone. Możesz użyć swojego psychoskopu, aby go śledzić, jeśli patrzysz na niego przez psychoskop, a znaczniki wroga są włączone w HUD. Wieżyczki mogą wykrywać poltergeisty nawet wtedy, gdy są zamaskowane dzięki wbudowanym psychoskopom.<br /> <br />

W przeciwieństwie do swojego kuzyna, upiora, brakuje mu ataku wręcz; jednak nie należy tego traktować jako zaproszenia do zbliżenia się, ponieważ są jedynymi Tyfonami, które faktycznie zadają obrażenia, po prostu uderzając kluczem lub balistyką (rodzaj odbicia obrażeń kinetycznych, z wyjątkiem tego, że oni również otrzymują obrażenia). Ta zdolność, podobnie jak jej niewidzialność, nie może zostać zeskanowana i nie wydaje się, aby miała dużo czasu na ochłonięcie między instancjami. Chociaż to sprawia, że ​​klucz jest niewskazany, szeroki rozrzut strzelby sprawia, że ​​jest to przydatne narzędzie do wyczuwania jej pozycji w pomieszczeniu, a jednocześnie zadaje średnie obrażenia, chociaż może również zwrócić obrażenia od pocisków ze strzelby. Q-beam może ominąć tę obronę, a tym samym nie spowodować odbicia obrażeń. Ta zdolność „odbicia obrażeń” jest pasywna, co oznacza, że ​​nie jest wyłączana przez psychoszok lub nadajnik fal zerowych. Warto zauważyć, że poltergeisty są podatne na wywołujące strach efekty luzów tylko wtedy, gdy wykorzystują pasywne odbicie obrażeń przeciwko tobie, aby przeciwdziałać zadawanym przez ciebie obrażeniom kinetycznym. Jeśli uderzysz jednego, gdy twój luz jest aktywny, ucieknie i pozostawi w powietrzu jasnoniebieski „ślad strachu”, który ułatwi śledzenie go przez krótką chwilę.<br /> <br />

Uważaj, aby spojrzeć na podłogę, abyś mógł zobaczyć nadchodzące pole windy. Jeśli zostaniesz uwięziony w polu windy, użyj systemu napędowego Artax (znajdującego się w laboratoriach sprzętowych), aby uciec. Jeśli na otwartej przestrzeni staje się to mniej niebezpieczne, ale w pomieszczeniach z niskimi sufitami ich pole może zadać obrażenia krytyczne.<br /> <br />

Poltergeisty są bardzo podatne na miny (oprócz EMP). Niezbyt wymijający, może być trudno uciec z promienia raz aktywowanego. Ponadto, ich nawyk rysowania przedmiotów, aby w ciebie rzucić, można przeciwdziałać, umieszczając je na przedmiotach, takich jak krzesła, stoły i inne małe i średnie elementy wyposażenia, które wydają się preferować jako broń, wciągając je na własne ryzyko.<br /> <br />

Poltergeisty są natychmiast zabijane przez spadające szyby grawitacyjne. Mogą przejść od pełnego zdrowia do zerowego, gdy mają wyjść z szybu grawitacyjnego, którego użyli.


        </p>
    </>
  )
}
