import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const NeuromodDivision = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/neuromodDivision.gif')} alt="Nauromody" />
        </div>
        <h2 className={styles.header}>
            Omówienie
        </h2>
        <p>
          Oddział Neuromodów to miejsce, w którym na pokładzie Talosa I w Prey (2017) prowadzone są wszystkie badania związane z neuromodami.<br /> <br />
          Wykonano tu replikę mieszkania Morgana Yu i to tutaj rozpoczniesz grę.

Oddział Neuromod powstał w celu projektowania, testowania i produkcji neuromodów. W efekcie atrium Dywizji ma za zadanie zaimponować potencjalnym klientom, a tym samym zwiększyć sprzedaż.<br /> <br />

Umiejętności używane w neuromodach są rejestrowane przez VIP-ów, takich jak Gustav Leitner, w Rejestratorze umiejętności. Rzeczywiste neuromody są składane w Fabryce przy użyciu szablonów umiejętności i materiałów z Psychotroniki. Są one następnie instalowane w ochotnikach do celów testowych.<br /> <br />

Kilka miesięcy przed wybuchem epidemii wystartował nowy projekt stworzenia Neuromodów o parapsychicznych zdolnościach Tyfona. Morgan Yu zgłosił się na ochotnika do przetestowania Neuromodów i wymazał ich wspomnienia, w wyniku czego został umieszczony w symulacji mieszkania.


        </p>
        <div className={styles.imageBox} >
            <img className={styles.image} src={require('../../../../../../grafika/neuromodDivision1.png')} alt="Nauromody" />
        </div>
    </>
  )
}
