import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const OpsLanding = () => {
  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/operatorsEnemy.png')} alt="Operatorzy" />
        </div>
        <h2 className={styles.header}>
            Operatorzy
        </h2>
        <p>
          Od czasu pojawienia się obcych Tyfonów na stacji, operatorzy, podobnie jak wielu innych mieszkańców stacji, padli ofiarą wynikający z tego chaos, w wyniku którego niektóre z nich zostały uszkodzone lub opętane przez Tyfona.
        </p>
    </>
  )
}
