import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const HansKelstrup = () => {
  return (
    <>
        
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/hans2.png')} alt="Hans Kelstrup" />
        </div>
        <div className={styles.imageBox} style={{width: "15%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/hans.png')} alt="Hans Kelstrup" />
        </div>

        <h2 className={styles.header}>
          Hans Kelstrup
        </h2>
        <p>
          Doktor Hans Kelstrup jest dyrektorem Psychotronics pracującym nad Talosem I podczas Prey (2017).
        </p>

        <h2 className={styles.header}>
          Historia
        </h2>
        <h3 className={styles.smallHeader}>
          Tło
        </h3>
        <p>
          Hans Kelstrup był dyrektorem działu psychotroniki na pokładzie Talosa I. W szczególności musiał uporać się z obawami Annalise Gallegos dotyczącymi ich eksperymentów na ochotnikach i Michaiła Iljuszyna, który próbował zdobyć urządzenie podsłuchowe dla Psychotroniki.
        </p>
        <h3 className={styles.smallHeader}>
          Prey (2017)
        </h3>
        <p>
          Morgan Yu znajduje zwłoki Kelstrupa w swoim biurze w pobliżu zepsutej skrzynki przyłączeniowej, co sugeruje, że to była przyczyna jego śmierci. Kelstrup miał zwyczaj ukrywać neuromody w swoim biurze, o czym Alex twierdzi, że był tego świadomy. Nosi próbkę z probówki, kartę dostępu do swojej kabiny i kartę dostępu do swojego biura.
        </p>
    </>
  )
}
