import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const TrevorY = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "25%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/trevor.png')} alt="Trevor Young" />
        </div>

        <h2 className={styles.header}>
          Trevor J. Young
        </h2>
        <p>
          Trevor J. Young jest badaczem pracującym na pokładzie Talosa I podczas Prey (2017).
        </p>
        <h2 className={styles.header}>
          Historia
        </h2>
        <h3 className={styles.smallHeader}>
          Tło
        </h3>
        <p>
          Trevor J. Young był fizykiem matematycznym pracującym w psychotronice. Podczas jednego z eksperymentów wszedł do komnaty z telepatą bez psychoskopu. To powoduje, że Tyfon kontroluje umysł Younga. Następnie został poddany kwarantannie, pod ścisłą obserwacją Mathiasa Kohla i usunięto mu neuromody. Incydent pozostawił go zdezorientowanego, paranoicznego i bardzo agresywnego.
        </p>
        <h3 className={styles.smallHeader}>
          Prey (2017)
        </h3>
        <p>
          Morgan Yu znajduje Younga zamkniętego w kwarantannie w Trauma Center. Jeśli Morgan wejdzie do pokoju, w którym zdrapał kod do sejfu, Young zaatakuje Morgana. Alternatywnie, gracz może wrócić później i znokautować go za pomocą ogłuszającego pistoletu zakłócającego lub nadajnika fal zerowych.
        </p>
    </>
  )
}
