import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Neuromod = () => {
  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../grafika/neuromod1.png')} alt="Neuromod" />
        </div>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../grafika/neuromod2.png')} alt="Neuromod" />
        </div>
        <div className={styles.imageBox} style={{width: "40%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/1 - gcEvjWy.png')} alt="Neuromod" />
        </div>
        <h2 className={styles.header}>
            Omówienie
        </h2>
        <p>
          Neuromod, skrót od Neural Modifier, to narzędzie opracowane przez TranStar, które pozwala Morganowi ulepszać swoje umiejętności w Prey (2017). Można je znaleźć na stacjach Talos I i Pyteas. <br /> <br />
          Neuromody zmieniają strukturę mózgu użytkownika na podstawie szablonu zeskanowanego od osoby posiadającej odpowiednie umiejętności, zwanego mapą neuronową lub „konektomem”; na przykład Neuromod stworzony ze skanu koncertowego pianisty pozwoli użytkownikowi mieć wszystkie umiejętności, które posiadał pianista i grać tak, jakby zawsze wiedział, jak to zrobić. Skany te można nawet zebrać od osób, które nie są już w stanie samodzielnie wykonywać czynności, a wielu ochotników do programu jest w takim stanie, widząc, że skanowanie ich mózgu pozwala ich umiejętnościom żyć w innych. <br /> <br />
          Zeskanowany wzór synaptyczny jest następnie używany do konstruowania samego Neuromoda. Urządzenie składa się z wtryskiwacza przypominającego pistolet, który ma dwie długie igły, które wydają się wchodzić wokół orbity gałki ocznej, oraz emiter w jednej linii ze środkiem gałki ocznej. Surowica wstrzykiwana przez igły wpływa albo na nerw wzrokowy, albo na korę wzrokową, przygotowując mózg do przeprogramowania. Następnie Neuromod tworzy „mapę” mózgu użytkownika, porównuje ją z pożądanym wzorem synaptycznym, a następnie ekstrapoluje zmiany wymagane do stworzenia dopasowania: zasadniczo tworzy wersję skanu mózgu, która już zna pożądaną wiedzę, i odbudowują mózg, aby do niego pasował. <br /> <br />
          Wydobycie neuromodu resetuje mózg do stanu, w jakim znajdował się przed zastosowaniem neuromodu, co oznacza nie tylko utratę pożądanej umiejętności, ale także utratę wszystkich wspomnień uzyskanych w okresie od zainstalowania neuromodu. Może to spowodować drastyczne zmiany w osobowości użytkownika, w wyniku utraty potencjalnie lat doświadczenia. <br /> <br />
          Wiadomo, że stan zwyrodnieniowy znany jako „parapleksa” lub bardziej potocznie jako „biały szum” uniemożliwia instalację neuromodów. TranStar postanowił również nie tworzyć skanów konektomów od osób z parapleksją ani nie wykorzystywać istniejących, powołując się na „nieokreślone ryzyko”. Jednym z możliwych powodów jest to, że będąc zaburzeniem neurologicznym, choroba w rzeczywistości replikuje się u biorcy neuromodu wykonanego ze skanu osoby cierpiącej. Neuromodów nie można również zainstalować, jeśli podmiot cierpi obecnie na skutki wstrząsu mózgu. <br /> <br />
        </p>
        <h2 className={styles.header}>
            Produkcja
        </h2>
        <p>
          Proces produkcji Neuromodów jest co najmniej makabryczny. Po pierwsze, osoby, które rzekomo są osadzonymi w celi śmierci, są zamykane w celi przechowawczej, gdzie następnie uwalniany jest mimik. Mimic zabija człowieka i wchłania jego / jej składniki odżywcze, powodując w ten sposób rozszczepienie i produkcję trzech nowych mimików. Po zwabieniu na pole recyklingu przez przynęty na tyfony, surowy egzotyczny materiał mimików jest rafinowany w sterylne kostki egzotycznego materiału, które następnie można podłączyć do fabrykatora wraz z kostkami materiałów organicznych, mineralnych i syntetycznych, tworząc neuromod. <br /> <br />
          Na Talosie I surowy materiał Typhona jest przetwarzany w sterylny egzotyczny materiał w laboratoriach Psychotroniki, podczas gdy wszystkie inne procesy wytwarzania i testowania partii odbywają się w Dziale Neuromodów.
        </p>
        <h2 className={styles.header}>
            Działanie
        </h2>
        <p>Umiejętności, których Morgan może się nauczyć za pomocą neuromodów, dzielą się na dwie szerokie kategorie: Człowiek i Obcy. Kategorie te są z kolei podzielone na trzy podkategorie: naukowiec, inżynier i ochrona; oraz Energia, Przemiana i Telepatia dla Obcych.</p>
        <h3 className={styles.smallHeader}>
          Człowiek - umiejętności
        </h3>

        <div className={styles.imageBox} style={{width: "40%"}}>
            <img className={styles.image} src={require('../../../../../../grafika/skills.png')} alt="Umiejętności" />
        </div>

          <ul className={styles.wikiPageList}>

            <li>
                Naukowiec
              <ul>
                <li>Hakowanie - pomaga omijać różne poziomy zabezpieczeń</li>
                <li>Fizyk - polepsza działanie apteczek</li>
                <li>Wspomaganie metaboliczne - Podwaja zarówno czas trwania premii "najedzony", jak i zdrowie zdobyte dzięki spożywaniu żywności</li>
                <li>Nekropsja - więcej materiału zebranego z pokonanych tyfonów</li>
                <li>Zdolności psioniczne - zwiększa poziom Twojego psi</li>
                <li>Neurostymulacja - zwiększona efektywność umiejętności opartych na psi</li>
                <li>Psychotronika - odblokowuje więcej slotów na chipsety w Twoim psychoskopie</li>
              </ul>
            </li>

            <li>
                Ochrona
              <ul>
                <li>Broń palna - zwiększone obrażenia zadawane bronią ochrony</li>
                <li>Kondycjonowanie - zwiększenie prędkości i poziomu zdrowia</li>
                <li>Wytrzymałość - znaczne zwiększenie poziomu życia</li>
                <li>Mobilność - znaczne zwiększenie mobilności</li>
                <li>Kondycja - zwiększenie poziomu kondycji</li>
                <li>Skupienie w walce - wejdź w stan skupienia bojowego</li>
                <li>Podstęp - mniejsza szansa na wykrycie przez przeciwników</li>
                <li>Atak z zaskoczenia - zadanie bardzo dużej ilości obrażeń, gdy przeciwnik cię jeszcze nie wykrył</li>
              </ul>
            </li>

            <li>
                Inżynier
              <ul>
                <li>Dźwignia - podnoszenie ciężkich przedmiotów</li>
                <li>Naprawa - pozwala naprawić uszkodzone wyposażenie Talosa I</li>
                <li>Rusznikarz - Pozwala ulepszyć broń ochrony na poziom wyższy od I.</li>
                <li>Modyfikacja kombinezonu - większa ilość miejsca na chipsety i więcej miejsca w ekwipunku</li>
                <li>Demontaż - rozkładanie przedmiotów w ekwipunku na czynniki pierwsze i zbieranie złomu z operatorów</li>
                <li>Ekspert od materiałów - zwiększa ilość pozyskiwanych surowców</li>
                <li>Technologie laboratoryjne - Umożliwia korzystanie z zestawów ulepszeń broni w celu ulepszenia niestandardowej broni technologicznej powyżej poziomu modyfikacji I</li>
                <li>Kalibracja uderzenia - mniejsze zmęczenie podczas ataku kluczem francuskim</li>
                <li>Rozdrobnienie - atakowanie kluczem ma 25% szans na zadanie dodatkowych obrażeń</li>
              </ul>
            </li>

          </ul>

        <h3 className={styles.smallHeader}>
          Obcy - umiejętności
        </h3>
          <ul className={styles.wikiPageList}>
            <li>
              Energia
              <ul>
                <li>Kinetyczna eksplozja - stwórz eksplozję zadającą obrażenia i odpychającą wszystko na określony dystans</li>
                <li>Eskplozja elektrostatyczna - stwórz eksplozję zadającą obrażenia i ogłuszającą przeciwników na określony czas</li>
                <li>Pole grawitacyjne - tworzy windę grawitacyjną, która może też więzi oponentów</li>
                <li>Opór elektrostatyczny - neguje ogłuszenie i zmniejsza obrażenia elektryczne</li>
                <li>Absorpcja elektrostatyczna - pochłania obrażenia elektryczne jako punkty psi</li>
                <li>Supertermika - tworzy pułapkę(i) z gorącej plazmy zadającej obrażenia od ognia, również podpala</li>
                <li>Odporność termiczna - otrzymujesz 50% mniej obrażeń od ognia</li>
                <li>Absorpcja temriczna - absorbuje 50% obrażeń od ognia jako punkty psi</li>
              </ul>
            </li>
            <li>
              Przemiana
              <ul>
                <li>Materia mimiczna - przybranie formy pobliskiego obiektu kosztem psi</li>
                <li>Regeneracja - natychmiastowe odzyskanie określonej ilości życia po otrzymaniu obrażeń</li>
                <li>Fantomowa przemiana - zmień szybko pozycję o określony dystans i zostaw za sobą dopelgangera mylącego przeciwników</li>
                <li>Fantomowa geneza - tworzy mocniejszego fantoma losowego rodzaju przy pomocy ludzkiego ciała</li>
                <li>Odporność na eter - otrzymujesz mniej obrażeń od ataków i zagrożeń typu eterycznego</li>
                <li>Absorpcja eteryczna - pochłaniasz 50% obrażeń eterycznych jako punkty psi</li>
              </ul>
            </li>
            <li>
              Telepatia
              <ul>
                <li>Reakcja - Utworzenie tymczasowej tarczy chroniącej przed następnym atakiem</li>
                <li>Nieustraszoność - skrócenie trwania efektów strachu</li>
                <li>Kontrola umysłu - wrogie organiczne jednostki tymczasowo są po twojej stronie</li>
                <li>Maszynowy umysł - wrogie robotyczne jednostki tymczasowo są po twojej stronie</li>
                <li>Psychoszok - dokonanie bezpośredniego ataku psychicznego zadającego obrażenia i tłumiącego psi</li>
                <li>Zdalna manipulacja - poruszanie odległymi przedmiotami za pomocą umysłu</li>
              </ul>
            </li>
          </ul>
    </>
  )
}
