import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Lobby = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/lobby.gif')} alt="Lobby" />
        </div>
        <h2 className={styles.header}>
            Omówienie
        </h2>
        <p>
          Lobby Talosa I to główne lobby i główne centrum na pokładzie Talosa I w grze Prey (2017).


        </p>
        <div className={styles.imageBox} style={{width: "60%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/lobby1.png')} alt="Lobby" />
        </div>
        <div className={styles.imageBox} style={{width: "60%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/lobby2.png')} alt="Lobby" />
        </div>
    </>
  )
}
