import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Wrench = () => {

  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../grafika/wrench.png')} alt="Schemat"/>
        </div>
        <h2 className={styles.header}>Ogólne</h2>
        <h3 className={styles.smallHeader}>Talos I</h3>
        <p className={styles.info}>Po raz pierwszy znaleziony, gdy Morgan ucieka z ich mieszkania i znajduje go w pobliżu ciała Patricii Varmy, klucz jest sposobem na ucieczkę przez fałszywe akwarium lub okno balkonowe. <br /><br />

<span className={styles.span}>Obrażenia</span>: 10-20 (niezmodyfikowane).<br /><br />

Klucz nie akceptuje zestawów ulepszeń broni.</p>
        <h3 className={styles.smallHeader}>Pytheas</h3>
        <p>Joan Winslow nosi podstawowy klucz francuski jako swoją domyślną broń.

W bazie Pyteasa można znaleźć kilka kluczy francuskich, z różnymi modelami, od standardowych po elitarne. Mogą również mieć ulepszenia, takie jak efekt znoszący psi podczas uderzania w cel.</p>
        <h2 className={styles.header}>Działanie</h2>
        <p className={styles.info}>Klucz to powolna broń do walki w zwarciu, która może zadawać pojedyncze ciosy lub ładować się za cięższe trafienie, przytrzymując przycisk strzału. Działa na pasku wytrzymałości Morgana, a każde uderzenie zmniejsza pasek o określoną wartość: zbyt wiele trafień w krótkim czasie spowoduje, że Morgan będzie zdyszany i nie będzie w stanie uderzyć (lub zrobić niczego innego wymagającego wytrzymałości), dopóki pasek całkowicie się nie uzupełni. W rezultacie klucz najlepiej nadaje się do taktyki „uderz i uciekaj”.</p>
        <p>Wymagane materiały:
          <img className={styles.materialIcon} alt="Mineral icon" src={require('../../../../../../grafika/Mineral.png')} width="15" height="15"></img> 2
        </p>
    </>
  )
}
