import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const CharLanding = () => {
  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/charLand.png')} alt="Postaci" />
        </div>
        <h2 className={styles.header}>
          Postacie
        </h2>
        <p>W grze Prey z 2017 roku, oprócz postaci obcych Tyfonów, istnieje również grupa ważnych postaci ludzkich. Gracze spotkają się z różnymi naukowcami, inżynierami, ochroniarzami i innymi członkami personelu stacji kosmicznej Talos I. Każda z tych postaci ma swoje własne motywacje, cele i wpływ na fabułę gry. Gracz będzie musiał interweniować w życiu tych postaci, nawiązywać z nimi relacje, odkrywać ich tajemnice lub podejmować decyzje, które będą miały konsekwencje dla dalszego rozwoju historii. Wśród postaci ludzkich można znaleźć zarówno sojuszników, jak i wrogów, a ich interakcje dodają głębi i złożoności do świata gry Prey.</p>
    </>
  )
}
