import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const LocationsLanding = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/13 - JfjK9oL.png')} alt="Talos I" />
        </div>
        <h2 className={styles.header}>
            Lokacje
        </h2>
        <p>
          różnorodność lokacji jest niesamowicie szeroka i wciągająca. Gracz eksploruje ogromną stację kosmiczną Talos I, gdzie znajdują się zarówno eleganckie biura, jak i przerażające labirynty podziemi. Każda lokacja oferuje unikalne wyzwania i możliwości interakcji z otoczeniem, co pozwala na wielostronne doświadczenia rozgrywki. Bez względu na to, czy gracz preferuje eksplorację ekskluzywnych pomieszczeń czy zwiedzanie opuszczonych korytarzy, Prey zapewnia emocjonującą różnorodność miejsc do odkrywania.
        </p>
    </>
  )
}
