import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const January = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "40%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/january.png')} alt="January" />
        </div>

        <h2 className={styles.header}>
          January
        </h2>
        <p>
          January to przeprogramowany Operator, który prowadzi Morgan Yu przez Talosa I w Prey (2017). <br /> <br />
          January to Operator zmodyfikowany przez Morgan Yu, aby pamiętał wszystko, o czym zapomnieli podczas ekstrakcji neuromodów. Mówi głosem Morgana, który ma męski lub żeński głos, w zależności od wyboru gracza na początku gry.
        </p>

        <h2 className={styles.header}>
          Historia
        </h2>
        <h3 className={styles.smallHeader}>
          Tło
        </h3>
        <p>
          Kiedy Morgan Yu odkrył, że byli zmuszani do przeżywania tego samego dnia w kółko poprzez wielokrotne wstrzykiwanie i usuwanie neuromodów przez ich brata, Alexa Yu, podjęli drastyczne działania, aby uwolnić się z cyklu. Morgan potajemnie przeprogramował Operatora o kryptonimie „January”, aby działał jako swego rodzaju kopia zapasowa na wypadek wymazania ich wspomnień, kierując go, aby pomógł ich przyszłemu ja zniszczyć Talosa I, wraz ze wszystkimi badaniami nad gatunkiem Typhon . <br /> <br />

Morgan został ostatecznie zmuszony do powrotu do laboratorium symulacyjnego, pozostawiając January, aby poczynił przygotowania, ukrywając neuromody wokół stacji i gromadząc zasoby. Kiedy rozpoczęła się epidemia tyfona, January skorzystał z okazji, by uratować Morgana, zastępując kolejną rundę neuromodów pustymi miejscami, pozostawiając nienaruszoną pamięć Morgana o wydarzeniach z poprzedniego dnia. Następnie wysłał e-mail do Morgana w ich mieszkaniu, ostrzegając ich, że są w niebezpieczeństwie, i skontaktował się, aby pomóc im uciec z symulacji i wrócić do ich biura. January był w stanie przybliżyć obecnemu Morganowi ostatnie wydarzenia.

        </p>
        <h3 className={styles.smallHeader}>
          Prey (2017)
        </h3>
        <p>
          January służy jako przewodnik dla Morgana za pośrednictwem Transkrypcji, pomagając im w poruszaniu się po ich celach i oferując wgląd w tematy, o których Morgan zapomniał. January przebywa we względnym bezpieczeństwie w biurze Morgan Yu i jest uzbrojony w paralizator Disruptor do samoobrony. Tyfon generalnie ignoruje lub oddala się od stycznia, ale nie jest to kluczowa postać i można go zniszczyć w odpowiednich okolicznościach (lub jeśli zaatakuje go Morgan). <br /> <br />

Krótko po pierwszym spotkaniu w styczniu „osobiście” i wysłaniu do Psychotronics, Morgan kontaktuje się z innym niestandardowym Operatorem, znanym jako December, twierdząc, że został zaprojektowany, aby pomóc Morganowi uciec z Talosa I przy użyciu osobistego promu ratunkowego Alexa. Mówi Morganowi, aby spotkał się z nim w foyer Oddziału Neuromodów, jednak jeśli styczeń nadal będzie działał, pokona tam Morgana i zniszczy grudzień. Styczeń stwierdza, że ​​​​grudzień był zawodny i wadliwy projekt pozostałość po wczesnych planach Morgana.<br /> <br />

W miarę rozwoju fabuły Alex pokazuje Morganowi wideo przedstawiające ich pierwotnego siebie, w którym okazuje się, że Morgan opracował urządzenie zwane Prototypem Nullwave Transmitter na wypadek, gdyby Typhon kiedykolwiek złamał zabezpieczenie. Teoretycznie Nullwave zabiłby organizmy Typhon, ale pozostawiłby stację i ocalałych bez szwanku. Pierwotny Morgan był nieugięty, że ich wiedza o Tyfonie miała zostać zachowana na wypadek, gdyby Ziemia kiedykolwiek została zaatakowana, a stycznia stara się pogodzić z jego dyrektywami. Ostatecznie January odrzuca ten pomysł jako zbyt ryzykowny, zauważając, że tylko dlatego, że ta wersja Morgana pojawiła się jako pierwsza, nie oznacza to, że mają rację.<br /> <br />

Pod koniec gry, o ile obie strony przeżyją, January i Alex stoczą konfrontację na moście Talos I. Jeśli Morgan zdecydował się zniszczyć Talosa I, January pozbawi Alexa przytomności, skazując ich na śmierć w eksplozji. Jeśli jednak Morgan zdecyduje się rozmieścić Prototype Nullwave Transmitter, January odmówi Morganowi dostępu do konsoli aktywacyjnej, zmuszając Morgana do zniszczenia January. Jeśli Morgan przygotował zarówno autodestrukcję, jak i ustawił nadajnik, Alex zakończy kłótnię z January, strzelając do niego.<br /> <br />

Główną dyrektywą stycznia jest niedopuszczenie, aby jakikolwiek materiał genetyczny Tyfona uciekł z Talosa I i zagroził Ziemi. Jeśli Morgan ukończył misję Obezwładnij Dahla i przygotuje stację do samozniszczenia, January pochwali ich za znalezienie sposobu na ucieczkę dla ocalałych (jeśli tacy istnieją). Jeśli jednak Morgan wstrzyknął sobie zbyt wiele neuromodów opartych na Typhonie, January poinformuje ich, że będą stanowić zagrożenie, jeśli wrócą na Ziemię. Następnie January kontaktuje się z Dahlem przez Transkrypcję i udając Morgana, mówi mu, że nie dadzą rady i odejdą z ocalałymi. Styczeń sugeruje również, aby Morgan po prostu usiadł na krześle i przeżył ostatnie chwile, zanim Talos I zostanie zniszczony. Czas ewakuacji Morgana zostaje skrócony z siedmiu do trzech, co czyni ucieczkę szczególnie trudną, jeśli nie zrealizowali grudniowego celu, jakim jest odblokowanie osobistego promu ratunkowego Alexa w Arboretum.<br /> <br />

Będąc zaprogramowanym przez oryginalnego Morgana, January aprobuje, kiedy obecny Morgan pomaga ocalałym w pobliżu Talosa I. January komentuje negatywnie, gdy Morgan krzywdzi niewinnych, zamiast tego woli dać tym, którzy wciąż żyją, nadzieję w ostatnich godzinach. Argumentuje przeciwko Morganowi używającemu neuromodów opartych na Typhonie, uznając ich moc, ale wskazując, że doprowadziły one do niczego poza katastrofą, i błędnie twierdzi, że Koszmar pojawił się na stacji dopiero po tym, jak Morgan zainstalował neuromody Typhona, jeśli to zrobili (pojawia się niezależnie od ). Błaga Morgana, aby nie tracił z oczu tego, kim są, ani ich celu, jakim jest ochrona ludzkości.


        </p>
    </>
  )
}
