import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Moonshark = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "40%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/moonshark1.png')} alt="Rekin" />
        </div>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/moonshark2.png')} alt="Rekin" />
        </div>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/moonshark3.png')} alt="Rekin" />
        </div>
        <h2 className={styles.header}>
          Omówienie
        </h2>
        <p>
          Rekin księżycowy to typ tyfona napotkany na Pyteas podczas wydarzeń z DLC Prey (2017), Mooncrash. <br /> <br />
          Zostają wprowadzone do symulacji po udanej ucieczce z Andriusem Alekną. Pojawiają się najpierw w Kraterze, ale później mogą również pojawiać się w Moonworks na wyższym poziomie zepsucia. Przyciągają je obiekty i postacie dotykające powierzchni księżyca i natychmiast zakopują się w ostatnio dotkniętej części powierzchni i wynurzają się, by zaatakować osobę odpowiedzialną. Mogą albo spowodować szczelinę, aby zaatakować gracza (często powodując uraz krwotoczny), albo użyć kamieni na swojej osobie i rzucić nimi w gracza (często wywołując wstrząs mózgu). Mają duże zapasy zdrowia i są odporne na obrażenia od pistoletu z tłumikiem lub strzelby, co sprawia, że ​​jest jeszcze trudniejszy do pokonania.<br /> <br />

Podobnie jak wszystkie tyfony w symulacji, ich wytrzymałość wzrasta wraz ze wzrostem poziomu zepsucia. Odradzają się również z każdym wzrostem Poziomu Zepsucia. Po śmierci upuszczają trzydzieści organów tyfona, a także moduł Delay_Loop.Time i kilka dodatkowych części tyfona, które tylko Riley Yu może odzyskać za pomocą sekcji zwłok.<br /> <br />

Księżycowe rekiny w kraterze mogą być bardzo niebezpieczne, jeśli gracz ma problemy z przekroczeniem krateru bez wchodzenia na powierzchnię, ponieważ mogą one spowodować aktywację Typhon Gates, blokując w rezultacie ucieczkę gracza. Dlatego dobrym pomysłem jest posiadanie paralizatora zakłócającego lub przynajmniej ładunku EMP, jeśli to możliwe, podczas przekraczania krateru z aktywnym Moon Sharkiem. Można tego użyć do wymuszonego dezaktywowania Bramy Typhon na kilka sekund, aby umożliwić graczowi przejście.
        </p>
        <h2 className={styles.header}>
          Design
        </h2>
        <p>
          Księżycowy rekin to potężna masa czarnych wąsów zgodna z biologią innych tyfonów, wykazująca niejasne podobieństwo do Koszmaru pod względem wielkości i postawy. Jednak wydają się również posiadać liczne kamienie przyklejone do ich ciała, które służą jako prowizoryczna zbroja przeciwko konwencjonalnym atakom. Wydają niskie, monstrualne pomruki, gdy są na powierzchni i bezczynnie, które stają się głośnymi piskami po wykryciu gracza. Księżycowe rekiny przemieszczają się z miejsca na miejsce, przekopując się przez powierzchnię księżyca, przez co nie można ich trafić, dopóki nie wyjdą z podziemia.


        </p>
        <h2 className={styles.header}>
          Strategie
        </h2>
        <ul className={styles.wikiPageList}>
          <li>Najbardziej rzucającym się w oczy faktem dotyczącym księżycowych rekinów jest to, że wydają się być częściowo ślepe — mogą wykryć gracza tylko wtedy, gdy dotknie on powierzchni księżyca lub poruszy się w ich polu widzenia. Z tego powodu, jeśli gracz pozostaje przykucnięty i wciąż blisko osoby badającej jego lokalizację, możliwe jest uniknięcie walki z nim, nawet jeśli jest ona zwrócona w ich ogólnym kierunku. Gracz nie powinien przebywać w tym czasie na powierzchni księżyca, aby uniknąć zakopania się w nim.</li>
          <li>Wieżyczki, ładunki i działa GLOO mogą być również użyte przeciwko Księżycowemu Rekinowi, aby go zamrozić. Jednak może to wymagać dużo GLOO, aby utrzymać go na miejscu. Biorąc pod uwagę, że amunicja GLOO nie gwarantuje, że będzie znajdować się w tym samym miejscu podczas każdego lotu, używanie jej wyłącznie do Moon Sharka może być uważane za zbyt kosztowne, jeśli nie można jej wyprodukować.</li>
          <li>Broń laserowa jest najskuteczniejsza w zabijaniu rekina księżycowego, ponieważ może bardzo szybko wyssać jego zdrowie. Wieżyczki laserowe są do tego idealne. Jednak Q-Beam wyszkolony na Moon Shark również nie idzie źle. Chociaż nieulepszony Q-Beam będzie wymagał dwóch pełnych pakietów komórek, aby zabić Moon Shark na poziomie Korupcji I.</li>
          <li>Użycie ładunku recyklingowego może szybko pozbawić zdrowia Księżycowego Rekina, jeśli zostanie wylądowany bezpośrednio, ale pamiętaj, że spowoduje to również recykling przenoszonej przez niego Delay_Loop.Time, co może stanowić problem w późniejszych seriach, jeśli nie masz planu fabrykacji do kupienia lub sfabrykować własne.</li>
          <li>Jeśli zbliża się spotkanie z Moon Sharkiem i brakuje ci siły ognia, aby go pokonać, możesz użyć przynęty Typhon Lure, aby odwrócić jego uwagę i szybko uciec. Niektóre można znaleźć na ciele Michaela Cheunga we wczesnych seriach, ale w innych seriach może ich nie być, ze względu na losowe generowanie przedmiotów w symulacji.</li>
          <li>Alternatywnie, można użyć Typhon Spore, aby szybko osłabić zdrowie Moon Shark. Uważaj jednak, aby nie upuścić go blisko siebie, ponieważ reaguje na ruch, ponieważ niektóre macki mogą się odwrócić. Jeśli planujesz użyć zarodnika tyfona, nie używaj obok niego przynęty tyfona, ponieważ odwróci to uwagę macek oraz księżycowego rekina, uniemożliwiając im wysysanie jego zdrowia. Alternatywnie, można użyć przedmiotu do rzucania lub Łowczyni Boltcaster, aby zwabić Księżycowego Rekina w pobliżu Gniazda Macek, aby uzyskać podobne rezultaty.</li>
          <li>W szczególności Krater ma kilka Wieżyczek Laserowych, Wieżyczek Balistycznych i Wieżyczkę GLOO leżących w jego częściach, które można ułożyć w jednym miejscu, a następnie użyć razem przeciwko Księżycowemu Rekinowi, który patroluje ten obszar. To jeden z bezpieczniejszych sposobów radzenia sobie z Księżycowym Rekinem, który zaczyna się w obszarze Krateru, a jeśli to konieczne, możesz odrodzić się w dodatkowym, aby pomóc, grając jako Joan Winslow.</li>
          <li>Można również aktywować Wieże Tyfona z Centrum Dowodzenia Krateru, aby zabić Księżycowego Rekina jednym uderzeniem. Wymaga to jednak znalezienia dwóch działających modułów kontrolnych, aby wieże mogły zostać aktywowane. Moduły zostaną uszkodzone, gdy wieża uwolni swoją moc, zmuszając do ich naprawy (jeśli umiejętność jest dostępna) lub wymiany na nowe, jeśli Moon Shark ucieknie przed wybuchem. Aby upewnić się, że Moon Shark pozostanie w pobliżu wybranej wieży, można użyć przynęty na tyfony.</li>
        </ul>
    </>
  )
}
