import React from 'react'
import { Routes, Route } from 'react-router-dom'
import { AirReg } from './wikiPageContent/AirReg'
import { TechLanding } from './wikiPageContent/TechLanding'
import { Artax } from './wikiPageContent/Artax'
import { Chipsets } from './wikiPageContent/Chipsets'
import { Fabricator } from './wikiPageContent/Fabricator'
import { SzybGraw } from './wikiPageContent/SzybGraw'
import { Glass } from './wikiPageContent/Glass'
import { Recycler } from './wikiPageContent/Recycler'
import { Neuromod } from './wikiPageContent/Neuromod'
import { Psychoscope } from './wikiPageContent/Psychoscope'
import { Transcribe } from './wikiPageContent/Transcribe'
import { Operators } from './wikiPageContent/Operators'
import { ScienceOper } from './wikiPageContent/ScienceOper'
import { MedicalOper } from './wikiPageContent/MedicalOper'
import { EngineerOper } from './wikiPageContent/EngineerOper'
import { Harvester } from './wikiPageContent/Harvester'


const Description = () => {
  return (
    <>
      <Routes>

        <Route path="/">
          <Route index={true} element={<TechLanding/>} />
          
          <Route path="air_regulator" element={<AirReg/>} />
          <Route path="artax_propulsion" element={<Artax/>} />
          <Route path="chipsets" element={<Chipsets/>} />
          <Route path="fabricator" element={<Fabricator/>} />
          <Route path="grav_shaft" element={<SzybGraw/>} />
          <Route path="looking_glass" element={<Glass/>} />
          <Route path="material_recycler" element={<Recycler/>} />
          <Route path="neuromod" element={<Neuromod/>} />
          <Route path="psychoscope" element={<Psychoscope/>} />
          <Route path="transcribe" element={<Transcribe/>} />
          <Route path="operators">
            <Route index={true} element={<Operators/>} />
            <Route path="science_operator" element={<ScienceOper/>} />
            <Route path="medical_operator" element={<MedicalOper/>} />
            <Route path="engineer_operator" element={<EngineerOper/>} />
            <Route path="harvester" element={<Harvester/>} />
          </Route>
        </Route>

      </Routes>
    </>
  )
}

export default Description