import React from 'react'
import { Routes, Route } from 'react-router-dom'
import { WeaponsLanding } from './wikiPageContent/WeaponsLanding'
import { Throwable } from './wikiPageContent/Throwable'
import { Emp } from './wikiPageContent/Emp'
import { Nullwave } from './wikiPageContent/Nullwave'
import { RecycleCharge } from './wikiPageContent/RecycleCharge'
import { Lure } from './wikiPageContent/Lure'
import { Wrench } from './wikiPageContent/Wrench'
import { Firearms } from './wikiPageContent/Firearms'
import { Ppn8 } from './wikiPageContent/Ppn8'
import { Shotgun } from './wikiPageContent/Shotgun'
import { Rsv77 } from './wikiPageContent/Rsv77'
import { Lepik } from './wikiPageContent/Lepik'
import { Emitter } from './wikiPageContent/Emitter'


const Description = () => {
  return (
    <>
      <Routes>

        <Route path="/">
          <Route index={true} element={<WeaponsLanding/>}/>

          <Route path="throwable">
            <Route index={true} element={<Throwable/>}/>
            <Route path="emp_charge" element={<Emp/>} />
            <Route path="nullwave_transmitter" element={<Nullwave/>} />
            <Route path="recycle_charge" element={<RecycleCharge/>} />
            <Route path="typhon_lure" element={<Lure/>} />
          </Route>

          <Route path="wrench" element={<Wrench/>} />

          <Route path="firearms">
            <Route index={true} element={<Firearms/>}/>
            <Route path='ppn_8' element={<Ppn8/>}/>
            <Route path='s_4' element={<Shotgun/>}/>
          </Route>

          <Route path="rsv_77" element={<Rsv77/>} />
          <Route path="gelifoam_cannon" element={<Lepik/>} />
          <Route path="TS-QPB-S11" element={<Emitter/>} />
        
        </Route>

      </Routes>
    </>
  )
}

export default Description