import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const CorruptedOp = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/corrOp.png')} alt="Operator" />
        </div>
        <h2 className={styles.header}>
          Omówienie
        </h2>
        <p>
          Opętany operator to wrogi operator kontrolowany przez Tyfona, którego można znaleźć na pokładzie Talosa I w Prey (2017) i księżycowej bazie Pyteas w Prey: Mooncrash. <br /> <br />
          Skażony Operator to Operator, który został spaczony przez Technopatę. Zamiast pomagać graczowi, jak zwykle operatorzy, zamiast tego atakują gracza, często za pomocą wbudowanych miotaczy ognia lub ogłuszaczy. Ich ataki są krótkie, ale Spaczeni Operatorzy poruszają się znacznie szybciej niż zwykli Operatorzy i zbliżają się szybko, gdy tylko wykryją gracza. <br /> <br />

Skażonych operatorów można zniszczyć za pomocą broni lub umiejętności albo zhakować, aby ponownie stać się przyjaznymi. Można je zdobyć na części zamienne, jeśli posiadasz umiejętność demontażu.<br /> <br />

Niestety, zepsuci operatorzy nie mogą zostać przywróceni przez gracza do ich normalnych funkcji (leczenie, naprawa kombinezonu lub przywracanie punktów psi).


        </p>
    </>
  )
}
