import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const DanielleSho = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "20%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/danielle.png')} alt="Danielle Sho" />
        </div>
        <div className={styles.imageBox} style={{width: "40%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/danielle2.png')} alt="Danielle Sho" />
        </div>

        <h2 className={styles.header}>
          Danielle Sho
        </h2>
        <p>
          Danielle Sho była administratorem systemów technicznych stacjonującym na pokładzie Talosa I.
        </p>

        <h2 className={styles.header}>
          Historia
        </h2>
        <h3 className={styles.smallHeader}>
          Tło
        </h3>
        <p>
          Danielle Sho była głównym archiwistą serwerowni Talosa I, sektora zawierającego ogromne skarbce danych, które zawierały wszystkie badania i przesłane dane cyfrowe stacji. Nie jest jasne, w jaki sposób znalazła się na tym stanowisku pod rządami Alexa Yu, ponieważ była wokalną krytyką zarówno TranStar, jak i rodziny Yu w szczególności. <br /> <br />

Danielle poważnie traktowała swoją pracę związaną z bezpieczeństwem systemów, dbając o przestrzeganie przepisów w celu ochrony mieszkańców stacji. Morgan Yu zasłużyła na jej gniew, kiedy pomogli Willowi Mitchellowi zmienić Operatora z nieautoryzowanym modułem osobowości, aby stworzyć Skillet, co było naruszeniem protokołu IT. Później Danielle znalazła dowody, że Morgan mógł stworzyć nielicencjonowanego operatora z dostępem do komputera głównego stacji, co stanowiło poważne naruszenie, które stanowiło zagrożenie dla bezpieczeństwa załogi. Zwróciła się z tą sprawą do Alexa Yu, a następnie zagroziła, że ​​pójdzie do zarządu, kiedy ten początkowo odrzucił jej obawy.<br /> <br />

Danielle wykonała piosenkę ( Semi Sacred Geometry ) ze Skye Braxton na przyjęciu z okazji stworzenia 8000. Neuromoda firmy Transtar i dołączyła do trwającej gry stacji w Fatal Fortress 2 . Jednak na służbie Danielle często kłóciła się z rodzeństwem Yu, których uważała za zagrożenie dla personelu swoimi niekonwencjonalnymi metodami badawczymi. Nakazano jej szukać porady dotyczącej jej gniewu, a nagranie jednej z jej ocen psychicznych można znaleźć na terminalu Mathiasa Kohla.<br /> <br />

W pewnym momencie Danielle była w romantycznym związku z Abigail Foy, ale zerwała z nią, gdy Abigail żartobliwie porównała ją do Alexa Yu. Danielle później żałowała tego, co się stało, ale ta dwójka nie miała szansy na pogodzenie się przed wybuchem Tyfonu.


        </p>
        <h3 className={styles.smallHeader}>
          Prey (2017)
        </h3>
        <p>
          Morgan Yu kieruje się do kwater załogi, aby znaleźć próbki głosu Danielle, które sprowadzą blokadę wokalną do Deep Storage. Podczas poszukiwań TranScribes Morgan może natknąć się na ciało Abigail Foy. Danielle zostawiła jej wiadomość, że ukrywa się na zewnątrz Talosa I i żeby zapukała do szyby w centrum fitness. Po oczyszczeniu Telepaty i ludzi kontrolowanych umysłem Morgan może stuknąć w okno, aby zwrócić na siebie uwagę Sho. Danielle, zdając sobie sprawę, że Foy nie żyje, prosi Morgana o zabicie zabójcy Luki Golubkina, znanego również jako Ochotnik 37, który podszywa się pod szefa kuchni. Zaczyna brakować jej tlenu i obiecuje, że wykorzysta czas, który jej pozostał, na poszukiwanie sposobów pomocy z zewnątrz. Rozmowa z Danielle nie jest wymagana do ukończenia Gathering Echos, a Morgan może przejść do Deep Storage, używając tylko swoich TranScribes.
<br /> <br />
Kiedy Alex blokuje stację i więzi Morgana w Deep Storage, Danielle kontaktuje się z nimi za pośrednictwem TranScribe. Sugeruje Morganowi wydostanie się na zewnątrz, wyrzucając się z jednego ze skarbców danych, tak jak zrobiła to, by uciec przed Tyfonem. Ostrzega Morgana, że ​​będą musieli znaleźć inny sposób, aby dostać się z powrotem do Talosa I.<br /> <br />

W scenie post-kredytowej Danielle pojawia się jako jeden z operatorów wraz z Mikhailą, Igwe i Sarah, kiedy Alex rekonstruuje Morgana przy użyciu materiałów Typhona.


        </p>
        <h2 className={styles.header}>
          Osobowość i umiejętności
        </h2>
        <p>
          Danielle Sho wykazuje niechęć do rodziny Yu i ich niekonwencjonalnych metod, które stosują, aby osiągnąć swoje cele. Nawet gdy Abigail Foy nieumyślnie porównuje ją do Yus, Danielle reaguje w wybuchu nienawiści, każąc jej wyjść z pokoju, chociaż później tego żałuje. Z zewnątrz jest funkcjonariuszem, który nigdy nie kwestionuje jej rozkazów. Tylko najbliższym odsłania swoją dość wyluzowaną, dowcipną osobowość.
        </p>
    </>
  )
}
