import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Peter = () => {
  return (
    <>
        
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/peterFamily.png')} alt="Peter" />
        </div>
        <div className={styles.imageBox} style={{width: "40%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/peter.png')} alt="Peter" />
        </div>

        <h2 className={styles.header}>
          Peter
        </h2>
        <p>
          Peter jest głównym bohaterem gry Prey: Mooncrash. Jest hakerem zatrudnionym przez Kasmę do szpiegowania ukrytego obiektu Pyteas firmy Transtar na Księżycu. Został wysłany do zbadania miejsca po tym, jak zapadła cisza. Jednak znalazł się walcząc o przetrwanie z Tyfonem.
        </p>

        <h2 className={styles.header}>
          Historia
        </h2>
        <h3 className={styles.smallHeader}>
          Tło
        </h3>
        <p>
          Nie jest jasne, czy Peter jest pracownikiem Kasmy, czy też jest przez nich zatrudniony. Chociaż sugeruje się, że przyjął kontrakt od Kasmy, ponieważ jego rodzina potrzebowała pieniędzy, ze względu na jego kontrakt jako hakera prawdopodobnie ma doświadczenie w informatyce lub innej nauce, a także mógł być hakerem przed podpisaniem kontraktu i często był wysłany do więzienia, w wyniku czego Kasma musiał wykupić go za kaucją w zamian za szpiegostwo na TranStar, za co w nagrodę mieliby spłacić długi jego rodziny.
<br /><br />
Peter ma co najmniej jedną córkę, która chodzi do szkoły. Kasma wysyła mu pokaz slajdów swojej rodziny, który obejmuje jego córkę i dorosłą kobietę, prawdopodobnie jego żonę, która wydaje się być zapaloną ogrodniczką. Wydaje się, że bardzo troszczy się o swoją rodzinę, zwłaszcza o córkę, ponieważ trzyma jej zabawkę jako pamiątka.


        </p>
        <h3 className={styles.smallHeader}>
          Prey: Mooncrash
        </h3>
        <p>
          Jego obecna misja odbywa się w kosmosie, gdzie otrzymuje zniszczonego, uśpionego Operatora. Podłącza go i uruchamia symulację obiektu Pyteas widzianego oczami pięciu różnych pracowników stacji.<br /><br />

Kiedy jego kapsuła jest uszkodzona, musi ją naprawić. Następnie Peter podsłuchuje rozmowę między Bazyliszkiem, jego opiekunem, a Teddym, dyrektorem Kasmy. Tutaj okazuje się, że Teddy uważa zarówno Petera, jak i Bazyliszka za zbędnych, a Bazyliszek nie jest zadowolony ani z sytuacji jej, ani Petera.<br /><br />

Po tym, jak Peter wykona wszystkie zamówienia Kasmy, Teddy kontaktuje się z nim bezpośrednio. Zauważa, że koszt wydobycia go z jego kapsuły nie jest tego wart, ale planuje dotrzymać jej kontraktu i wysłać rodzinie obiecaną zapłatę. Następnie wyłącza jego kapsułę. Zanim jednak Peter zdąży się udusić, zakłada skafander przygotowany dla niego przez Bazyliszka. Następnie bierze klucz operacji Kasma Claire Whitten pozostawiony w Operatorze. Zastępując sterowanie kapsułą, rozbija ją w pobliżu Bazy Księżycowej Pyteasa.<br /><br />

Przeżywając katastrofę, Peter udaje się do bazy i wsiada do rakiety zmierzającej na Ziemię, nie wiedząc, że zabrał ze sobą Mimika przebranego za lalkę jego córki.


        </p>
    </>
  )
}
