import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const LukaGolubkin = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/golubkin.png')} alt="Luka Golubkin" />
        </div>
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/golubkin2.png')} alt="Luka Golubkin" />
        </div>

        <h2 className={styles.header}>
          Luka Golubkin
        </h2>
        <p>
          Luka Golubkin to zbiegły ochotnik znaleziony na pokładzie Talosa I. Jego identyfikator wolontariusza to V-010655-37, zwykle skracany do „37” w dziennikach, które go dotyczą.
        </p>
        <h2 className={styles.header}>
          Historia
        </h2>
        <h3 className={styles.smallHeader}>
          Tło
        </h3>
        <p>
          Golubkin jest skazanym mordercą z Rosji. Podobnie jak wielu innych „ochotników” na Talosie I, zgodził się zostać obiektem testowym w badaniach Morgan Yu nad życiem w sowieckim gułagu, najprawdopodobniej za obietnicę ułaskawienia za swoje zbrodnie, tak jak w przypadku Aarona Ingrama. Wydaje się, że ma jakieś wojskowe lub terrorystyczne doświadczenie, ponieważ wspomina, że ​​uczył się robić improwizowane miny, kiedy był na Ukrainie [będącej częścią Związku Radzieckiego]. <br /> <br />

W pewnym momencie Golubkin został wystawiony na działanie Tyfona Telepaty. Telepata odmówił skolonizowania umysłu Golubkina i faktycznie rozwinął do niego niechęć.<br /> <br />

Dzięki Neuromodom Golubkin otrzymał umiejętności kulinarne Willa Mitchella, szefa kuchni Talosa I. Kiedy Morgan po raz pierwszy spotyka Golubkina, poprosi ich o odebranie nagrody kulinarnej z kwatery Willa Mitchella. Golubkin prawdopodobnie odziedziczył emocjonalne przywiązanie Mitchella do tej nagrody.<br /> <br />

Neuromody dały również Golubkinowi psychiczną świadomość zbliżającego się zagrożenia ze strony Tyfona. Podobnie jak Morgan Yu, wyczuł, że w kosmosie jest jakiś złowrogi i żarłoczny organizm, i że eksperymenty Yu przyciągają go na Ziemię. Personel w Kwaterach Wolontariuszy zauważył, że spędzał godziny w milczeniu kołysząc się w swoim fotelu, nawiedzany przez niepokojące wizje. Wydawał się również być świadomy obcej natury neuromodów, nazywając je „czarnymi jajami” w ludzkich głowach.


        </p>
        <h3 className={styles.smallHeader}>
          Prey (2017)
        </h3>
        <p>
          Golubkin po raz pierwszy spotyka się w kwaterach załogi, gdzie udaje szefa kuchni stacji, Willa Mitchella. Dwóch telepatów w okolicy wydaje się równie niechętni do kontrolowania jego umysłu, jak ten, którego spotkał w eksperymencie, i zamknął się w kuchniach, aby uniknąć „marionetek”, które za nim wysyłają.<br /> <br />

Po zażądaniu od Morgana zneutralizowania Telepaty w jadalni, zanim zacznie mówić, Golubkin wysyła Morgana, aby odzyskał trofeum z pokoju Willa Mitchella. Po wykonaniu tej czynności wpuszcza Morgana do kuchni i oferuje dzielenie się sprzętem, który zgromadził w zamrażarkach, ale to sztuczka. Golubkin spróbuje uwięzić Morgana, którego, jak się wydaje, z opóźnieniem rozpoznał jako spokrewnionego z właścicielami TranStaru, w kuchennej zamrażarce, jako zemstę za eksperymenty, które wycierpiał. Po ucieczce Morgana z zamrażarki, Luka opuścił już kwatery załogi. Ustawił boczne drzwi za pomocą miny recyklera z zamiarem zabicia Morgana, gdyby uciekli, i zauważa jego rozczarowanie, gdy tak się nie dzieje. Zastawił te pułapki również w kilku innych miejscach.<br /> <br />

Danielle Sho jest pierwszą osobą, która poprosiła gracza o zabicie Golubkina za zamordowanie jej dziewczyny, Abigail Foy. Następnie gracz może znaleźć Golubkina w kapsule ratunkowej w zatoce kapsuł ratunkowych na moście Talos I. Spróbuje zwabić Morgana do kapsuły i zabić ich obu Ładunkiem Recyklera. Nawet jeśli gracz zauważy i rozbroi minę, Golubkin i tak popełni samobójstwo trucizną. Jednak możliwe jest pozbawianie go przytomności za pomocą Paralizatora zakłócającego i w przeciwieństwie do innych NPC pozostanie nieprzytomny do końca gry.
        </p>
        <h2 className={styles.header}>
          Osobowość i umiejętności
        </h2>
        <p>
          Golubkin jest brutalnym i niestabilnym człowiekiem, który wydaje się mieć jedynie słabe pojęcie o rzeczywistości: nie jest jasne, czy podszywa się pod Willa Mitchella, czy też naprawdę wierzy, że jest Willem Mitchellem. Odgryzł palce dr Mitsuko Tokaji, badaczce Talosa I, i przypuszcza się, że je połknął. Żywi nienawiść do TranStar i jego pracowników, co, jak sugeruje, wykracza daleko poza przeprowadzane na nim eksperymenty: wydaje się, że był zaangażowany w jakąś wojnę na Ukrainie, gdzie TranStar zaopatrywał opozycję. <br /> <br />

Golubkin był bardzo biegły w przemycaniu kontrabandy do systemu więziennictwa stacji. Obejmuje to dość niebezpieczne materiały, takie jak ładunki recyklingowe. Został już złapany z trzema w swoim posiadaniu przed wydarzeniami z gry.<br /> <br />

Wydaje się, że Golubkin żywi szczególną niechęć do prawdziwego Willa Mitchella. Nigdy nie jest jasne, czy w pewnym sensie wierzy, że faktycznie jest Willem Mitchellem, a jego złość odnosi się do widoku kogoś, kto pozornie udaje, że jest nim, lub jest związana z rolą Willa w jego wymuszonym neuromodowaniu. Oznaki tego można zobaczyć w tym, jak wydrapał twarz Willa na zdjęciu i całkowicie zniszczył zwłoki Willa, zamiast po prostu przechowywać je w stanie nienaruszonym, jak w przypadku innych jego ofiar.<br /> <br />

Można wywnioskować, że Golubkin jest kanibalem, biorąc pod uwagę, że przechowuje ciała w zamrażarce, a szczątki Mitchella umieścił w pudełku z napisem „tajemnicze mięso”.
        </p>
    </>
  )
}
