import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Fabricator = () => {
  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../grafika/fabrykator2.png')} alt="Fabrykator" />
        </div>
        <h2 className={styles.header}>
            Omówienie
        </h2>

        <p>
          Fabrykatory to specjalne urządzenia, które umożliwiają tworzenie przedmiotów przy użyciu materiałów znalezionych na Talosie I podczas Prey (2017) i Pyteas podczas Prey: Mooncrash.
        </p>
        <h2 className={styles.header}>
            Działanie
        </h2>
        <p>
          Fabrykatorzy to zasadniczo duże drukarki 3D, które wytwarzają użyteczne przedmioty z surowców. Kilku fabrykantów znajduje się w różnych miejscach na pokładzie Talosa I. Biuro Morgana Yu zawiera jednego, wraz z recyklerem do wydobywania materiałów wymaganych do produkcji z elementów wyposażenia. Aby odblokować produkcję określonego przedmiotu, należy najpierw zdobyć plan produkcji tego przedmiotu. Zdobycie niektórych planów produkcyjnych jest częścią celów misji, a niektóre są zdobywane jako losowy łup.
        </p>
    </>
  )
}
