import { NavLink } from 'react-router-dom';
import styles from './DropdownSubmenu.module.scss'
import interfaceAudio from '../../../../../../../sounds/interface.wav' 
const audio = new Audio(interfaceAudio)

const DropdownSubmenu = ({ item }) => {
  
  const handleAudio = () => audio.play()

  return (
        <NavLink onClick={handleAudio} to={item.path} className={styles.dropdownSubmenu}>
            {item.title}
        </NavLink>
  )
}

export default DropdownSubmenu