import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Throwable = () => {

  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/throwable.png')} alt="Schemat"/>
        </div>
        <h2 className={styles.header}>
          Broń miotana
        </h2>
        <p>W grze Prey z 2017 roku, broń miotana odgrywa ważną rolę w walce przeciwko niebezpieczeństwom na pokładzie statku kosmicznego Talos I. Gracze mają dostęp do różnorodnych rodzajów granatów i podobnych urządzeń, które mogą wykorzystać w swojej przygodzie. Te brońmiotane są niezwykle przydatne w walce z wrogami oraz rozwiązywaniu zagadek.</p>
    </>
  )
}
