import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const CathYu = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/cathYu.png')} alt="Catherine Yu" />
        </div>
        <h2 className={styles.header}>
          Catherine Yu
        </h2>
        <p>Catherine Yu jest jedną z czołowych twarzy zarządu TranStar. Jest także żoną Williama Yu oraz matką Morgana i Alexa Yu. Jest Niemką.</p>
        <h2 className={styles.header}>
          Osobowość i umiejętności
        </h2>
        <p>Wydaje się być zdolną strzelczynią, biorącą udział w Centennial European Shooting Championship ze swoją zrobioną na zamówienie strzelbą.</p>
    </>
  )
}
