import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const GrantLockwood = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "20%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/lockwood.png')} alt="Grant Lockwood" />
        </div>

        <h2 className={styles.header}>
          Grant Lockwood
        </h2>
        <p>
          Grant Lockwood jest zajmuje się ładunkiem na Talosie I podczas Prey (2017).
        </p>

        <h2 className={styles.header}>
          Historia
        </h2>
        <h3 className={styles.smallHeader}>
          Tło
        </h3>
        <p>
          Grant Lockwood zajmował się ładunkami na pokładzie Talosa I w ładowni. Po opuszczeniu sesji treningowej Grant skontaktował się z Jessicą Wiley, aby wyjaśnić mu, jak obchodzić się z ładunkiem. W końcu został zwolniony i miał opuścić stację ostatnim wahadłowcem na Ziemię. Niezadowolony z powodu zwolnienia Grant kradnie Neuromoda i chipset.
        </p>
        <h3 className={styles.smallHeader}>
          Prey (2017)
        </h3>
        <p>
          Można go znaleźć poza Talosem I, unoszącego się w przestrzeni poza pierścieniami orbitującymi stację, mniej więcej poza sekcją Zatoki wahadłowców. Obok ma walizkę. Sugeruje się, że albo próbował opuścić Talosa I w inny sposób i mu się nie udało, albo został w jakiś sposób wyrzucony w kosmos.
        </p>
    </>
  )
}
