import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const WeaponsLanding = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/weaponplan.png')} alt="Schemat" />
        </div>
        <h2 className={styles.header}>
          Broń
        </h2>
        <p>Stacja kosmiczna Prey, Talos 1, jest pełna stworzeń, które próbują cię zabić. Na szczęście po całej stacji rozrzucona jest również broń. Zaskakująco łatwo jest przegapić niektóre z tych broni — czasami po prostu przechodzisz obok nich, nie zauważając ich, a czasami po prostu trudno się do nich dostać, co często nie jest łatwym zadaniem. W szczególności na początku gracz będzie walczył nawet ze stosunkowo słabymi wrogami, takimi jak mimiki, dlatego znalezienie odpowiedniej broni oraz wiedza, kiedy i jak jej użyć, jest kluczem do dobrej zabawy w Prey. Oto jak wygląda arsenał, zarówno w grze, jak i w dodatku do gry Mooncrash.</p>
    </>
  )
}
