import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const CystLanding = () => {
  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/cyst.jpg')} alt="Cystoidy" />
        </div>
        <h2 className={styles.header}>
            Cystoidy
        </h2>
        <p>
          Cystoidy to typ Tyfona napotkany na Talosie I i wokół niego podczas Prey (2017) i bazy księżycowej Pyteasa podczas Prey: Mooncrash.
        </p>
    </>
  )
}
