import React from 'react'
import { Routes, Route } from 'react-router-dom'
import {Typhon} from './wikiPageContent/Typhon'
import { AdvLanding } from './wikiPageContent/AdvLanding'
import { ApexLanding } from './wikiPageContent/ApexLanding'
import { Apex } from './wikiPageContent/Apex'
import { ApexTendril } from './wikiPageContent/ApexTendril'
import { CystLanding } from './wikiPageContent/CystLanding'
import { Cyst } from './wikiPageContent/Cyst'
import { CystNest } from './wikiPageContent/CystNest'
import { PhanLanding } from './wikiPageContent/PhanLanding'
import { Phantom } from './wikiPageContent/Phantom'
import { VoltaicPhantom } from './wikiPageContent/VoltaicPhantom'
import { EthericPhantom } from './wikiPageContent/EthericPhantom'
import { ThermalPhantom } from './wikiPageContent/ThermalPhantom'
import { MimicsLanding } from './wikiPageContent/MimicsLanding'
import { Mimic } from './wikiPageContent/Mimic'
import { GreaterMimic } from './wikiPageContent/GreaterMimic'
import { EthericMimic } from './wikiPageContent/EthericMimic'
import { ThermalMimic } from './wikiPageContent/ThermalMimic'
import { VoltaicMimic } from './wikiPageContent/VoltaicMimic'
import { OpsLanding } from './wikiPageContent/OpsLanding'
import { MilitaryOper } from './wikiPageContent/MilitaryOper'
import { CorruptedOp } from './wikiPageContent/CorruptedOp'
import { Turret } from './wikiPageContent/Turret'
import { ControlledHuman } from './wikiPageContent/ControlledHuman'
import { Moonshark } from './wikiPageContent/Moonshark'
import { Nightmare } from './wikiPageContent/Nightmare'
import { Weaver } from './wikiPageContent/Weaver'
import { Telepath } from './wikiPageContent/Telepath'
import { Poltergeist } from './wikiPageContent/Poltergeist'

const Description = () => {
  return (
    <Routes path='/'>

      <Route index={true} element={<AdvLanding/>}/>
      <Route path='typhon' element={<Typhon/>}/>

      <Route path='apex_species'>
        <Route index={true} element={<ApexLanding/>}/>
        <Route path='apex' element={<Apex/>}/>
        <Route path='apex_tendril' element={<ApexTendril/>}/>
      </Route>

      <Route path='cystoids'>
        <Route index={true} element={<CystLanding/>}/>
        <Route path='cystoid' element={<Cyst/>}/>
        <Route path='cystoid_nest' element={<CystNest/>}/>
      </Route>

      <Route path='phantoms'>
        <Route index={true} element={<PhanLanding/>}/>
        <Route path='phantom' element={<Phantom/>}/>
        <Route path='voltaic_phantom' element={<VoltaicPhantom/>}/>
        <Route path='etheric_phantom' element={<EthericPhantom/>}/>
        <Route path='thermal_phantom' element={<ThermalPhantom/>}/>
      </Route>

      <Route path='mimics'>
        <Route index={true} element={<MimicsLanding/>}/>
        <Route path='mimic' element={<Mimic/>}/>
        <Route path='greater_mimic' element={<GreaterMimic/>}/>
        <Route path='etheric_mimic' element={<EthericMimic/>}/>
        <Route path='thermal_mimic' element={<ThermalMimic/>}/>
        <Route path='voltaic_mimic' element={<VoltaicMimic/>}/>
      </Route>

      <Route path='nightmare' element={<Nightmare/>}/>
      <Route path='weaver' element={<Weaver/>}/>
      <Route path='telepath' element={<Telepath/>}/>
      <Route path='poltergeist' element={<Poltergeist/>}/>

      <Route path='operators'>
        <Route index={true} element={<OpsLanding/>}/>
        <Route path='military_operator' element={<MilitaryOper/>}/>
        <Route path='corrupted_operator' element={<CorruptedOp/>}/>
        <Route path='turret' element={<Turret/>}/>
      </Route>

      <Route path='mind_controlled_human' element={<ControlledHuman/>}/>
      <Route path='moonshark' element={<Moonshark/>}/>
    
    </Routes>
  )
}

export default Description