import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Artax = () => {
  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../grafika/artax.png')} alt="Schemat" />
        </div>
        <h2 className={styles.header}>
            Omówienie
        </h2>
        <p>System napędowy Artax to możliwe do wytworzenia narzędzie na Talosie I podczas Prey (2017) i na Pyteas podczas Prey: Mooncrash.
          Artax Propulsion System is a zero g propulsion device that lets Morgan explore the space outside the station. The device can be crafted at any Fabricator on the station.
          Artax Propulsion System to urządzenie napędowe o zerowej grawitacji, które pozwala Morganowi badać przestrzeń poza stacją. Urządzenie można wytworzyć w dowolnym Fabrykatorze na stacji.
        </p>
        <h2 className={styles.header}>
            Działanie
        </h2>
        <h3 className={styles.smallHeader}>
          Prey (2017)
        </h3>
        <p>
          Głównym zastosowaniem Artax Propulsion System jest środek napędowy w środowiskach o zerowej grawitacji. Pozwala noszącemu skutecznie latać w tych miejscach, oferując pełny trójwymiarowy zakres ruchu i możliwość obracania się „do góry nogami”. <br /><br />
          System napędowy Artax posiada również zdolność przyspieszenia (odpowiednik sprintu w plecaku odrzutowym), której można użyć z pełną prędkością w dowolnym kierunku poziomym (funkcje wznoszenia/schodzenia z tego nie korzystają). Należy jednak zachować ostrożność podczas dopalania, ponieważ kolizje z dużą prędkością z dużymi obiektami i powierzchniami uszkodzą Morgana. <br /> <br />
          Ma również wartość w strefach normalnej grawitacji: może przerwać niebezpieczny upadek i może być używany do „prześlizgiwania się” przez szczeliny, które w przeciwnym razie byłyby zbyt szerokie, aby przeskoczyć. <br /> <br />
          APS ma dwa możliwe ulepszenia. Chipset ARTX Propulsion Gen 1 zwiększa prędkość szybowania SOA i czas jego trwania w normalnym g oraz zwiększa jego maksymalną prędkość doładowania w zerowym g; a ulepszenie Gen 2 dodaje do niego zdolność doładowania odrzutowego, która zwiększa początkowy ciąg w górę w obszarach o normalnym g.
        </p>
        <h3 className={styles.smallHeader}>
          Mooncrash
        </h3>
        <p>
          System napędowy Artax jest bardzo cenny podczas eksploracji krateru księżycowego podstawy Pytheas, ponieważ umożliwia jego użytkownikowi, w połączeniu z niską grawitacją, skakanie ze skały na skałę, unikając w ten sposób chodzenia po regolicie i ostrzegając Księżyc Rekiny ich obecności. Vijay Bhatia ma w swoim domyślnym wyposażeniu system napędowy Artax. Wszystkie inne postacie ucieleśnione przez Petera muszą go znaleźć lub wytworzyć (jeśli plan został znaleziony).
        </p>
    </>
  )
}
