import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Transcribe = () => {
  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../grafika/tcArt.png')} alt="Schemat" />
        </div>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../grafika/tcArt2.png')} alt="Schemat" />
        </div>
        <h2 className={styles.header}>
            Omówienie
        </h2>
        <p>
          "Obsługiwany osobisty asystent danych i urządzenie komunikacyjne wydawane wszystkim pracownikom TranStar." <br /> <br />
          TranScribe to przenośne urządzenie komunikacyjne opracowane przez firmę TranStar. Urządzenie obsługuje czat głosowy, pocztę e-mail, nawigację po stacjach i zarządzanie zapasami. Transcribe ma rozszerzalny wyświetlacz LCD, który umożliwia Morganowi dostęp do map, ekwipunku, instalacji neuromodów i innych form komunikacji.
        </p>
    </>
  )
}
