import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Lepik = () => {

  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../grafika/lepik.png')} alt="Lepik"/>
        </div>
        <h2 className={styles.header}>Ogólne</h2>

        <p className={styles.info}> 
          Gelifoam Lattice Organism Obstructor, w skrócie Działo GLOO czy LEPIK, to narzędzie, które można znaleźć na Talosie I podczas Prey (2017) i Pyteas podczas Prey: Mooncrash. <br />
          LEPIK to wszechstronne narzędzie, które strzela gelifoam, szybko twardniejącą, trudnopalną, nieprzewodzącą, klejopodobną pianką, która rozszerza się podczas utwardzania. Każdy kanister GLOO, którego używa jako amunicji, zawiera 42 skompresowane kulki żelowej pianki.

Narzędzie może być używane do chwytania wrogów tam, gdzie stoją, zakrywania odsłoniętych płonących rur i skrzynek elektrycznych lub zapewniania interesującej nowej ścieżki. Podobnie jak Łowczyni Boltcaster, Działo GLOO nie zadaje obrażeń i nie zabija wrogów innych niż Cystoidy, chociaż oficer ochrony (Alton Weber w łazienkach) w Talos I Life Support najwyraźniej popełnił samobójstwo, celując nim w jej głowę i strzelając strzał.


        </p>
        <h2 className={styles.header}>Ulepszenia</h2>
        <p>
          Działo GLOO jest „niestandardową bronią technologiczną” i dlatego wymaga neuromodów Lab Tech I i II, aby w pełni ulepszyć je za pomocą zestawów ulepszeń broni. „Współczynnik obezwładnienia” odnosi się do liczby trafień wymaganych do całkowitego unieruchomienia celu (innymi słowy, jest to odpowiednik obrażeń), podczas gdy „obsługa” to procent normalnej prędkości chodzenia, z jaką porusza się Morgan podczas strzelania z działa GLOO.
        </p>

        <h3 className={styles.smallHeader}>
          Standardowy wariant
        </h3>
        <p>
          Na Pytheas standardowe działa GLOO są bardziej skuteczne niż podstawowe standardowe i można je znaleźć w różnych miejscach, podobnie jak ich plany produkcyjne. Ich kolor tła w ekwipunku jest zielony, a ich wbudowane ulepszenia są losowe.
        </p>
        <h3 className={styles.smallHeader}>
          Zaawansowany wariant
        </h3>
        <p>
          Na Pytheas zaawansowane działa GLOO są bardziej skuteczne niż standardowe i można je znaleźć w różnych miejscach, podobnie jak ich plany produkcyjne. Ich kolor tła w ekwipunku jest niebieski, a ich wbudowane ulepszenia są losowe.
        </p>

        <h2 className={styles.header}>Działanie</h2>
        <p className={styles.info}>
          Działo GLOO jest niezwykle wszechstronne, mimo że nie zadaje obrażeń. Jego najbardziej oczywistym zastosowaniem jest unieruchamianie wrogów w celach bojowych lub do skanowania za pomocą Psychoskopu. Liczba trafień wymaganych do unieruchomienia wroga, stopień jego obezwładnienia i czas jego unieruchomienia zależą od tego, jak duży jest: Mimik zostanie całkowicie zamrożony po kilku strzałach, podczas gdy Upiór oddaje wiele strzałów i nadal będzie jest w stanie dokończyć każdy atak dystansowy, który rozpoczął przed trafieniem. Wrogowie otoczeni GLOO otrzymują dodatkowe obrażenia, zwłaszcza od Klucza.

Urządzenie ma również wiele zastosowań w prostym poruszaniu się po stacji. Morgan może z łatwością wspinać się na „splaty” GLOO za pomocą mechaniki chwytania półki, a ścieżki można łatwo tworzyć po przekątnej w górę ścian lub naprzemiennych boków szybu, aby dotrzeć do niedostępnych obszarów. GLOO może również ugasić większość pożarów (chociaż po pewnym czasie „wybuchnie”, jeśli przyczyną pożaru jest płonący przewód gazowy), zatykać dziury w przewodach gazowych, tymczasowo zatrzymywać wyładowania elektryczne z zepsutego sprzętu i uszczelniać wyrwy w kadłubie Talos I. GLOO jest również nieprzewodzący, dzięki czemu można go używać do tworzenia bezpiecznych mostów nad podłogami naelektryzowanymi.

Można go również użyć do stworzenia krótkotrwałych prowizorycznych barier blokujących drzwi lub utrudniających drogę mniejszym tyfonom, takim jak mimiki, lub przed cystoidami, które albo zostaną uwięzione, albo zdetonują na bańce GLOO, zabijając się w procesie.

Ważną informacją jest to, że ikony GLOO nie przylegają do szkła ani innych ikon GLOO, ponieważ żel jest witreofobowy i będą odbijać się od powierzchni szklistych oraz przylegać i rozszerzać się na następnej powierzchni, której dotkną.

Główną piętą achillesową działa GLOO jest szybkość przeładowania, która domyślnie jest bardzo niska. Co gorsza, po rozpoczęciu animacji gracz nie może zmienić broni, dopóki się nie zakończy, dlatego ulepszenie powinno być priorytetem. Obsługę można poprawić za pomocą zestawów ulepszeń broni i chipsetu „Heavy Gear Optimizer CRU”.

Ponieważ zawiera elektronikę, działo GLOO, podobnie jak wiązka Q i paralizator zakłócający, jest tymczasowo wyłączane przez każdy atak, który zadaje obrażenia od żywiołów elektrycznych, w tym łuki z uszkodzonego sprzętu i ataki dystansowe fantoma woltaicznego i technopaty.
        </p>
        <p>Wymagane materiały:
          <img className={styles.materialIcon} alt="Mineral icon" src={require('../../../../../../grafika/Mineral.png')} width="15" height="15"></img> 3,
          <img className={styles.materialIcon} alt="Synthetic icon" src={require('../../../../../../grafika/Synthetic.png')} width="15" height="15"></img> 3
        </p>
    </>
  )
}
