import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const PhanLanding = () => {
  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/phanSpecies.png')} alt="Fantomy" />
        </div>
        <h2 className={styles.header}>
            Fantomy
        </h2>
        <p>
          Fantom, czy też inaczej upiór (Typhon anthrophantasmus) to typ Tyfona spotykany na Talosie I w Prey i Pyteas w Prey: Mooncrash. Można napotkać cztery typy fantomów, z których każdy ma swój własny zestaw unikalnych zdolności i może zostać stworzony przez Morgana z ludzkich zwłok za pomocą zdolności Phantom Genesis. Operatorzy naukowi klasyfikują je wszystkie jako „Metempsychotyczne Resztki Organizmów”. Termin „Upiór” jest używany zamiennie w odniesieniu do gatunku jako całości, a także do ogólnych podgatunków, które opierają się na kulach kinetycznych i walce wręcz do ataku.
        </p>
    </>
  )
}
