import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const YuFamily = () => {
  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/66 - 0ZiNnp8.png')} alt="Rodzina Yu" />
        </div>
        <h2 className={styles.header}>
          Rodzina Yu
        </h2>
        <p>Rodzina Yu jest wpływową siłą w świecie Prey, a ich najnowsze osiągnięcia koncentrują się na badaniach dotyczących wielu najnowszych technologii w wielu dziedzinach, w tym neuromodu, pozwalającego na modyfikację umysłów ludzkich.</p>
    </>
  )
}
