import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const LifeSupport = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/lifeSupport.gif')} alt="Wspomaganie życia" />
        </div>
        <h2 className={styles.header}>
            Omówienie
        </h2>
        <p>
          Dział wspomagania życia to lokalizacja występująca w Prey (2017). <br /> <br />
          Podstawa życia w Talos I, talia podtrzymywania życia zawiera obfity zapas rzeczy związanych z samym przetrwaniem. Jeśli tryb podtrzymywania życia nie jest aktywny, żadna załoga nie może przeżyć na pokładzie stacji. <br /> <br />

Na pokładzie podtrzymywania życia inżynierowie zapewniają, że środowisko Talosa I pozostaje oddychające, a jakość powietrza jest wysoka, przetwarzają ścieki i oczyszczają wodę oraz przesyłają energię elektryczną z Elektrowni do innych miejsc wokół stacji.
        </p>
        <div className={styles.imageBox} >
            <img className={styles.image} src={require('../../../../../../grafika/lifeSupport1.png')} alt="Wspomaganie życia" />
        </div>
    </>
  )
}
