import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Phantom = () => {
  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/phantom.png')} alt="Fantom" />
        </div>
        <h2 className={styles.header}>
          Omówienie
        </h2>
        <p>
          Upiory to humanoidalne Tyfony, które Tkacze są w stanie stworzyć z ludzkich zwłok. Chociaż zwykle widać, jak tasują powoli, gdy zostaną sprowokowane, mają tendencję do poruszania się z dużą prędkością w kierunku gracza lub obszaru blisko niego. Ta umiejętność sprawia, że są niezwykle niebezpieczni w walce, ponieważ mogą szybko poruszać się za Morganem, jeśli zmierzą się bezpośrednio, a także niemal natychmiast pokonywać duże odległości, nawet przesuwając się obok pułapek wyzwalanych ruchem. Nie jest to jednak prawdziwa teleportacja, a fantomy raczej „fazują” bardzo szybko, nie mogąc w zdecydowanej większości przypadków poruszać się przez solidne ściany. Ta ich cecha nie wydaje się być oparta na ich potencjale psychoaktywnym, ponieważ są doskonale zdolni do zmiany, gdy są pod wpływem Psychoszoku lub Nadajnika Nullwave. Wydaje się, że Eteryczne Upiory mają bardziej zaawansowaną formę, ponieważ są w stanie podzielić się na słabszy duplikat i częściej się przesuwać. Morgan Yu może zainstalować słabszą odmianę tej zdolności za pomocą neuromodów, zwanych Phantom Shift. <br /><br />

Fantomy wykazują podobne do ludzkich wzorce zachowań w postaci ciekawej obserwacji swojego otoczenia, a nawet wykazano, że są w stanie naśladować ludzką mowę. Potrafią nawet powtarzać frazy, których nie słyszeli ludzie, których użyto do stworzenia zjawy. Chociaż oni, wraz z Poltergeistem, są jednymi z jedynych Tyfonów zdolnych do odtwarzania ludzkiej mowy, nigdy nie wykazano, że rozmawiają z ludźmi lub innymi zjawami, zamiast tego często papugują do siebie losowe zdania i frazy. Należy jednak zauważyć, że Phantom czasami mamrocze kontekstowo odpowiednie frazy, na przykład gdy rzuca okiem na gracza. Posunęli się nawet tak daleko, że nazwali Morgana, gdy zostali zauważeni, prawdopodobnie sugerując, że nie są całkowicie pozbawieni zdolności rozumienia mowy.
<br /><br />
Inne zachowania podobne do ludzi obejmują ich pozorną frustrację, gdy tracą ślad wroga, co mogą pokazać, rozkładając ramiona i agresywnie wymachując swoimi składowymi mackami. Chociaż stosunkowo rzadko uciekają przed walką w porównaniu z większością innych gatunków tyfonów, czasami uciekają nawet bez odczuwania Strachu. Można to zaobserwować, gdy Upiór odsuwa się od gracza po otrzymaniu ciężkich obrażeń, zyskując dużą odległość i klękając, gdy przestanie się poruszać. Uważaj, ponieważ nie boją się gracza długo, a po zauważeniu Morgana odzyskają determinację. Wiadomo, że niektóre Phantomy kucają niesprowokowane, mimo że nie biorą udziału w walce, i pozostaje tajemnicą, co powoduje, że takie zachowanie występuje u nich.
<br /><br />
W trakcie gry Morgan może napotkać szereg Upiorów, które są członkami załogi Talosa I, którzy zostali nawróceni przez Tyfona. Osoby te będą miały te same bransoletki śledzące i imiona, które miały, gdy były ludźmi, a niektóre z nich trzeba pokonać, aby zdobyć przedmioty powiązane z określonymi celami.
        </p>
        <h2 className={styles.header}>
          Design
        </h2>
        <p>
          Wszystkie Upiory to czarne, cieniste, humanoidalne stworzenia. Wydają się zgarbieni, mają szyje lekko wydłużone do przodu i, jak wszystkie Tyfony, wydają się być zrobione z czarnej, półstałej substancji przypominającej wąsy. Posiadają również dwanaście świecących białych plam zamiast oczu, a ich kończyny są cienkie i smukłe. Każde z ich ramion składa się z trzech splecionych macek, które czasami się rozdzielają; gdy są owinięte wokół siebie, końcówki tych macek wydają się naśladować palce człowieka.
        </p>
        <h2 className={styles.header}>
          Strategie
        </h2>
        <ul className={styles.wikiPageList}>
          <li>Działo GLOO jest niezwykle skuteczne w przygważdżaniu dowolnego nietermicznego fantomu, co pozwala na stosunkowo łatwe zabicie go. Jeśli się uwolnią, po prostu ponownie je GLOO i kontynuuj atak. Uwięziony w GLOO, fantom nie może wystrzelić żadnych wybuchów kinetycznych poza tymi, które już zaczął ładować. Bądź gotowy do ruchu, jeśli GLOOujesz Upiora i nadal widzisz kulę zbliżającą się do Ciebie.</li>

    <li>Strzelby są w stanie zadać Upiorowi duże obrażenia, zwłaszcza po ogłuszeniu go, aby zmniejszyć dystans. Strzelba jest najskuteczniejsza z bliskiej odległości, powodując, że upiór otrzymuje maksymalne obrażenia, mając jednocześnie szansę zrzucić go z nóg. W połączeniu z ukryciem lub anulowaniem psi, widmo będzie miało niewielkie szanse na przeżycie. Po prostu uważaj na swoją amunicję.</li>

    <li>Wieżyczki mają zmienną skuteczność. Chociaż zadają dużo obrażeń, wszystkie typy Phantom są w stanie obezwładnić jednego na wiele sposobów. Najlepiej sprawdzają się jako wsparcie w walce z fantomami eterycznymi, termicznymi i podstawowymi. Wieżyczki nie są zalecane do walki z Voltaic Phantom, ponieważ wszystkie ich ataki mogą wyłączyć wieżyczkę, a sama ich obecność może zmusić ją do wyłączenia.</li>

    <li>Większość Phantomów zwykle woli iść powoli w kierunku celu, używając ataków dystansowych. Zalecane jest utrzymywanie dystansu, ponieważ daje to więcej czasu na uniknięcie ich pocisku, a w przypadku potężniejszych Phantomów uchroni cię przed efektami pasywnego generowania żywiołów. Wyjątkiem jest Upiór Eteryczny, który raczej lubi przesuwać się w stronę gracza, dlatego powinien być wyposażony w broń i umiejętności lepiej dopasowane do wrogów z bliskiej odległości (np. Strzelba lub Psychoszok). Pamiętaj, że wszystkie Upiory, z wyjątkiem wariantu Voltaic, nadal mogą przesuwać się w Twoją stronę i są do tego skłonne, jeśli ich zdolności psioniczne zostaną anulowane lub stracą z Tobą kontakt wzrokowy.</li>

<li>Paralizator Disruptor jest bardzo skuteczny na wszystkie Phantomy, z wyjątkiem Voltaic Variant. Choć sam w sobie nie zadaje bezpośrednich obrażeń, Paralizator sprawia, że ​​trafione przez niego Upiory nie mogą się poruszać przez krótki czas, co jest idealne zarówno do bezpośredniego rozprawienia się z widmem za pomocą wieżyczki lub strzelby, jak i całkowitego uniknięcia walki przez ogłuszając go wystarczająco długo, abyś mógł się przemknąć.</li>

<li>
    Pasywne aury Voltaic i Thermal (jeśli są świadome gracza) Phantoms mogą powodować zapalenie się plam oleju i materiałów wybuchowych. W pobliżu takich środowisk należy zachować ostrożność.
</li>
    <li>Jeśli gracz posiada zdolność Phantom Genesis, może tworzyć Upiory z ludzkich zwłok, aby o nie walczyć. Voltaic Phantoms, w szczególności, są skuteczne w radzeniu sobie ze Spaczonymi Operatorami i Operatorami Wojskowymi ze względu na ich zdolność do unieszkodliwiania wrogów typu maszynowego swoimi atakami oraz zwiększone obrażenia zadawane wspomnianym typom wrogów.</li>

    <li>Zdolność tyfona Psychoshock i nadajnik Nullwave mogą zniweczyć ofensywne zdolności psioniczne Phantomów, wyłączając ich aury, a także uniemożliwiając im używanie ataków dystansowych, przy czym ten pierwszy zadaje bezpośrednie obrażenia i zwiększa obrażenia otrzymywane z dowolnego źródła. Należy pamiętać, że zmiana biegów nie jest wyłączona, a fantom może to wykorzystać, aby skrócić dystans do jedynego ataku, którego może teraz użyć, lub uciec i skrócić efektywny czas, na jaki jego zdolności są wyłączone.</li>

    <li>Ładunki EMP mogą tymczasowo wyłączyć elektryczną aurę Voltaic Phantom, powstrzymując go przed używaniem tej zdolności przez mniej więcej połowę czasu, w jakim zrobiłaby to Nullwave.</li>

    <li>Dźwignia zmieni dowolny przedmiot w pokoju w broń, przy czym wyższe poziomy Dźwigni zazwyczaj zadają większe obrażenia. Nawet wariant Phantom może mieć znacznie zmniejszone zdrowie po dobrze wycelowanym strzale Reployera.</li>

<li>Klucz nie jest zalecany do walki z Fantomami. We wczesnej fazie gry może być trudno zadać większe obrażenia podstawowemu Phantomowi, używając tylko walki wręcz. Nie zaleca się również umieszczania neuromodów w celu zwiększenia mocy klucza wyłącznie do walki z Widmami, ponieważ zdolności generujące aurę i większe pule zdrowia wariantów Widmo spowodują, że zużyjesz więcej zasobów zdrowia i naprawy kombinezonu, niż gdybyś zdecydował się stosować strategię dystansową. Jeśli jednak jesteś zaangażowany, mieszanka przedmiotów Psi Nullification, Gloo i Stun Gun lub ładunków EMP z pewnością pomoże ci w walce wręcz z dowolnym Phantomem.</li>

    <li>Pole nośne może wyłączyć wiele Widm naraz, jeśli są blisko siebie, zadać duże obrażenia, jeśli złapie w nie więcej niż jeden przedmiot lub zostanie umieszczone pod sufitem, ma przyzwoity czas odnowienia i jest skuteczne przeciwko każdemu typowi Widma, powstrzymując je przed poruszaniem się jak długo to trwa. Może warto użyć kilku neuromodów na tej zdolności.</li>

    <li>Uderzenie Kinetyczne, Super Termiczne i Wybuch Elektrostatyczny są skutecznymi umiejętnościami Psi, przy czym Uderzenie Kinetyczne zadaje największe obrażenia i jest w stanie zrzucić Upiory z nóg, Super Termiczne jest używane jako pułapka na patrolującego Upiora, a Wybuch Elektrostatyczny może ogłusz Upiora podobnie jak Paralizator Zakłócający. Należy pamiętać, że warianty Phantom ze specjalnymi powinowactwami, takimi jak ciepło lub woltaika, będą naturalnie odporne na niektóre rodzaje obrażeń (odpowiednio termiczne i elektrostatyczne).</li>



        </ul>
    </>
  )
}
