import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Telepath = () => {
  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/telepathArt.png')} alt="Telepata" />
        </div>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/telepath1.png')} alt="Telepata" />
        </div>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/telepath2.png')} alt="Telepata" />
        </div>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/telepath4.png')} alt="Telepata" />
        </div>
        <h2 className={styles.header}>
          Telepata
        </h2>
        <h3 className={styles.smallHeader}>
          Talos I
        </h3>
        <p>
          Telepaci to organizmy Typhon, które służą jako mini-bossowie w Prey. Są produkowane przez Tkaczy z wybranych Mimików i wydają się być adaptacją Tyfona do obecności wrogich, czujących form życia. Unoszą się w otoczeniu i obezwładniają umysły każdego, kto ma pecha znaleźć się w ich obecności, wykorzystując je zarówno jako sieć sensoryczną, jak i bomby samobójcze. Psychoskop chroni przed tą obezwładniającą zdolnością, co wyjaśnia, dlaczego telepata nie przejmuje po prostu kontroli nad Morganem Yu, gdy go napotka, tak jak ma to miejsce w przypadku ludzi, którzy nie noszą psychoskopów. Telepaci mogą przetrwać i poruszać się w środowisku próżni, dzięki czemu można ich spotkać zarówno wewnątrz, jak i na zewnątrz Talosa I. <br /><br />

Kontrolowani umysłowo ludzie opętani przez telepatów są świadomi swojego zniewolenia, ale nie są w stanie nic z tym zrobić. Oprócz dowodzenia hordą niewolników umysłów, Telepaci mogą emitować samonaprowadzające się kule energii psychicznej, które zadają niemal śmiertelne obrażenia, które nie tylko anulują Psi celu na bardzo krótki czas, ale także zamazują mu wzrok i mogą spowodować wstrząs mózgu . Telepaci z Talosa I mogą również wyemitować rodzaj krótkiej, wielokierunkowej fali uderzeniowej Psychoshock, jeśli się do nich zbliżą.<br /><br />

Kiedy Telepata próbuje zaatakować cel przy użyciu człowieka kontrolowanego umysłem lub po prostu chce pozbyć się jednego ze swoich niewolników, powoduje to eksplozję głowy niewolnika. Eksplozja zarówno zadaje obrażenia, jak i anuluje zdolności Psi dowolnego celu w promieniu wybuchu.<br /><br />

Kiedy Morgan aktywuje zraszacze w szklarni Arboretum, Telepata znajdujący się w nim na krótko ucieka ze szklarni, co sugeruje, że Tyfon, a przynajmniej Telepaci, niechętnie przebywają w obecności wody.


        </p>
        <h3 className={styles.smallHeader}>
          Pytheas
        </h3>
        <p>
          Telepaci z księżycowej bazy Pyteas są podobni do swoich odpowiedników z Talosa I. Jednak ich związek z ich zniewolonymi poddanymi jest bardziej widoczny, ponieważ kontrolowani umysłowo ludzie są powiązani z Telepatą fioletowymi strunami energii psionicznej.<br /><br />

Telepaci Pyteasa mają również charakterystyczną cechę polegającą na tym, że są otoczeni fioletową tarczą Backlash, najwyraźniej napędzaną przez ludzi, których kontrolują. Każda próba zastrzelenia Telepaty, gdy jest on chroniony przez to pole energii, wywoła strach w umyśle strzelca, podczas gdy Telepata pozostanie nietknięty. Pole energii znika dopiero wtedy, gdy wszyscy ludzie kontrolowani przez Telepatę zostaną zabici lub w inny sposób oddzieleni od jego wpływu. Telepata również nie wystrzeli swoich psionicznych wybuchów, dopóki pole siłowe jest aktywne, raczej polegając na swoich ludzkich bombach, aby zaatakować swoje cele.


        </p>
        <h2 className={styles.header}>
          Design
        </h2>
        <p>
          Podobnie jak inne Tyfony, Telepaci składają się z poruszającej się masy czarnych wąsów. Mają amorficzny kształt, a ich wąsy zwisają i zachodzą na siebie. Świecąca fioletowo-biała kula z przodu Telepaty wydaje się służyć jako oko. Telepaci na Pyteas są chronieni fioletowym polem siłowym podczas kontrolowania ludzkich niewolników i mają fioletową aurę, gdy nie są osłonami, co prawdopodobnie łatwiej odróżnia ich od technopatów.
        </p>
        <h2 className={styles.header}>
          Strategia
        </h2>
        <p>
          Jeśli Telepata zwabił dużą grupę Ludzi do swojej służby, Ludzie powinni być twoim pierwszym celem, ponieważ są w stanie zadać Morganowi duże obrażenia za pomocą taktyki samobójczej, w której eksplodują im głowy. Użyj nadajnika Nullwave, ogłuszacza lub Mindjack Typhon, aby ich znokautować, a następnie rozpraw się z Telepatą. Kontrolowani umysłowo ludzie zazwyczaj ostrzegają cię o swojej obecności płaczem lub krzykiem. Działo GLOO jest bardzo skuteczne w tymczasowym wyłączaniu ich, jeśli inne, szybsze opcje nie są dostępne. Jest to również bardzo pomocne, jeśli Telepata leci wysoko, ponieważ może odnieść obrażenia od upadku, gdy jest wystarczająco pokryty GLOO.<br /><br />

Sam Telepata atakuje przede wszystkim ciągłym strumieniem kul Psychoszoku obezwładniających Psi. Te kule poruszają się powoli, ale śledzą swój cel, nawet do tego stopnia, że ​​prowadzą poruszającą się istotę, aby zapewnić kolizję. Jednak kule te wolno reagują na zmiany kierunku, co oznacza, że ​​najlepszą strategią jest skłonienie ich do podążania w jednym kierunku, a następnie szybki bieg w przeciwnym kierunku na krótko przed uderzeniem. Zbytnie zbliżenie się do Telepaty spowoduje, że wyemituje on wszechkierunkowy podmuch energii. Ładowanie tego ataku trwa około dwóch sekund i jest oznaczone symbolem zagrożenia nałożonym na Telepatę, co daje ci niewiele czasu na wycofanie się. Atak ten może zadawać obrażenia i zabijać ludzi w jego pobliżu, niezależnie od tego, czy są kontrolowani umysłowo, czy nieprzytomni, chociaż otrzymują znacznie mniejsze obrażenia niż gracz. Optymalną strategią jest zatem pozostawanie na średnim dystansie podczas walki z Telepatami, ponieważ daje to czas na reakcję na ich samonaprowadzające się kule bez wywoływania ataku fali uderzeniowej.<br /><br />

Telepaci napotkani na zewnątrz Talosa I nigdy nie będą mieli ludzi do kontrolowania, więc główna strategia pozostaje taka sama. Zwróć uwagę, że nagła zmiana kierunku w mikrograwitacji trwa dłużej niż zwykle, ponieważ musisz przeciwdziałać obecnemu pędowi, zanim ponownie zaczniesz się poruszać. Poruszanie się z mniejszą prędkością pozwala na szybsze zwroty, ale może sprawić, że staniesz się łatwiejszym celem śledzenia kul, więc trzeba zachować równowagę.<br /><br />
Jeśli pokonasz Telepatę, gdy ludzie wciąż żyją i są przez niego kontrolowani umysłowo, stracą przytomność. Notatka o zmianie obszaru mówi, że ludzie uwolnieni spod kontroli umysłu Tyfona w końcu się obudzą i udają się w bezpieczne miejsce (w grze właściwie całkowicie przestają istnieć). Jeśli jednak uda ci się uratować Rani Chaudhary podczas jej powiązanego zadania, nie straci ona przytomności i zamiast tego uda się do ładowni B, aby dołączyć do tamtejszych sił bezpieczeństwa.<br /><br />

Jeśli użyjesz Mindjacka na Rani Chaudhary, wyciągnie ona pistolet i zaatakuje nim Telepatę, zanim Telepata odzyska nad nią kontrolę. Użycie Nadajnika Nullwave lub ataku elektrycznego na Telepatę zapobiegnie temu, więc możesz podwoić się z Rani, aby go zaatakować.<br /><br />

W Prey: Mooncrash Telepaci są chronieni potężnym fioletowym polem energetycznym, które czyni ich niewrażliwymi na klasyczne ataki. Konieczne jest zabicie lub przynajmniej obezwładnienie wszystkich pobliskich Kontrolowanych Umysłem Ludzi kontrolowanych przez Telepatę, aby dezaktywować tę barierę i sprawić, że Telepata będzie podatny na obrażenia. Jednak bariera może zostać przedwcześnie zniszczona, jeśli znajdzie się w obszarze wybuchu nadajnika Nullwave.



        </p>
    </>
  )
}
