import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const EscapePods = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/podBay.png')} alt="Kapsuły" />
        </div>
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/pod.png')} alt="Kapsuły" />
        </div>
        <div className={styles.imageBox} style={{width: "40%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/podInside.png')} alt="Kapsuły" />
        </div>
        <div className={styles.imageBox} >
            <img className={styles.image} src={require('../../../../../../prey-graphics/podConc.png')} alt="Kapsuły" />
        </div>
        <div className={styles.imageBox} >
            <img className={styles.image} src={require('../../../../../../prey-graphics/podConc2.png')} alt="Kapsuły" />
        </div>
        <h2 className={styles.header}>
            Omówienie
        </h2>
        <p>
          Stanowiska kapsuł ratunkowych można znaleźć w różnych miejscach na pokładzie Talosa I i w bazie księżycowej Pyteas. Zatoki kapsuł ratunkowych zostały zainstalowane na stacjach na wypadek sytuacji awaryjnych, które zmusiłyby załogi Talosa I i Pyteasa do ewakuacji ich odpowiednich obiektów. <br /> <br />
        </p>
        <h3 className={styles.smallHeader}>
          Prey (2017)
        </h3>
        <p>
          Na Talosie I znajdują się trzy zatoki kapsuł ratunkowych, połączone z każdym głównym węzłem komunikacyjnym, do którego można dostać się główną windą, w tym zatoką wahadłową (połączoną z lobby Talos I), mostem Talos I (połączonym z arboretum) i podtrzymywaniem życia. Alex Yu ma również prywatną kapsułę ratunkową zacumowaną w swoim biurze w Arboretum.<br /> <br />

Każda zatoka kapsuł ratunkowych ma sześć białych kapsuł ratunkowych w kształcie kuli z miejscami dla ośmiu osób (łącznie 48 miejsc na zatokę). Drzwi kapsuły ratunkowej muszą być zamknięte przed startem. Można je zamykać i otwierać z wnętrza kapsuły ratunkowej lub z zewnątrz za naciśnięciem przycisku. Każda kapsuła ratunkowa jest również ponumerowana, np. kapsuła ratunkowa I-33.<br /> <br />

        </p>
        <h3 className={styles.smallHeader}>
          Mooncrash
        </h3>
        <p>
          Pyteas zawiera w sumie trzy zatoki kapsuł ratunkowych; po jednym dla każdego ze swoich rejonów, z wyjątkiem Aneksu dla załogi. Sądząc po ich kamiennych ścianach, wszystkie znajdują się pod powierzchnią Księżyca. Wszystkie trzy przęsła mają ten sam układ: duża otwarta przestrzeń z dwoma magazynami na pierwszym poziomie i miejscem na trzy kapsuły ratunkowe zablokowane Bramą Tyfonu na drugim piętrze, do którego można dotrzeć schodami po lewej stronie lub Szyb grawitacyjny po prawej stronie obszaru. <br /> <br />

Kapsuły ratunkowe używane w Pyteas są takie same jak te na Talosie I.
        </p>
        <h2 className={styles.header}>
            Historia
        </h2>
        <h3 className={styles.smallHeader}>
          Tło
        </h3>
        <p>
          Kapsuły ratunkowe na Talosie I prawdopodobnie nigdy nie były używane przed wybuchem Tyfonu, ponieważ nie datuje się żadnego wydarzenia wymagającego ewakuacji. Nieznane ogółowi społeczeństwa, kapsuły ratunkowe uległy awarii, przez co nie nadawały się do użytku w nagłych przypadkach. Inżynier pokładowy Frank Jones wraz z Alexem Yu, Emmanuellą DeSilvą i Emmanuelem Mendezem byli jedynymi, którzy o tym wiedzieli. Jones miał właściwie naprawić kapsuły ratunkowe, ale Alex Yu odmówił mu, ponieważ miał niewłaściwe części. Frank Jones i Emmanuella DeSilva są jedynymi znanymi członkami załogi, którym udało się wystrzelić kapsułę ratunkową po tym, jak Jones naprawił ją w Shuttle Bay, ale śruby włazu wyjściowego nie zadziałały i nie byli w stanie uciec ze stacji, utknęli w wyrzutni. Kirk Remmer, Erica Teague i kilku ocalałych z ładowni próbowało uciec w kapsułach zacumowanych w zatoce podtrzymywania życia, ale jak w każdej innej zatoce nie udało im się wystartować. Remmer skontaktowała się z Aliką James i zapytała, czy mogłaby wystrzelić kapsułę z miejsca, w którym siedziała, ale nie była w stanie tego zrobić, pozostawiając Remmera i innych ocalałych otwartych na ataki Tyfona, z których ostatecznie zginęli. <br /> <br />

W przeciwieństwie do Talosa I, strąki w Pyteas nie działały całkowicie nieprawidłowo. Jednak w wybranych kapsułach brakowało chipów nawigacyjnych potrzebnych do latania kapsuły. Chociaż istnieje potencjalnie łącznie dziewięć kapsuł ratunkowych, w symulacji Kasma są obecne tylko dwie z każdego biegu rozpoczynającego się po pierwszym (w tym biegu jest tylko jedna kapsuła). Nie wiadomo, czy oznacza to, że pracownicy byli w stanie uciec przed Pyteaszem w strąkach. Z dwóch kapsuł jeden działa, ale brakuje mu chipa nawigacyjnego. Druga kapsuła jest zepsuta i nie można jej użyć do ucieczki, ale można jej użyć jako części zamiennych do naprawy drugiej kapsuły. Podczas wypełniania celu fabularnego Andriusa Alekny pokazana jest trzecia kapsuła, która nie pojawi się w żadnym innym biegu i której używa jako części tej historii. Lokalizacja tych kapsuł jest wybierana losowo między zatokami przy każdym resecie karty SIM.


        </p>
        <h3 className={styles.smallHeader}>
          Prey (2017)
        </h3>
        <p>
          Zatoka kapsuł ratunkowych Shuttle Bay znajduje się na poziomie 1 placówki. Gdy tylko Morgan Yu wejdzie, Frank Jones przekaże Yu przez radio z wnętrza zablokowanej kapsuły ratunkowej. Wyjaśnia sytuację swoją i DeSilvy i błaga Yu o pomoc. To, co stanie się dalej, zależy od gracza: gracz może wejść na zewnątrz Talosa I i otworzyć właz, przygotowując kapsułę ratunkową do startu, ratując życie Jonesa i DeSilvy lub wystrzelić kapsułę ratunkową bez otwierania włazu z konsoli kapsuły ratunkowej , zmuszając ich do błagania o życie, zanim zostaną zabici. Gracz może zobaczyć, jak zniszczona kapsuła ratunkowa opuszcza stację przez okno w zatoce kapsuły ratunkowej. Jedna z kapsuł ratunkowych ma niesprawne drzwi, w których znajdują się przedmioty dla gracza. Dostęp do kapsuły ratunkowej można uzyskać tylko za pomocą mocy Tyfona. Drew Springer, Donald „Skipper” Hall, Leon Woods, Todd Matsuyama i Nils Kjaergaard zginęli w tej Escape Pod Bay. <br /> <br />

Ta zatoka kapsuły ratunkowej jest wyjątkowa, ponieważ ma konsolę kapsuły ratunkowej, której nie ma żadna inna zatoka.


        </p>
    </>
  )
}
