import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Rsv77 = () => {

  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../grafika/rsv77.png')} alt="Rsv77"/>
        </div>
        <h2 className={styles.header}>Ogólne</h2>
        <p className={styles.info}>Zakłócacz neuroelektryczny RSV-77, potocznie znany jako Zakłócacz lub po prostu Paralizator, to broń znaleziona na Talosie I podczas Prey (2017) i na Pyteasie podczas Prey: Mooncrash. <br />
        Ta ręczna broń elektrowstrząsowa Throne Tactical unieruchamia zarówno ludzi, jak i maszyny za pomocą prądu elektrycznego o krótkim zasięgu. Ogłusza również Tyfona na krótki okres czasu.

Zakłócacz można znaleźć na drugim piętrze Foyer Oddziału Neuromod przy zwłokach V-010255-01 w odległym rogu, obok zablokowanego panelu dostępu do konserwacji. Można go również znaleźć w Pokoju Przesłuchań Symulacji na górze niebieskich rejestratorów, gdzie Morgan znajduje klucz do kapsuły ratunkowej Alexa podczas Kim jest grudzień? zadanie poboczne. Inne lokalizacje od wczesnej do środkowej fazy gry obejmują zwłoki znalezione w GUTS. dostęp w Arboretum oraz w kwaterach załogi przed stołówką.



</p>
        <h2 className={styles.header}>Ulepszenia</h2>
        <p>
          Paralizator Disruptor jest „bronią bezpieczeństwa” i dlatego wymaga neuromodów Gunsmith I i II, aby w pełni ulepszyć go za pomocą zestawów ulepszeń broni.


        </p>
        <h2 className={styles.header}>Działanie</h2>
        <p className={styles.info}>
          Zakłócacz może być użyty do tymczasowego unieruchomienia większości tyfonów, jak również operatorów/wieżyczek napotkanych na Talosie I, chociaż Woltaiczne Upiory są odporne na Zakłócacz. Może być również używany do obezwładniania ludzi bez zabijania ich, co może się przydać przeciwko ludziom kontrolowanym przez umysł. Musi zostać naładowany przez przytrzymanie przycisku strzału, zanim będzie można go wystrzelić. Utrudnia to efektywne wykorzystanie.

Po ulepszeniu są niezwykle skuteczne w zadawaniu obrażeń technopatom.

Disruptor Stun Gun to niezwykle cenny element wyposażenia do noszenia w księżycowej bazie Pytheas. Rzeczywiście, można go użyć do tymczasowej dezaktywacji Wrót Tyfona, umożliwiając jego właścicielowi ucieczkę przed potężnymi Tyfonami, zwłaszcza Księżycowymi Rekinami; lub dotarcie do obszarów leżących za takimi bramami, nawet jeśli w pobliżu są jeszcze jakieś Tyfony; lub jeśli nosiciel posiada zbyt wiele zdolności neuromodów opartych na tyfonie, które powodują, że bramy identyfikują nosiciela jako tyfona.


        </p>
        <p>Wymagane materiały:
          <img className={styles.materialIcon} alt="Mineral icon" src={require('../../../../../../grafika/Mineral.png')} width="15" height="15"></img> 1,
          <img className={styles.materialIcon} alt="Synthetic icon" src={require('../../../../../../grafika/Synthetic.png')} width="15" height="15"></img> 2
        </p>
    </>
  )
}
