import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Powerplant = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/powerplant.gif')} alt="Elektownia" />
        </div>
        <h2 className={styles.header}>
            Omówienie
        </h2>
        <p>
          Elektrownia jest rdzeniem źródła zasilania na Talosie I w Prey (2017).

<br /> <br />
          Wytwarza i utrzymuje energię dla całej stacji za pomocą zawartego w niej reaktora termojądrowego SuperForge. Tam też powstaje sztuczna grawitacja stacji.


        </p>
        <div className={styles.imageBox} >
            <img className={styles.image} src={require('../../../../../../grafika/powerplant1.png')} alt="Elektownia" />
        </div>
    </>
  )
}
