import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const WilliamMitchell = () => {
  return (
    <>
        
        <div className={styles.imageBox} style={{width: "20%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/will.png')} alt="William Mitchell" />
        </div>
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/will2.png')} alt="William Mitchell" />
        </div>

        <h2 className={styles.header}>
          William Mitchell
        </h2>
        <p>
          William Mitchell był szefem kuchni Talosa I, o którym mowa w Prey (2017).
        </p>

        <h2 className={styles.header}>
          Historia
        </h2>
        <h3 className={styles.smallHeader}>
          Tło
        </h3>
        <p>
          Był wyjątkowo utalentowanym, wielokrotnie nagradzanym szefem kuchni. Przed wyjazdem na Talosa I Mitchell był szefem kuchni w Instytucie Kosmicznym. <br /> <br />

Kiedy Annalise Gallegos i Quinten Purvis postanowili ujawnić prawdę o eksperymentach przeprowadzonych na Talosie I, Mitchell zgodził się przemycić trochę jedzenia do pojemnika, którego Purvis zamierzał użyć do powrotu na Ziemię.<br /> <br />

Również Morgan Yu przeprogramowuje swojego pomocnika Operatora Skillet, któremu Mitchell jest bardzo wdzięczny.<br /> <br />

Jego talenty zostały skopiowane do Neuromoda, a następnie wszczepione do Ochotnika 37 jako test. Po tym, jak Typhony wydostały się z przechowalni, Ochotnik 37 zamordował Mitchella, poćwiartował jego ciało i umieścił je w pudełku w zamrażarce w swojej kuchni. Ochotnik 37 następnie udaje Mitchella, kiedy konfrontuje się z graczem w kwaterach załogi.


        </p>
        <h3 className={styles.smallHeader}>
          Prey (2017)
        </h3>
        <p>
          Morgan może znaleźć „podejrzane mięso” w kuchennej zamrażarce, która ma być szczątkami Mitchella. Wewnątrz nich można znaleźć bransoletkę śledzącą Williama.
        </p>
    </>
  )
}
