import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Emp = () => {

  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../grafika/emp.png')} alt="Emp"/>
        </div>
        <h2 className={styles.header}>Ogólne</h2>
        <p className={styles.info}>Ładunek EMP emituje impuls elektromagnetyczny, który tymczasowo zakłóca pobliską elektronikę, w tym elektronikę użytkownika. Efekt ten trwa tylko przez 10 sekund, po czym maszyna, na którą ma wpływ, ponownie zaczyna działać. Nie mają żadnego wpływu na podmioty nieposiadające elektroniki, poza potencjalnym ostrzeganiem ich o obecności użytkownika.</p>
        <h2 className={styles.header}>Działanie</h2>
        <p className={styles.info}>Urządzenia te mogą być używane przeciwko skorumpowanym operatorom, wrogim wieżyczkom, zaalarmowanym bramom tyfonów i technopatom i są równie skuteczne w zakłócaniu tych ostatnich jak nadajniki fal zerowych, ale robią to przez około połowę krótszego czasu. Można ich również używać do wyłączania uszkodzonych węzłów zasilania i innych przeszkód elektrycznych, takich jak duże wentylatory znajdujące się w GUTS. Są niezwykle przydatne przeciwko hordom wrogich operatorów wojskowych, którzy stają się głównymi wrogami Morgana pod koniec gry podstawowej. Jeśli użyjesz jednego punktu z bliska, bądź przygotowany na to, że cała twoja elektronika, w tym system napędowy Artax, działo GLOO i wiązka Q, będzie działać nieprawidłowo przez 10 sekund.</p>
        <p>Wymagane materiały:
          <img className={styles.materialIcon} alt="Mineral icon" src={require('../../../../../../grafika/Mineral.png')} width="15" height="15"></img> 1,
          <img className={styles.materialIcon} alt="Synthetic icon" src={require('../../../../../../grafika/Synthetic.png')} width="15" height="15"></img> 2
        </p>
    </>
  )
}
