import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const GarfieldL = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "20%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/langly.png')} alt="Garfield Langly" />
        </div>

        <h2 className={styles.header}>
          Garfield Langly
        </h2>
        <p>Garfield Langly jest kierownikiem operacyjnym pracującym nad Talosem I podczas Prey (2017).</p>

        <h2 className={styles.header}>
          Historia
        </h2>
        <h3 className={styles.smallHeader}>
          Tło
        </h3>
        <p>
          Z Langly kontaktuje się Laurel Davis, która opowiada o śmierci Andersa Kline'a. Uspokaja Davisa i każe jej pozostać na miejscu, gdy idzie szukać Sarah Elazar. Później Garfield kontaktuje się z Miyu Okabe, kiedy zauważa zwiększoną obecność ochrony. Pyta ją, czy wszystko jest w porządku w laboratoriach sprzętowych. Miyu odpowiada, że cały dzień pracowała w laboratorium i nie zwracała uwagi na to, co dzieje się na zewnątrz. Kiedy wygląda przez okno, słychać głośną eksplozję, a Miyu mówi, że właśnie stracili dwa laboratoria. Następnie kończy rozmowę.
        </p>
        <h3 className={styles.smallHeader}>
          Prey (2017)
        </h3>
        <p>
          Zanim Morgan Yu spotyka Langly'ego, jest już przemieniony w Fantoma i przemierza Centrum Urazowe.
        </p>
    </>
  )
}
