import React, { useEffect, useRef } from 'react';
import { Route, Routes } from 'react-router-dom';

import styles from './Content.module.scss'
import About from './subcomponents/about/About';
import Adversaries from './subcomponents/adversaries/Adversaries';
import Weapons from './subcomponents/weapons/Weapons';
import Tech from './subcomponents/tech/Tech'
import Locations from './subcomponents/locations/Locations';
import Characters from './subcomponents/characters/Characters';

const Content = () => {


    let content = useRef()
    const handleRotation = e => {
        let wiki = content.current
        let centerX = wiki.offsetLeft + wiki.offsetWidth/2;
        let centerY = wiki.offsetTop + wiki.offsetHeight/2;

        let posY = e.clientY - centerY;
        let posX = e.clientX - centerX;

        let rotateY = -7 * posY/(wiki.offsetHeight/2)
        let rotateX = 13 * posX/(wiki.offsetWidth/2)

        wiki.style.transform = `rotateX(${rotateY}deg) rotateY(${rotateX}deg)`;
    }

    useEffect(()=>{
        let wiki = content.current
        wiki.addEventListener('mousemove', handleRotation)
        wiki.addEventListener("mouseout", function(){
            wiki.style.transform = `rotateY(0deg) rotateX(0deg)`;
        });

        return ()=>{
            wiki.removeEventListener('mousemove', handleRotation)
            wiki.removeEventListener("mouseout", function(){
                wiki.style.transform = `rotateY(0deg) rotateX(0deg)`;
            });
        }
    }, [])

    return ( 
        <div className={styles.content} ref={content}>
                <Routes>
                    <Route path='/' element={<About/>}/>
                    <Route path='/adversaries/*' element={<Adversaries/>}/>
                    <Route path='/weapons/*' element={<Weapons/>}/>
                    <Route path='/tech/*' element={<Tech/>}/>
                    <Route path='/characters/*' element={<Characters/>}/>
                    <Route path='/locations/*' element={<Locations/>}/>
                </Routes>
        </div>
    );
}

export default Content;