import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const RileyYu = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/rileyYu.png')} alt="Riley Yu" />
        </div>
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/rileyYuModel.png')} alt="Riley Yu" />
        </div>

        <h2 className={styles.header}>
          Riley Yu
        </h2>
        <p>Riley Yu jest członkiem rodziny Yu i głównym administratorem bazy księżycowej Pyteas podczas Prey (2017) i Mooncrash.</p>

        <h2 className={styles.header}>
          Osobowość i umiejętności
        </h2>
        <p>Pod względem osobowości Riley wydaje się być bardzo surowa, zimna i biznesowa, prawie do tego stopnia, że celowo zastrasza wszystkich swoich pracowników. Witając nowoprzybyłych na Pyteaszu, spokojnie, ale bezpośrednio ostrzegała ich, że mogą zginąć w bazie, choć upierała się, że było warto. <br /> <br />

Cieszy się dużym szacunkiem i domaga się, by nazywano ją „Doktorem Yu” lub „Madame Director”.

</p>

        <h2 className={styles.header}>
          Historia
        </h2>
        <h3 className={styles.smallHeader}>
          Tło
        </h3>
        <p>Riley Yu jest kuzynką Alexa i Morgana Yu. Ukończyła Mikeal-Launders University z tytułem magistra informatyki. Pozycję administratora Pyteasza zdobyła dzięki koneksjom rodzinnym, ale była na tyle bezwzględna i kompetentna, że przyniosła wyniki, które pozwoliły jej utrzymać pozycję i była jej godna.</p>
        <h3 className={styles.smallHeader}>
          Prey: Mooncrash
        </h3>
        <p>
          W pewnym momencie swojej kadencji Riley dowiedziała się, że Claire Whitten była szpiegiem KASMA i nakazała Vijaya Bhatii zająć się nią. <br /> <br />

Na pewnym wczesnym etapie wybuchu Riley omówił z Noelem Bensonem próbne odpalenie Wież Typhon w Kraterze. Kiedy został zabity w połowie wiadomości, Riley wyruszył, aby ręcznie odpalić wieże, aby pomóc powstrzymać wybuch.
        </p>
    </>
  )
}
