import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Cyst = () => {
  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/cystConc.png')} alt="Cystoid" />
        </div>
        <h2 className={styles.header}>
          Cystoid
        </h2>
        <p>
          Cystoidy to najmniejsze tyfony, które gracz spotka w Prey, ale ich rozmiar może być mylący. Mniej więcej wielkości piłki do softballu Cystoidy działają jak ruchome „miny” dla Tyfona, szukając ruchu i gwałtownie detonując. Ta eksplozja rani gracza i emituje promieniowanie. Cystoidy często można znaleźć w środowiskach o zerowej grawitacji, takich jak GUTS i zewnątrz Talos I, gdzie mogą latać bez wysiłku. Zamiast tego w środowiskach wewnętrznych Cystoidy będą się toczyć. Czasami są przechowywane w Gniazdach Cystoidów, aby zostać uwolnione, gdy gracz się zbliża, i są produkowane przez Tkaczy jako mechanizm obronny.
        </p>
        <h2 className={styles.header}>
          Opis
        </h2>
        <p>
          Cystoidy wyglądają jak małe, czarne kulki z kolcami. Wstrząśnięte świecą na żółto, a kiedy mają wybuchnąć, powiększają się i zaczynają świecić na czerwono.
        </p>
        <h2 className={styles.header}>
          Strategia
        </h2>
        <p>
          Cystoidy zabijają się, gdy eksplodują, więc dopóki gracze są świadomi swojego otoczenia, mogą łatwo sobie z nimi poradzić, rzucając obiektem w ich kierunku. Wybuchowe kanistry są skuteczne przeciwko wielu cystoidom i gniazdom cystoidów. <br /><br />

Działo GLOO i Łowczyni Boltcaster mogą zniszczyć Cystoidy jednym strzałem. W przypadku Boltcaster, Cystoidy będą ścigać bełty i eksplodują, jeśli trafią je bezpośrednio. Jest to jednak nieco bardziej skuteczne w zerowej grawitacji niż w innych lokalizacjach.<br /><br />

Rój Cystoidów może emitować dużo promieniowania, a wystarczająco duży rój może zabić gracza. Przy włączonych opcjach przetrwania Cystoidy na zewnątrz mogą zagrozić zaopatrzeniu gracza w tlen, uszkadzając jego kombinezon, czyniąc Cystoidy znacznie bardziej niebezpiecznymi.


        </p>
    </>
  )
}
