import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const ApexLanding = () => {
  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/apex.png')} alt="Apex" />
        </div>
        <h2 className={styles.header}>
            Apex
        </h2>
        <p>
          Apex to wrogi Tyfon napotkany na Talosie I podczas Prey (2017). <br /> <br />
        </p>
    </>
  )
}
