import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Ppn8 = () => {

  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../grafika/ppn.png')} alt="PPN-8"/>
        </div>
        <h2 className={styles.header}>Ogólne</h2>
        <p className={styles.info}>Pistolet TranStar PPN-8 jest standardową bronią dla wszystkich pracowników ochrony TranStar. Mały, szybki i cichy, ten pistolet zadaje zadowalające obrażenia przy zachowaniu pewnego stopnia ukrycia. <br />
        Broń boczna do obrony osobistej TranStar PPN-8, znana również jako pistolet z tłumikiem, to broń dystansowa, którą można znaleźć na Talosie I podczas Prey (2017) oraz na Pyteas podczas Prey: Mooncrash. <br />
        Morgan znajduje broń przy zwłokach poległego ochroniarza. Z tyłu pistoletu znajduje się mały ekran, na którym wyświetlana jest ilość naboi zarówno w inwentarzu, jak i magazynku. Wydaje się, że broń jest zbudowana wokół zmodyfikowanej blokady dźwigni, ponieważ część górnej części jej górnej ramy jest wygięta pod kątem i porusza się do tyłu, aby wyrzucić wystrzelony pocisk. Podczas pustego przeładowania widać, jak Morgan ciągnie małą rączkę z boku pistoletu, aby odbezpieczyć akcję i włożyć nowy nabój.
        </p>
        <h3 className={styles.smallHeader}>
          Standardowy wariant
        </h3>
        <p>
          Na Pytheasie standardowe pistolety są skuteczniejsze niż podstawowe i można je znaleźć w różnych miejscach, podobnie jak ich plany produkcyjne. Ich kolor tła w ekwipunku jest zielony, a ich wbudowane ulepszenia są losowe. Niektóre mają dodatki do żywiołów, które modyfikują ich pociski tak, że zadają dodatkowe obrażenia, czy to elektryczne, ogniste, żrące (prawdopodobnie eteryczne) czy psychiczne.
        </p>
        <h3 className={styles.smallHeader}>
          Zaawansowany wariant
        </h3>
        <p>
          Na Pytheasie zaawansowane pistolety są skuteczniejsze niż standardowe i można je znaleźć w różnych miejscach, podobnie jak ich plany produkcyjne. Ich kolor tła w ekwipunku jest niebieski, a ich wbudowane ulepszenia są losowe. Niektóre mają elementarne przywiązania.
        </p>
        <h2 className={styles.header}>Ulepszenia</h2>
        <p>
          Na Talosie I Pistolet z tłumikiem jest „bronią bezpieczeństwa” i można go w pełni ulepszyć tylko za pomocą zestawów ulepszeń broni z neuromodami Gunsmith I i II. Na Pytheas, który nie ma zestawów ulepszeń ani planów produkcji, standardowe i zaawansowane pistolety są tworzone bezpośrednio na podstawie ich planów produkcyjnych i mają wbudowane ulepszenia.
        </p>
        <h2 className={styles.header}>Działanie</h2>
        <p className={styles.info}>
          Pistoletu najlepiej używać przeciwko słabszym wrogom lub podczas walki z wrogami, którzy potrafią latać, a tym samym mogą pozostać poza zasięgiem strzelby. Pistolet jest generalnie najlepiej używany jako broń zapasowa, ponieważ pokonanie większości wrogów zajmuje trochę czasu i dużo amunicji. Może to być przydatne do powolnego osłabiania silnego wroga atakami z ukrycia, ponieważ premia do obrażeń od ataków z ukrycia jest znaczna, a wróg nie będzie świadomy dokładnego źródła ataku. Pistolet jest również doskonałym narzędziem do zdalnego niszczenia obiektów, takich jak wybuchowe kanistry. Oczywiście Złoty Pistolet Artemis reprezentuje ogólnie lepszą broń, a dwa strzały z w pełni ulepszonej Artemidy oddają nieco mniej niż jeden strzał z niezmodyfikowanej strzelby. Ten wariant pistoletu jest użyteczną bronią nawet w bezpośredniej walce z dość twardymi wrogami, takimi jak Upiory.

Jako broń bezpieczeństwa na Talosie I, Pistolet korzysta z neuromodów Broń palna I i II.

Na Pytheasie standardowe i zaawansowane dodatki do pistoletów mogą być używane do kompensacji braku innych wyspecjalizowanych zdolności lub broni związanych z żywiołami. Na przykład, możesz zrekompensować brak paralizatora zakłócającego lub elektrycznych zdolności tyfona, takich jak wybuch elektrostatyczny, jeśli masz dodatek do żywiołu galwanicznego, ponieważ powoduje on, że pociski wystrzeliwane obok niego zadają obrażenia elektryczne, które można wykorzystać do ogłuszenia wrogów, i są szczególnie skuteczne przeciwko robotycznym wrogom, chociaż są mniej skuteczne niż specjalistyczna broń elektryczna lub moce. Jeśli nie masz psychostatycznego noża, nadajnika fal zerowych lub psychoszoku, ale masz psychiczne przywiązanie do żywiołów, nadal możesz tymczasowo anulować zdolności psychiczne Typhona, chociaż na krótszy czas niż w przypadku wspomnianych tradycyjnych metod.


        </p>
        <p>Wymagane materiały:
          <img className={styles.materialIcon} alt="Mineral icon" src={require('../../../../../../grafika/Mineral.png')} width="15" height="15"></img> 2,
          <img className={styles.materialIcon} alt="Synthetic icon" src={require('../../../../../../grafika/Synthetic.png')} width="15" height="15"></img> 1
        </p>
    </>
  )
}
