import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const ThermalPhantom = () => {
  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/thermalPhan2.png')} alt="Fantom" />
        </div>
        <div className={styles.imageBox} style={{width: "40%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/thermalPhantom.png')} alt="Fantom" />
        </div>
        <h2 className={styles.header}>
          Omówienie
        </h2>
        <p>
          Fantom termiczny to znacznie silniejsza i bardziej niebezpieczna wersja zwykłego Phantoma. Wersja termiczna może tworzyć wokół siebie supertermiczne aury ognia i słupy ognia pod swoimi celami. Mogą również ładować Mimików energią cieplną, podpalając ich przez około osiem sekund, nie będąc dla nich śmiertelnymi, i umożliwiając im zadawanie obrażeń od ognia i oparzeń u wrogów.
        </p>
        <h2 className={styles.header}>
          Design
        </h2>
        <p>
          Termiczne Upiory w większości wyglądają identycznie jak zwykłe Upiory, z wyjątkiem tego, że tlą się i są otoczone płomieniami.
        </p>
        <h2 className={styles.header}>
          Strategia
        </h2>
        <p>
          Ogólne taktyki stosowane przeciwko normalnym fantomom działają również przeciwko wersji termicznej. Fantom termiczny może spalić GLOO, pozwalając im uwolnić się od niego szybciej niż standardowe fantomy. Mogą również podpalać plamy ropy podczas walki i mogą się wypaczać jak zwykłe zjawy. Istnieje opóźnienie między rozpoczęciem ich pionowych ataków ogniowych a utworzeniem samych kolumn ognia, więc wykorzystaj ten czas, aby wydostać się z zasięgu pułapki ogniowej. Ponieważ bycie tuż obok nich, gdy ich supertermiczna aura jest aktywna, jest jak wchodzenie i przebywanie w szalejącym ogniu, trzymaj się od nich z daleka, chyba że oparzenia i uszkodzenia sprzętu nie stanowią problemu. Alternatywnie, mogą tymczasowo uczynić ich jeszcze mniej niebezpiecznymi niż ich normalni, kinetyczni kuzyni, tymczasowo wyłączając ich zdolności pirokinetyczne, co można zrobić, zdetonując nadajnik fal zerowych w ich pobliżu lub stosując przeciwko nim psychowstrząs.
        </p>
    </>
  )
}
