import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Typhon = () => {
  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/typhonChart.png')} alt="Cargo" />
        </div>
        <h2 className={styles.header}>
            Omówienie
        </h2>
        <p>
          Typhon to wrodzy kosmici napotkani na Talosie I podczas Prey (2017) i na Pyteas podczas Prey: Mooncrash. <br /> <br />
          Typhon to wrodzy kosmici, którzy byli przetrzymywani na stacji kosmicznej Talos I i bazie Pyteas Moon w celach badawczych. Celem badania było przekazanie ludziom mocy Tyfona poprzez mapowanie ich wzorców neuronowych na ludzi za pomocą neuromodów. <br /> <br />
          Tymczasem same neuromody wymagały użycia egzotycznych materiałów zebranych z narządów Tyfona, co umożliwiło modyfikację sieci neuronowych w ludzkim mózgu, nawet jeśli są używane do odciskania zdolności pochodzących od człowieka innym ludzkim odbiorcom. W tym celu w Talosie I znajdowała się również główna placówka do produkcji materiałów egzotycznych.
        </p>
        <h2 className={styles.header}>
            Charakterystyka
        </h2>
        <p>
          Wszystkie tyfony są uformowane z błyszczącej, połyskującej szaro-czarnej substancji, która wydaje się jednocześnie wykazywać cechy ciał stałych, cieczy i gazów, a tyfony mogą z łatwością zmieniać strukturę i solidność tego, aby zmienić kształt i używać ich przydatków jako broń. Ta półpłynna forma jest też najprawdopodobniej źródłem ich wysokiej trwałości. Prawie nic nie jest wyjaśnione ani ujawnione na temat ich anatomii lub składu biologicznego - w rzeczywistości ich fizjologia jest niezrozumiale obca. Ich rozmiary i kształty są bardzo zróżnicowane, od małych stworzeń podobnych do pajęczaków po duże humanoidy i unoszące się kule, ale zdecydowana większość z nich jest podatna na uraz wywołany kinetycznie, czy to przez tępą siłę, balistykę, czy doładowanie quasicząsteczkowe.<br /> <br />
          Każdy gatunek tyfona posiada różne zdolności, takie jak naśladowanie materii, telekineza, teleportacja (tj. szybkie przechodzenie w fazę lub wypaczanie do pobliskiego miejsca), wytwarzanie ognia, elektryczności lub żrącego gazu, kontrola umysłu, zdalna kontrola maszyn, atak psychiczny, a nawet tworzenie nowych gatunków z Tyfona. Pomimo złożonej struktury neuronowej brakuje im neuronów lustrzanych, co uniemożliwia im wczuwanie się w cierpienie innych istot, a nawet rozpoznanie, że są czymś więcej niż ofiarą. Z tego powodu Alex Yu odradza nazywanie ich „złymi”, ponieważ nie są celowo wrogie ani okrutne, po prostu dążą do tego, co postrzegają jako źródło pożywienia, którym według stycznia jest świadomość. Tyfony są w stanie replikować lub rozmnażać się w dziwnych procesach, o czym świadczą zdolności Mimików i Tkaczy.<br /> <br />
          Pomimo braku neuronów lustrzanych lub empatii, Typhon są w stanie wyczuć, czy otaczające je formy życia są częścią ich kolektywu, czy nie. W związku z tym nie będą atakować innych Tyfonów, które nie są kontrolowane umysłowo, ale natychmiast i bezlitośnie zaatakują każdą inną formę czującego, organicznego życia, zwłaszcza ludzi. Jedynymi wyjątkami są gniazda macek i cystoidy, których reakcja na otoczenie opiera się wyłącznie na wykrywaniu ruchu.
        </p>
        <h2 className={styles.header}>
          Historia
        </h2>
        <h3 className={styles.smallHeader}>
          Wczesny etap
        </h3>
        <p>
          Tyfony zostały odkryte na satelicie Vorona-1, który został wystrzelony przez Sowietów w 1958 roku. Satelita krążył wokół Księżyca, dopóki Sowieci nie stracili z nim łączności. Następnie wysłali załogową misję w celu zbadania sprawy. Zespół naprawczy nie znalazł żadnych problemów zewnętrznych, więc radzieckie kierownictwo nakazało kosmonautom włamać się do satelity. W następnych chwilach kosmonauci napotkali pozaziemską formę życia, która zabiła wszystkich członków misji. <br /><br />
          Po dwóch latach milczenia Sowieci skontaktowali się ze Stanami Zjednoczonymi i błagali o pomoc w powstrzymaniu zagrożenia. Prezydent John F. Kennedy zgodził się iw 1963 roku współpraca amerykańsko-sowiecka zaowocowała programem Kletka. Zbudowano stałą instalację, aby pomieścić istoty pozaziemskie. Po nieudanej próbie zamachu na Kennedy'ego, Stany Zjednoczone przejęły kontrolę nad programem i zaczęły badać istoty pozaziemskie, ale nie były w stanie wykorzystać ich enigmatycznych zdolności do zastosowań wojskowych lub komercyjnych. Po tym, jak awaria zabezpieczenia doprowadziła do śmierci całego zespołu badawczego, Stany Zjednoczone opuściły stację. Ostatecznie korporacja TranStar zabezpieczyła opuszczoną stację i przekształciła ją w Talos I, najnowocześniejsze centrum innowacji. W ciągu zaledwie pięciu krótkich lat zespół na pokładzie Talosa I osiągnął doskonałe zrozumienie organizmów tyfonów i zaczął stosować swoje badania na ludziach (w postaci neuromodów).
        </p>
        <h3 className={styles.smallHeader}>
          Współczesność
        </h3>
        <p>
          Od 2035 roku Typhonom udało się przełamać zabezpieczenia i opanować stację, szybko zabijając większość załogi. Niektórym szczęśliwcom udało się uciec lub przetrzymać w kryjówkach na stacji. Zanieczyszczenia tyfonem rozprzestrzeniły się również na pobliską bazę księżycową Pyteas, również posiadaną i obsługiwaną przez Transtar.<br /><br />

Pod koniec gry „Morgan” okazuje się, że Tyfon dotarł na Ziemię i opanował planetę. Obecny stan populacji ludzkiej nie jest znany. Cokolwiek zrobił prawdziwy Morgan, nie udało mu się powstrzymać skażenia Tyfonem na pokładzie Talosa I.


        </p>
    </>
  )
}
