import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const JorgenThorstein = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "20%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/thorn.png')} alt="Jorgen Thornstein" />
        </div>
        <div className={styles.imageBox} style={{width: "40%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/thorn2.png')} alt="Jorgen Thornstein" />
        </div>

        <h2 className={styles.header}>
          Jorgen Thornstein
        </h2>
        <p>
          Jorgen Thorstein był dyrektorem laboratoriów sprzętowych na Talosie I podczas Prey (2017).
        </p>

        <h2 className={styles.header}>
          Historia
        </h2>
        <h3 className={styles.smallHeader}>
          Tło
        </h3>
        <p>
          Kiedy Lorenzo Calvino dzwoni z powodu choroby, Thorstein przedstawia prezentację Lustro, którą miał wykonać Calvino. Pod jego nieobecność zostawia Thaddeusowi Yorkowi kod do drzwi do biura. Thorstein wysyła również e-maile do Thaddeusa, aby miał oko na Franklina Goode'a, który spędzał dużo czasu na osobistych projektach i zajęciach rekreacyjnych, podejrzewając, że użyje systemu napędowego Artax, aby uzyskać dostęp do nieautoryzowanych obszarów. Thorstein oferuje Lily Morris przekazanie nowych planów produkcji Recycler Charge i nakazuje Franklinowi przeniesienie psychotronicznych elementów tranzystorowych ze starego satelity do nowego, ale żadne z nich nie zostaje wykonane z powodu przerwania zabezpieczenia Typhona. <br /> <br />

Kiedy następuje atak Tyfona, Thorstein wzywa Thaddeusa, aby spotkał się z nim w jego biurze, aby mogli się ewakuować i mówi Thorsteinowi, aby przyniósł Gloo Gun i wszystko, co uda mu się znaleźć, takie jak egzotyczne materiały i neuromody. Thaddeus nie dociera do biura Thorsteina, a dr Thorstein prawdopodobnie również zostaje zabity przez Tyfona.
        </p>
        <h3 className={styles.smallHeader}>
          Prey (2017)
        </h3>
        <p>
          Jorgena Thorsteina można spotkać jako fantoma w jego biurze.
        </p>
    </>
  )
}
