import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Turret = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/turret.png')} alt="Wieżyczka" />
        </div>
        <h2 className={styles.header}>
          Omówienie
        </h2>
        <p>
          Wieżyczki to przenośne autonomiczne karabiny maszynowe, które są używane na Talosie I podczas Prey (2017). <br /> <br />
          Wieżyczki to przenośne, autonomiczne systemy karabinów maszynowych, które atakują wrogów ustawionym łukiem do przodu. Używają wbudowanej technologii Psychoscope, która pozwala im odróżnić ludzi od organizmów Typhon. Ich domyślne IFF identyfikują istoty, które mają powyżej określonej ilości materiału Typhon (neurologicznie aktywny egzotyczny) jako wrogów, a te poniżej progu jako przyjaciół. Wydaje się, że ich psychoskopy nie mają zainstalowanych żadnych konkretnych chipsetów.<br /> <br />

Wieżyczki mogą być przyjazne lub wrogie graczowi. Jeśli są przyjazne, można je naprawić (wymaga naprawy neuromodu II i 3 części zamiennych) i wzmocnić (wymaga naprawy III i 3 części zamiennych), co daje wieżyczce większą redukcję obrażeń. Jeśli są wrogie, wieżyczki można zniszczyć, ogłuszyć działem GLOO, ładunkami EMP lub wybuchem elektrostatycznym, zhakować (wymaga Hacking II) lub zdominować telepatią (wymagany Machine Mind). Ogłuszone lub zniszczone wieżyczki nadal można zhakować.<br /> <br />

Wieżyczki można wytwarzać od Fabrykatorów po zdobyciu planu produkcji w Sejfie biura ochrony w ładowni. Kod do sejfu jest przekazywany przez Sarah Elazar, gdy gracz spotka się z nią w Cargo Bay B. <br /> <br />
Wieżyczki są również obecne na Pyteas w Prey: Mooncrash i są ponownie kolorowane i określane jako „wieżyczki balistyczne”. Inne warianty wieżyczek Pyteasa to wieżyczki GLOO i wieżyczki laserowe. Joan Winslow posiada zdolność, która pozwoli jej przywołać przyjazną wieżyczkę balistyczną, którą może posiadać Technopata.

        </p>
        <h2 className={styles.header}>
          Użycie
        </h2>
        <p>
          Wieżyczki mogą być używane jako alternatywny sposób radzenia sobie z wrogami. Zapewnią również ochronę ocalałym, których gracz właśnie uratował, aby uniknąć ataku Tyfonów. Warto zaznaczyć, że gracz nadal może zostać trafiony ogniem z przyjaznej wieżyczki, więc nie warto stawać między nią a wrogiem.<br /> <br />

Wieżyczki są bardzo potężne przeciwko tyfonom niskiego poziomu, ale zwykle potrzeba kilku, aby poradzić sobie z czymś większym niż Mimic, ponieważ pojedyncze bezpośrednie trafienie Upiora może przewrócić wieżyczkę i spowodować, że przestanie działać. W rezultacie ogólnie dobrym pomysłem jest rozstawienie wieżyczek w taki sposób, aby utworzyły obwód, zamiast skupiać je razem, gdzie ataki obszarowe wroga mogą znokautować kilka w tym samym czasie.<br /> <br />

Wieżyczki można podnosić i przenosić zgodnie z życzeniem gracza, ale później trzeba je ręcznie rozstawić. Tekst w grze i mowa z samych wieżyczek sugeruje, że jest to spowodowane błędem w kalibracji ich systemów wykrywania przechyłu.<br /> <br />

Zhakowane wieżyczki nie są wrogie wobec niczego, co jest sojusznikiem Morgana, w tym przyjaznych fantomów i zhakowanych skorumpowanych operatorów.


        </p>
    </>
  )
}
