import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const December = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "40%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/december.png')} alt="December" />
        </div>

        <h2 className={styles.header}>
          December
        </h2>
        <p>
          December to przeprogramowany Operator, który prowadzi Morgan Yu przez Talos I i pojawia się w Prey (2017).
        </p>

        <h2 className={styles.header}>
          Historia
        </h2>
        <h3 className={styles.smallHeader}>
          Tło
        </h3>
        <p>
          Po tym, jak udział Morgana w eksperymentach z neuromodami opartymi na Typhonie spowodował zmianę ich osobowości, opracowali plan powrotu na Ziemię i ostrzeżenia ludzkości przed Tyfonem, a także niebezpiecznymi badaniami Transtara. Morgan, wiedząc, że wkrótce ich pamięć zostanie zresetowana, stworzył grudzień, aby poprowadzić ich przyszłe ja w kradzieży prywatnej kapsuły ratunkowej Alexa Yu. <br /> <br />

Morgan wrócił do laboratorium symulacyjnego, ale ciągły dryf osobowości, a także przedłużanie cyklu eksperymentu w nieskończoność bez ich zgody doprowadziły ich do psychotycznego załamania. Morganowi udało się na krótką ucieczkę, podczas której postanowili zniszczyć Talosa I, używając funkcji samozniszczenia stacji. Przechowali szczegóły tego planu na nowym przeprogramowanym Operatorze, styczniu. Nie wiadomo, czy grudzień nie skontaktował się z tym wcieleniem Morgana, czy też tak się stało, a Morgan po prostu odrzucił ich poprzedni pomysł ucieczki. W obu przypadkach Morgan został ostatecznie schwytany i zmuszony do powrotu do symulacji.


        </p>
        <h3 className={styles.smallHeader}>
          Prey (2017)
        </h3>
        <p>
          Grudzień po raz pierwszy nawiązuje kontakt, gdy Morgan wchodzi do Psychotronics i próbuje przekonać ich do opuszczenia Talosa I za pośrednictwem kapsuły ratunkowej Alexa. Wyjaśnia, że Morgan wcześniej zdobył kartę dostępu i ukrył ją w Oddziale Neuromodów, i zachęca ich do jej odzyskania.<br /> <br />

Na szafce Morgan znajduje torbę na owady, którą zostawili dla siebie, ale Alex już odkrył plan ich rodzeństwa i wyjął kartę dostępu. December prosi Morgana o spotkanie w holu Neuromodów, stwierdzając, że biuro „nie jest bezpieczne”.<br /> <br />

Jeśli styczeń nadal będzie działał w tym momencie, pokona tam Morgana i zniszczy grudzień, nazywając go wadliwą pozostałością wcześniejszych planów Morgana. W przeciwnym razie, December będzie współpracować z Morganem, aby uzyskać dostęp do kapsuły ratunkowej w Arboretum. W przeciwieństwie do stycznia, grudzień dokonuje samozniszczenia, gdy Morgan dociera do kapsuły ratunkowej. Nawet jeśli grudzień zostanie zniszczony, cel odblokowania kapsuły ratunkowej Alexa pozostaje dostępny dla Morgana. Po otwarciu można go użyć do opuszczenia Talosa I w dowolnym momencie.
        </p>
    </>
  )
}
