import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Recycler = () => {
  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../grafika/fabricator.png')} alt="Recycler" />
        </div>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../grafika/rePoster.png')} alt="Recycler" />
        </div>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../grafika/materials.png')} alt="Materiały" />
        </div>
        <h2 className={styles.header}>
            Omówienie
        </h2>
        <p>
          Recykler materiałów to urządzenie używane w Talosie I w Prey (2017) i Pyteas w Prey: Mooncrash, które pozwala użytkownikom przetwarzać dowolne przedmioty, które mogą przechowywać w swoich ekwipunkach, na surowce. Materiały te można następnie wykorzystać do wytwarzania przedmiotów w Fabrykatorze.  <br /> <br />
          Recyklery to duże maszyny rozsiane po całym Talosie I. Recykler składa się z pojemnika na materiały do ​​recyklingu po lewej stronie, centralnego ekranu z przyciskiem uruchamiającym maszynę i wyświetlaczem tego, jakie będą produkty, oraz pojemnika na prawa strona, gdzie produkty surowcowe są zrzucane po zakończeniu pracy maszyny. Po lewej stronie maszyny znajduje się kanał, który może wymagać naprawy przed użyciem.
        </p>
        <h2 className={styles.header}>
            Działanie
        </h2>
        <p>
          Śmieci są używane wyłącznie do recyklingu i nie mają innego celu i można je szybko dodać do Recyklera, przytrzymując przycisk. Prawie każdy inny element ekwipunku można poddać recyklingowi, chociaż niektóre przedmioty z misji zostaną zablokowane, a w menu recyklera pojawi się czerwony X wskazujący, że nie można ich w nim umieścić. Recykler jest zatem dobrym sposobem na pozbycie się duplikatów broni lub nadmiarowych przedmiotów i czerpanie z tego korzyści. <br /> <br />

Recykling tak często, jak to możliwe, to dobry sposób na konsolidację niepotrzebnych łupów i zwolnienie miejsca w ekwipunku, ponieważ wszystkie surowce gromadzą się w zaledwie 4 miejscach w ekwipunku (po jednym dla każdego rodzaju materiału: organiczny, mineralny, syntetyczny i egzotyczny).
        </p>
    </>
  )
}
