import React, { useState } from 'react'
import { NavLink } from 'react-router-dom'
import interfaceAudio from '../../../../../../sounds/interface.wav' 
import styles from './SubMenu.module.scss'
import DropdownSubmenu from './subcomponents/DropdownSubmenu'
const audio = new Audio(interfaceAudio)

const SubMenu = ({item}) => {
    const [dropdownNav, setDropdownNav] = useState(false)
    const handleAudio = () => {
        item.subNav && setDropdownNav(!dropdownNav)
        audio.play()
    }
  return (
    <>
        <NavLink className={styles.mainNavElem} to={item.path} onClick={handleAudio}>
          {item.title}
        </NavLink>
        {dropdownNav && item.subNav.map((item, index) => {
                return (
                    <DropdownSubmenu item={item} key={index}></DropdownSubmenu>
                )
        })}
    </>
  )
}

export default SubMenu