import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const LivingQuar = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/livinQuar.gif')} alt="Crew quarters" />
        </div>
        <h2 className={styles.header}>
            Omówienie
        </h2>
        <p>
          Kwatery załogi to miejsce na pokładzie Talosa I w grze Prey (2017).

 <br /> <br />
          Kwatery załogi to główne pomieszczenia mieszkalne dla całego personelu na pokładzie Talosa I. W zależności od ich znaczenia na stacji, personelowi przydzielono luksusowy apartament, kabinę załogi lub kapsułę mieszkalną. <br /><br />

Kwatery załogi były również miejscem relaksu pracowników, w którym znajdowało się kino, specjalistyczne mieszane centrum rekreacyjne, bar, a nawet basen. Pracownicy i goście cieszyli się kuchnią szefa kuchni Williama Mitchella w stołówce, a także osobistym treningiem z Emmą Beatty w centrum fitness.<br /><br />

Kwatery załogi są wyposażone w mnóstwo luksusowych materiałów, takich jak drewno i skóra – rzeczy, których nie można się spodziewać na pokładzie stacji kosmicznej. To kolejna oznaka bogactwa TranStaru i kolejny środek przeciwdziałania długotrwałemu stresowi wśród personelu, jaki spowodował ich długotrwały pobyt w kosmosie.<br /><br />

Wielu członków załogi stacji zostało tu uwięzionych podczas wybuchu epidemii, ponieważ można tu znaleźć 19 ocalałych ludzi. Jednak wszystkie oprócz 1 z nich są pod kontrolą umysłu Telepaty. Kontrolowani umysłowo ocaleni są podzieleni na dwie grupy, w stołówce i centrum fitness, przy czym każda grupa ma tam stacjonującego telepatę.


        </p>
        <div className={styles.imageBox} >
            <img className={styles.image} src={require('../../../../../../prey-graphics/crewQuar1.png')} alt="Crew quarters" />
        </div>
    </>
  )
}
