import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const VoltaicMimic = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/voltMimic.png')} alt="Mimik" />
        </div>
        <h2 className={styles.header}>
          Omówienie
        </h2>
        <p>
          To małe tyfony, które potrafią kamuflować się, by wyglądać jak nieożywione obiekty w swoim otoczeniu, takie jak krzesła biurowe lub kosze na śmieci. Dodatkowo pasywnie emitują pola elektromagnetyczne, które tymczasowo wyłączają Typhon Gates i inną elektronikę, a także łuki elektryczne, które również wyrządzają szkody istotom organicznym w ich pobliżu. 
        </p>
        <h2 className={styles.header}>
          Design
        </h2>
        <p>
          Podobnie jak normalny mimik, wersja woltaiczna wygląda jak czworonożne stworzenie przypominające pająka, wykonane z czarnej, półparującej substancji. Wersja woltaiczna ma również wiele kryształowych narośli wystających z jej ciała, które podobnie jak fantomy woltaiczne stale emitują stałe łuki elektryczne.
        </p>
        <h2 className={styles.header}>
          Strategia
        </h2>
        <p>
          Te same strategie, które pasują do normalnego Mimika, działają również w wersji woltaicznej. Chociaż gracze powinni być znacznie bardziej ostrożni w przypadku wersji voltaic ze względu na ich zdolności elektryczne. Elektronika, taka jak pistolet GLOO i Q-Beam, nie działa w ich pobliżu, a wszelkie organizmy i maszyny w pobliżu są uszkodzone, więc staraj się trzymać jak najdalej od nich, chyba że nie jest to dla ciebie problemem. Ponieważ aura woltaicznego mimika jest generowana pasywnie, fala zerowa lub psychoszok nie wyłączą jej. Nullwave lub psychoshock zakłócą jednak zdolność mimikry woltaicznej.
        </p>
    </>
  )
}
