import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Arboretum = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "35%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/arboretum.gif')} alt="Arboretum" />
        </div>
        <h2 className={styles.header}>
            Omówienie
        </h2>
        <p>
          Arboretum to sztuczny park na pokładzie Talosa I w Prey (2017). <br /> <br />
          Arboretum to miejsce, w którym rośnie większość żywności stacji. Naukowcy pracują również nad różnymi przełomami agronomicznymi, takimi jak superowoce. Służy również jako miejsce wypoczynku dla załogi. Biuro Alexa Yu znajduje się pośrodku Arboretum, a na jego piętrze znajduje się osobista kapsuła ratunkowa. To tam Morgan i Alex będą świadkami przybycia Apex.
          <br /> <br />
Arboretum jest najlepszym miejscem do rozprawienia się z Koszmarami ze względu na wiele miejsc niedostępnych dla Koszmaru, a także będzie pierwszym miejscem, do którego przybędą Operatorzy Wojskowi.
        </p>
        <div className={styles.imageBox} >
            <img className={styles.image} src={require('../../../../../../prey-graphics/arboretumConc.png')} alt="Arboretum" />
        </div>
        <div className={styles.imageBox} >
            <img className={styles.image} src={require('../../../../../../prey-graphics/84 - BqOr3gj.png')} alt="Arboretum" />
        </div>
    </>
  )
}
