import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const MathiasKohl = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "20%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/kohl.png')} alt="Mathias Kohl" />
        </div>
        <div className={styles.imageBox} style={{width: "40%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/kohl2.png')} alt="Mathias Kohl" />
        </div>

        <h2 className={styles.header}>
          Mathias Kohl
        </h2>
        <p>
          Mathias Kohl jest doradcą pracującym nad Talosem I podczas Prey.
        </p>

        <h2 className={styles.header}>
          Historia
        </h2>
        <h3 className={styles.smallHeader}>
          Tło
        </h3>
        <p>
          Mathias Kohl pracował jako doradca na stacji. Wśród jego pacjentów byli tacy ludzie jak Lorenzo Calvino, Sarah Elazar i Danielle Sho. Próbował także pomóc Joshowi Daltonowi w zarządzaniu jego konfliktem z Lane Carpenterem, ale bezskutecznie. W jednym ze swoich e-maili Kohl wyraża zaniepokojenie niestabilnością psychiczną Calvino i wzburzonym zachowaniem podczas ich sesji i postanawia zgłosić to Alexowi Yu. Kohl zauważa, że ​​coraz większa liczba pracowników Działu Psychotroniki i Neuromodów cierpiała na problemy lękowe i planowała powiadomić o tym Alexa. Był również odpowiedzialny za stworzenie testu zachowania. Wygląda na to, że był torturowany przez głównego chirurga Hendrika DeVriesa na rozkaz Alexa w sprawie miejsca pobytu nagranych sesji Morgana.
        </p>
        <h3 className={styles.smallHeader}>
          Prey (2017)
        </h3>
        <p>
          Morgan Yu znajduje zwłoki Kohla w apartamencie DeVriesa w kwaterach załogi. Jego oszpecona twarz sugeruje, że Mimic zabił go, gdy był jeszcze przesłuchiwany, chociaż DeVries uciekł. Kohl niesie 4 apteczki.
        </p>
    </>
  )
}
