import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const ControlledHuman = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/control.png')} alt="Opętany" />
        </div>
        <h2 className={styles.header}>
          Omówienie
        </h2>
        <p>
          Kontrolowani umysłowo ludzie to ludzie, których umysły zostały całkowicie przejęte przez telepatów (których zwykle można zobaczyć w ich pobliżu). Wędrują po Talosie I, rozmawiając ze sobą, próbując wyrzucić Telepatę z głowy, ale bezskutecznie. Jeśli wykryją Morgana Yu lub Petera, rzucą się na nich i uwolnią silny Podmuch Kinetyczny, niszcząc umiejętności PSI gracza, zadając graczowi średnie lub ogromne obrażenia i zabijając się w tym procesie. <br /> <br />

Można ich jednak uwolnić, ogłuszając ich pistoletem ogłuszającym, używając Mindjacka lub zabijając Telepatę, który ich kontroluje.<br /> <br />

W Prey ludzi kontrolowanych przez umysły można śledzić jako zwykłych pracowników stacji bezpieczeństwa w całym Talosie I. W Mooncrash pojawiają się z etykietą „[Brak identyfikatora Transtar]” (ze względu na naturę symulacji bazy księżycowej) i mogą tylko być śledzone za pomocą czerwonych znaczników wroga.


        </p>
        <h2 className={styles.header}>
          Strategie
        </h2>
        <ul className={styles.wikiPageList}>
          <li>
Działo GLOO jest niezwykle skuteczne w przygważdżaniu ich i pozwala skanować, zabijać lub ogłuszać, bez martwienia się, że szarżują na ciebie i zabijają.
          </li>
          <li>

    Wystarczy jeden strzał z paralizatora Disruptor, aby natychmiast ich powalić.
          </li>
          <li>
    Walka wręcz nie jest najlepszym pomysłem ze względu na ich wybuch kinetyczny, chyba że użyłeś na nich działa GLOO.
          </li>
          <li>
  Mindjack również ich znokautuje. Możesz też zabić Telepatę, ale zrób to z bezpiecznej odległości iw miejscu, w którym nie mogą go dosięgnąć, by nie mogli cię zaatakować. Wiadomo, że jest to skuteczna strategia, ponieważ ludzie z kontrolowanymi umysłami często pojawiają się w obszarach, w których można znaleźć Telepatów. Jedynym wyjątkiem jest Centrum Urazowe w Lobby Talosa I.


          </li>
        </ul>
    </>
  )
}
