import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const DayoIgwe = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "40%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/dayo.png')} alt="Dayo Igwe" />
        </div>
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/dayo2.png')} alt="Dayo Igwe" />
        </div>

        <h2 className={styles.header}>
          Dayo Igwe
        </h2>
        <p>
          Doktor Dayo Igwe jest neurobiologiem pracującym na pokładzie Talosa I i pojawia się w Prey.
        </p>

        <h2 className={styles.header}>
          Historia
        </h2>
        <h3 className={styles.smallHeader}>
          Tło
        </h3>
        <p>
          Przed przybyciem na Talos I miał młodą żonę, która zmarła na tętniaka mózgu. Jego teorie dotyczące neuronauki były niepopularne, więc rekrutacja była tania.
        </p>
        <h3 className={styles.smallHeader}>
          Prey (2017)
        </h3>
        <p>
          Pojawia się uwięziony w kontenerze w kosmosie i musi zostać uratowany. Jeśli gracz go uratował, prosi gracza o odzyskanie konektomów Leitnera z jego pokoju (ukrytego w sejfie za plakatem, który otwiera nagranie gry na fortepianie Leitnera) i przekazanie mu ich, ponieważ są dla niego pamiątką. Kiedy Dahl przybywa, Igwe okazał się jednym z głównych asystentów gracza, w którym sugeruje graczowi, aby nie zabijał Dahla, ale pozbawił go przytomności paralizatorem. Następnie Igwe sprowadza Dahla z powrotem, aby wykonał operację neurotomii, aby usunąć rozkaz wykonania laski Talosa I za pomocą Neuromodów, ale zachował swoje zdolności pilota. Odbywa się to dzięki utracie wspomnień po usunięciu Neuromoda, co powoduje, że zapomina o rozkazach Williama Yu, a zamiast tego słucha prośby Igwe i Morgana o pilotowanie wahadłowca na Ziemię (zachowując mniej niż przyjazne nastawienie).<br /> <br />

Jeśli Igwe przeżyje do końca, wraz z ocalałymi, których gracz uratował, pojawi się w Dahl's Shuttle.<br /> <br />

Jeśli graczowi nie uda się uratować Igwe lub umrze przed przybyciem Dahla, jedyną możliwością ucieczki jest skorzystanie z kapsuły ratunkowej Alexa Yu.<br /> <br />

W pewnym momencie po incydencie w Talosie I jego głos został osadzony w operatorze naukowym i sugeruje się, że nie żyje.<br /> <br />

W scenie po napisach dostępne są dwie opcje, które prowadzą gracza do pomyślnego lub nieudanego ukończenia gry. Stwierdził, że jeśli jego iloraz empatii jest w średnim zakresie/wyjątkowo wysoki, to gracz właśnie pomyślnie ukończył grę. W przeciwnym razie, jeśli powie, że nie ma od niego ilorazu empatii, gracz oficjalnie nie ukończy gry, ponieważ Alex powie, że to porażka, a Michaiła mówi Alexowi, aby zaczął grę od nowa.


        </p>
        <h2 className={styles.header}>
          Osobowość i umiejętności
        </h2>
        <p>
          Mówi się, że Dayo Igwe jest introwertykiem w nagraniach Morgana znalezionych w jego pokoju. Podąża za tą cechą, będąc generalnie cichym. Jest niezwykle lojalny wobec Alexa Yu i został sprowadzony przez Alexa do pracy nad Talosem I, najwyraźniej ze względu na to, że jest tanim pracownikiem. Wynika to z jego wiary w zjawiska paranormalne. Wie o testach Morgana i losie ojca Michajły i sugeruje, aby Morgan nie odsyłał jej akt z powrotem, aby mogła je usłyszeć. <br /> <br />

Jest bardzo łagodną postacią, wydaje się, że nie chce denerwować ludzi i zachowuje dobre maniery w każdej sytuacji, w tym jeśli gracz zdecyduje się zabić Dahla po neurotomii. Dayo krzyczy zaalarmowany i zwraca uwagę, że Dahl był ich jedynym wyjściem, ale milczy i odmawia dalszych komentarzy. Poprosił również i udało mu się spotkać Gustava Leitnera, pianistę, którego Dayo bardzo podziwiał, choć był uprzejmy i nie chciał być nachalny, gdy powiedziano mu, że szanse na spotkanie z nim są niewielkie.<br /> <br />

W przeciwieństwie do większości ludzkich NPC, Igwe nie będzie wrogo nastawiony, jeśli zobaczy, jak używasz mocy tyfona. Zamiast tego po prostu skomentuje to.


        </p>
    </>
  )
}
