import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const EthericPhantom = () => {
  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/ethericPhan.png')} alt="Fantom" />
        </div>
        <h2 className={styles.header}>
          Omówienie
        </h2>
        <p>
          Fantom eteryczny to znacznie silniejsza i bardziej niebezpieczna wersja zwykłego Phantoma. Etheric Phantoms preferują walkę w zwarciu, ale są w stanie używać Kinetic Blasts podobnie jak zwykłe Phantomy. Mogą również rozdzielić się na dwie części podczas walki i po śmierci pozostawić toksyczną chmurę eteru.
        </p>
        <h2 className={styles.header}>
          Design
        </h2>
        <p>
          Wygląda identycznie jak normalny Phantom, z wyjątkiem tego, że nieustannie emituje wokół siebie chmury purpurowego, trującego gazu.
        </p>
        <h2 className={styles.header}>
          Strategia
        </h2>
        <p>
          W odróżnieniu od innych wersji fantoma, wersja eteryczna lubi szybko doskakiwać do gracza. Z tego powodu należy ich spotkać ze strzelbą. Ale bądź ostrożny, ponieważ eteryczne upiory po uszkodzeniu nadal mogą krwawić toksycznymi chmurami. Co więcej, w połowie bitwy Etheric Phantom może podzielić się na dwa Phantomy, co znacznie utrudnia walkę. Kopia jest znacznie słabsza od oryginału pod względem zdrowia i może przetrwać tylko kilka sekund.<br /><br />

Ich charakter walki wręcz bardzo zachęca ich do zbliżania się do gracza z wykluczeniem innych taktyk. Pamiętaj, że użyją swojej zdolności wybuchu kinetycznego, jeśli nie będą w stanie dosięgnąć gracza, ale rzadko będą jej używać, jeśli będą mogli znaleźć ścieżkę do gracza.<br /><br />

Q-Beam jest szczególnie skuteczny przeciwko tym wariantom Phantomów, być może ze względu na ich eteryczną naturę. Jeśli unieruchomienie ich jest trudne, można wcześniej użyć paralizatora, aby utrzymać je nieruchomo na tyle długo, aby zostały odparowane przez wiązkę Q.
        </p>
    </>
  )
}
