import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const CystNest = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/cystNest.png')} alt="Gniazdo" />
        </div>
        <h2 className={styles.header}>
          Omówienie
        </h2>
        <p>
          Gniazdo cystoidów to tyfon napotkany na Talosie I i wokół niego w Prey (2017) oraz w Pyteas w Prey: Mooncrash. <br /><br />
          Gniazda Cystoidów to duże, stacjonarne Tyfony, które służą przede wszystkim do przechowywania grup Cystoidów Tyfonów. Zwykle pojawiają się w obszarach o zerowej grawitacji lub w ich pobliżu, ale można je zobaczyć na całym Talosie I i Pyteasie. Gniazda Cystoidów emitują stałe i śmiercionośne promieniowanie i wybuchają, gdy zbliżają się do nich ludzie, uwalniając swój ładunek Cystoidów.
        </p>
        <h2 className={styles.header}>
          Design
        </h2>
        <p>
          Gniazda Cystoidów przypominają duże worki lub krosty czarnej substancji, z trzema mackami zwisającymi lub rozciągającymi się od jego środkowej masy. Świecą słabo na żółto, prawdopodobnie z powodu rojących się w środku Cystoidów.
        </p>
        <h2 className={styles.header}>
          Strategia
        </h2>
        <p>
          Z gniazdami najlepiej rozprawić się z daleka, używając broni dystansowej. Tanią i wydajną opcją jest Łowczyni Boltcaster, której bełty zwabią Cystoidy i można je później odzyskać.
<br /><br />
Gniazda Cystoidów można wykryć dzięki emitowanemu przez nie stałemu promieniowaniu, które uruchamia licznik Geigera munduru TranStar.
<br /><br />
Użycie wystarczająco silnej mocy, takiej jak Podmuch Kinetyczny, może zniszczyć gniazdo bez uwalniania znajdujących się w nim cystoidów.


        </p>
    </>
  )
}
