import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const GUTS = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/guts.gif')} alt="Cargo" />
        </div>
        <h2 className={styles.header}>
            Omówienie
        </h2>
        <p>
          G.U.T.S. (Gravity Utility Tunnel System) to tunel serwisowy i transportowy o zerowej grawitacji na pokładzie Talosa I w Prey (2017). <br /> <br />
          WNĘTRZNOŚCI. to system tuneli użytkowych o zerowej grawitacji, używany do transportu i różnych usług na pokładzie stacji. Tunel łączy się z czterema głównymi lokalizacjami; Psychotronics, Shuttle Bay, Arboretum i Cargo Bay, dzięki czemu załoga może dotrzeć do tych miejsc bez konieczności wychodzenia przez zewnętrzne stacje. <br /> <br />

Widma, Koszmary i Mimiki nigdy się tu nie pojawią z powodu Zero-G.


        </p>
        <div className={styles.imageBox} >
            <img className={styles.image} src={require('../../../../../../prey-graphics/guts1.png')} alt="Cargo" />
        </div>
    </>
  )
}
