import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const AdvLanding = () => {
  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../grafika/danger.png')} alt="Cargo" />
        </div>
        <h2 className={styles.header}>
            Przeciwnicy
        </h2>
        <p>
          W grze "Prey" z 2017 roku napotykasz różnorodne przeciwności. Są to obce stworzenia o różnych formach i umiejętnościach, które atakują bohatera. Dodatkowo, spotykasz także sztuczną inteligencję, która może być zainfekowana i stanowić zagrożenie. Na stacji kosmicznej można także natknąć się na ocalałych członków załogi, zarówno wrogich, jak i przyjaznych. W grze występują również mechaniczne pułapki i roboty, które utrudniają twoją podróż.
        </p>
    </>
  )
}
