import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const MorganYu = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "48%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/morganBanner.png')} alt="Morgan Yu" />
        </div>
        <div className={styles.imageBox} style={{width: "48%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/morganBanner3.png')} alt="Morgan Yu" />
        </div>
        <h2 className={styles.header}>
          Morgan Yu
        </h2>
        <p>
          Doktor Morgan Yu jest wiceprezesem i dyrektorem ds. badań w TranStar Industries oraz postacią gracza w grach Prey i Prey: TranStar VR z 2017 roku. Gracz może wybrać płeć Morgana na początku Prey.
        </p>
        <h2 className={styles.header}>
          Historia
        </h2>
        <h3 className={styles.smallHeader}>
          Tło
        </h3>
        <p>
          Morgan Yu urodziła się 5 maja 2005 roku jako drugie dziecko Catherine Yu, niemieckiej bizneswoman i Williama Yu, znakomitego chińskiego neurobiologa. Ich starszy brat to Alex Yu. W 2027 roku, po ukończeniu Periot College School of Science z tytułem doktora nauk ścisłych w dziedzinie neuronauki, Morgan został zatrudniony przez TranStar Industries, firmę ich rodziców, do testowania eksperymentalnego urządzenia znanego jako Neuromod. W 2032 roku Morgan został przeniesiony na orbitalną stację kosmiczną TranStar, gdzie w ciągu następnych trzech lat odegrali kluczową rolę w wynalezieniu Psychoskopu. <br /> <br />

Morgan zgłosił się na ochotnika jako główny obiekt testowy w eksperymentach mających na celu nadanie ludziom zdolności Tyfona, w pełni świadomy, że w ramach prób ich wspomnienia będą wielokrotnie usuwane, gdy neuromody będą instalowane, a następnie usuwane. W tym celu Morgan został umieszczony w symulacji ich mieszkania, gdy byli jeszcze na Ziemi, i zmuszany do przeżywania tego samego dnia w kółko. <br /> <br />

Późniejsze usuwanie i dodawanie różnych neuromodów wykazało nieznany wcześniej efekt uboczny dryfu osobowości, a także powolny wpływ na różne części ich umysłu w różnych kierunkach. To spowodowało, że osobowość Morgana całkowicie się zmieniła w taki sposób, że ich poprzednie opinie zostały wymazane i pozostał niestabilny psychicznie, co skutkowało gwałtownymi wybuchami. <br /> <br />

W pewnym momencie Morgan zorientował się, co się dzieje i uciekł, ale przewidując, że zostaną ponownie schwytani, przeprogramowali Operatora o imieniu January, aby pomógł przyszłemu ja cierpiącemu na amnezję, podkładając Neuromody, które byłyby w stanie pomóc Morganowi w walce. stacji i powiedzieć im, o czym zapomnieli w związku z usunięciem Neuromodów. Jednak Morgan został ostatecznie schwytany i ponownie wymazano mu pamięć. Głównym celem Morgana w tym momencie byłoby zniszczenie Talosa I, a wraz z nim Tyfona i zagrożenia, jakie stanowili dla rasy ludzkiej.


        </p>
        <h3 className={styles.smallHeader}>
          Prey (2017)
        </h3>
        <p>
          Morgan budzi się w czymś, co wydaje się być mieszkaniem na Ziemi w San Francisco. Kontaktuje się z nimi ich brat, Alex Yu, który mówi im, że muszą udać się do budynku TranStar. Zakładają mundur TranStar i są zabierani helikopterem do budynku TranStar. Spotykają swojego brata i biorą udział w serii testów, aż Mimic przybiera postać filiżanki kawy doktora Bellamy'ego i zaczyna atakować naukowców. Personel używa gazu, aby uspokoić Morgana, który zostaje znokautowany. <br /> <br />

Morgan budzi się ponownie w swoim mieszkaniu i kontaktuje się z nim za pośrednictwem poczty elektronicznej od operatora naukowego o imieniu January. Mówi Morganowi, że muszą uciec ze swojego mieszkania. W ten sposób trafiają do laboratorium i dowiadują się, że „mieszkanie”, helikopter i laboratorium to tylko seria starannie zamaskowanych zestawów, zaprojektowanych tak, aby uwierzyli, że wciąż są na Ziemi. Gdy Morgan ucieka z laboratorium, są świadkami i walczą z Mimami i Upiorami. Dowiadują się, że są na stacji kosmicznej Talos I, której właścicielem jest TranStar. <br /> <br />

Styczeń kontaktuje się z Morganem i prowadzi ich z powrotem do ich biura, dając im dostęp do większej liczby neuromodów i filmu, w którym poprzednia wersja Morgana wyjaśnia część sytuacji, zanim zostaje nagle przerwany przez ich brata Alexa Yu. Po odcięciu udają się na poszukiwanie gabinetu dr Calvino, aby przywrócić połączenie z serwerami Looking Glass, od którego Alex ich odciął. <br /> <br />

Po powrocie do swojego biura i wznowieniu wideo, Morgan zostaje poinformowany przez swoje dawne ja, że muszą zniszczyć Talosa I, stwierdzając, że nic nie może przetrwać, w tym oni i wszystkie badania, aby zapewnić, że Tyfon nigdy nie dotrze na Ziemię, informując ich o ich dawny plan zniszczenia Talosa I za pomocą pary kluczy uzbrajających do zdetonowania reaktora stacji. W ten sposób Morgan wyrusza w podróż po stacji, aby dotrzeć do Deep Storage i uzyskać dostęp do własnego klucza uzbrojenia, walcząc z Tyfonem i radząc sobie z ocalałymi, których napotkają po drodze.
        </p>
        <h2 className={styles.header}>
          Osobowość i umiejętności
        </h2>
        <p>
          Morgan jest bardzo inteligentną i zaradną osobą, która jest w stanie zhakować różne urządzenia na Talosie I, a nawet przeprogramować Operatora na swoje potrzeby, zanim rozpoczną się testy neuromodów. Zakładając, że neurotomia apto-regresywna nie usuwa pamięci niedeklaratywnej, Morgan wydaje się również biegły w używaniu różnych narzędzi i broni, w tym technologii eksperymentalnej, takiej jak działo GLOO. Morgan wynalazł Psychoskop i przyczynił się do wielu innych projektów w dziedzinie psychotroniki, a nawet nadzorował testy wolontariuszy na żywo. Morgan był dość zimny i profesjonalny, dystansując się od tematów, na których eksperymentowali.
        </p>
    </>
  )
}
