import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Glass = () => {
  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../grafika/lookglass.png')} alt="Schemat" />
        </div>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../grafika/lens.png')} alt="Schemat" />
        </div>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../grafika/lens2.png')} alt="Soczewka" />
        </div>
        <h2 className={styles.header}>
            Omówienie
        </h2>
        <p>
          Soczewka to technologia wynaleziona na Talosie I, opisana w Prey (2017). <br /> <br />
Wymyślone przez Lorenza Calvino, Looking Glass odnosi się zarówno do sprzętu — cienkiej warstwy elektronicznego szkła — jak i do oprogramowania, które je ożywia. Wyświetlacze Looking Glass są holograficzne i mogą symulować głębię w realistycznym stopniu. Technologia jest używana do komunikacji, nagrań osobistych, dekoracji, rozrywki i eksperymentów w laboratoriach symulacyjnych w dziale neuromodów.
        </p>
    </>
  )
}
