import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const AirReg = () => {
  return (
    <>
          <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../grafika/airregulator.png')} alt="Regulator powietrza" />
          </div>
          <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../grafika/brokenReg.png')} alt="Zepsuty regulator powietrza" />
          </div>
          <h2 className={styles.header}>
            Omówienie
          </h2>
        <p>Regulatory mieszanki powietrza są częścią przetwarzania atmosfery na pokładzie Talosa I. Zepsuty regulator mieszanki powietrza można znaleźć i naprawić w ramach zadania Dahl Ultimatum lub Ubezwłasnowolnić Dahla. Plan produkcji tego można znaleźć na ciele Maxa Weigela-Goetza w dziale wspomagania życia.</p>
    </>
  )
}
