import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const DeepStorage = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/deepStorage.gif')} alt="Deep storage" />
        </div>
        <h2 className={styles.header}>
            Omówienie
        </h2>
        <p>
          Deep Storage to lokalizacja opisana w Prey (2017). <br /> <br />
          Deep Storage to centrum serwerowe Talosa I. Zawiera pełne archiwa i bazy danych stacji; wszystkie dane przechodzące przez stację są przetwarzane i przechowywane w Deep Storage.
        </p>
        <div className={styles.imageBox} >
            <img className={styles.image} src={require('../../../../../../prey-graphics/servers.png')} alt="Deep storage" />
        </div>
    </>
  )
}
