import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Psychotronics = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/Psycho.gif')} alt="Psychotronics" />
        </div>
        <h2 className={styles.header}>
            Omówienie
        </h2>
        <p>
          Dział psychotroniczny to obszar Talosa I w Prey (2017). <br /> <br />
          Psychotronika rozpoczęła się jako Kletka (кле́тка, „cell:”, co w języku angielskim może oznaczać albo celę więzienną, albo komórkę biologiczną), sowiecka placówka orbitalna zbudowana w celu powstrzymania inwazji tyfonów na satelicie Vorona-1 i umożliwienia jej badane. Cała stacja kosmiczna Talos I została później zbudowana wokół Kletki.<br /> <br />

Ledwie zmieniona Kletka jest nadal obecna w sercu Psychotroniki, a cyrylica i radziecka ikonografia wciąż można zobaczyć na ścianach wewnętrznego cylindra zawierającego satelitę. Jest to teraz serce badań stacji nad Tyfonem, obejmujące laboratoria do eksperymentów na organizmach Tyfona, obszar wydobycia, w którym „ochotnicy” są zabijani przez Mimików zrodzonych przez Tkacza, aby umożliwić stworzenie egzotycznego materiału, szereg obiektów przygotowawczych, i kostnica stacji, prawdopodobnie w celu przetwarzania zwłok „ochotników” z dala od wścibskich oczu reszty stacji. Większość obszarów jest zablokowana przez drzwi, które otwierają się tylko wtedy, gdy nosi się psychoskop, aby zapobiec wpływowi eksperymentów z udziałem tyfonów telepatów na pracowników.<br /> <br />

Jest to również miejsce pierwotnego naruszenia zabezpieczeń na Talosie I, jak stwierdził styczeń.


        </p>
        <div className={styles.imageBox} >
            <img className={styles.image} src={require('../../../../../../prey-graphics/psycho1.png')} alt="Psychotronics" />
        </div>
    </>
  )
}
