import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const HardwareLabs = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/hardwareLabs.gif')} alt="Pracownie sprzętowe" />
        </div>
        <h2 className={styles.header}>
            Omówienie
        </h2>
        <p>
          Pracownie sprzętowe to skrzydło badawczo-rozwojowe na pokładzie Talosa I w Prey (2017).<br /> <br />
          Laboratoria sprzętowe są źródłem wszystkich nowych postępów technologicznych, broni i gadżetów w Talosie I. W pewnym momencie Morgan Yu pracował tutaj i brał udział w tworzeniu wielu urządzeń.
        </p>
        <div className={styles.imageBox} >
            <img className={styles.image} src={require('../../../../../../prey-graphics/hardwareLabs1.png')} alt="Pracownie sprzętowe" />
        </div>
    </>
  )
}
