import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Kennedy = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "30%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/kennedy.png')} alt="John Kennedy" />
        </div>

        <h2 className={styles.header}>
          John Kennedy
        </h2>
        <p>
          John F. Kennedy (1917-2031) był 35. prezydentem Stanów Zjednoczonych. Był jedną z sił napędowych podróży kosmicznych i wraz z sowieckim przywódcą Nikitą Chruszczowem zbudował stację kosmiczną Kletka.
        </p>

        <h2 className={styles.header}>
          Historia
        </h2>
        <p>
          Kennedy dowiedział się o Typhonie po tym, jak Związek Radziecki ostrzegł Stany Zjednoczone przed ich pierwszym spotkaniem z wrogimi obcymi formami życia. Prezydent zgodził się na nawiązanie wspólnej kolaboracji z Sowietami w powstrzymywaniu zagrożenia Tyfonem, co zaowocowało powstaniem programu Kletka. <br /> <br />

Kennedy przeżył później zamach na swoje życie w Dallas w Teksasie w 1963 roku. Wkrótce potem Kennedy umieścił Kletkę pod absolutną kontrolą Stanów Zjednoczonych i skierował więcej funduszy na amerykański program kosmiczny.<br /> <br />

Kennedy zakończył swoją kadencję jako prezydent, a także dożył wieku 114 lat.


        </p>
    </>
  )
}
