import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const AlexYu = () => {
  return (
    <>
        <div className={styles.imageBox} style={{width: "40%"}}>
            <img className={styles.image} src={require('../../../../../../prey-graphics/alexBanner.png')} alt="Alex Yu" />
        </div>
        <div className={styles.imageBox} >
            <img className={styles.image} src={require('../../../../../../prey-graphics/alexInGame.png')} alt="Alex Yu" />
        </div>
        <h2 className={styles.header}>
          Alex Yu
        </h2>
        <p>Dr Alexander „Alex” Yu jest prezesem i dyrektorem generalnym TranStar oraz starszym bratem Morgan Yu. Jest główną postacią w Prey.
        </p>
        <h2 className={styles.header}>
          Historia
        </h2>
        <h3 className={styles.smallHeader}>
          Tło
        </h3>
        <p>
          Trzy lata przed wydarzeniami z gry Alex nadal żył na Ziemi. W 2025 roku został dyrektorem generalnym TranStar Industries i jest w dużej mierze odpowiedzialny za większość tego, co się wydarzyło lub zostało opracowane na stacji Talos I. W pewnym momencie w przeszłości zadał sobie wiele trudu, aby zatrzymać Morgan Yu, jego bliski brat / siostra, ubezwłasnowolniony przez usunięcie niektórych z pierwszych neuromodów, które zainstalowali, powodując, że zapomnieli o wszystkim, co wydarzyło się po ich zainstalowaniu, i uczynił z nich obiekt testowy dla kilku neuromodów opartych na Typhon Biology. <br /> <br />

W dalszej części gry, jeśli Morgan wejdzie do biura Alexa przedwcześnie, Alex przyznaje, że kiedyś złamał Morganowi rękę, kiedy dowiedział się, że Morgan usunął pliki zapisu jednej ze swoich ulubionych gier. Alex jednak tego żałuje.
        </p>
        <h3 className={styles.smallHeader}>
          Prey (2017)
        </h3>
        <p>
          Pierwsza wzmianka o Alexie w grze to telefon do Morgana bezpośrednio po przebudzeniu. Wezwanie instruuje Morgana, aby założył mundur TranStar i spotkał się z nim w budynku Transtar. Kiedy Morgan przybywa, Alex wita się bezpośrednio przed wejściem do testów, rozmawiając z nimi o testach io tym, jak Morgan wkrótce znajdzie się w kosmosie. Alex jest następnie widziany, gdy Morgan jest uspokojony, a Alex instruuje inny głos, aby „zapewniał mu bezpieczeństwo”. <br /> <br />

Przez większą część gry po htis Alex po prostu obserwuje podróż Morgana, od czasu do czasu kontaktując się z Morganem i January. Zwłaszcza podczas pierwszej wizyty Morgana w Psychotronics i kiedy Alex inicjuje blokadę stacji, aby uwięzić Morgana w serwerowni.<br /><br />

Po ucieczce Morgana z Deep Storage, Alex niechętnie zgodzi się spotkać osobiście i zaoferuje Morganowi swój klucz do uzbrojenia, ale nie wcześniej niż skontaktuje się z Morganem i błaga ich, aby zamiast tego zbudowali specjalne urządzenie Nullwave, które zniszczy Typhona, ale pozostawi stację nietkniętą , powołując się na to, że ich badania są zbyt cenne, by je stracić.<br /><br />

Alexa można zabić od pierwszego wejrzenia, po czym jego Klucz uzbrojenia, kartę dostępu do jego apartamentu i kilka innych zapasów można zabrać z jego ciała. Apex Typhon nadal będzie się pojawiał.<br /><br />

Zanim Alex wręczy klucz uzbrajający, zleca Morganowi zeskanowanie Typhona „Coral” rosnącego wokół stacji i odkrywa, że ​​Typhon budują jakąś sieć neuronową. Jednak ich próby zbadania sieci neuronowej zostają przerwane, gdy zarząd TranStar dowiaduje się o naruszeniu zabezpieczenia i wysyła ekipę sprzątającą, aby wyeliminować zarówno Typhona, jak i jakąkolwiek ocalałą załogę stacji. Po wyeliminowaniu ekipy sprzątającej (Walther Dahl i jego operator, KASPAR), Alex dalej analizuje dane i dochodzi do wniosku, że Typhon wysyła sygnał w przestrzeń kosmiczną, aby coś wezwać. Po tym pojawi się potężny Tyfon i zakłóci działanie systemów Talosa I, gdy ten zacznie otaczać Arboretum.<br /><br />

Morgan ma wtedy możliwość uratowania nieprzytomnego Alexa, przenosząc go do jego bezpiecznego bunkra. Jeśli Alex przeżyje w tym momencie, zostanie uwzględniony w promie ratunkowym (jeśli Morgan zdecydował się przygotować taki prom), ale nie będzie już kontaktował się z Morganem do końca gry.
        </p>
        <h3 className={styles.smallHeader}>
          Prey: Mooncrash
        </h3>
        <p>
          Wysyła wiadomość do Riley, informując ją, że sprawy na Talosie I stają się chaotyczne i że Morgan miał rację co do połączenia Tyfona. Mówi jej również, że może być wśród niej szpieg Kasmy. Następnie Alex mówi Riley, aby zrobiła mentalną kopię siebie na wypadek, gdyby umarła.
        </p>
        <h2 className={styles.header}>
          Osobowość i umiejętności
        </h2>
        <p>
          Alex Yu nazywa specjalny organizm podobny do mchu, który stworzył do kolonizacji Marsa, imieniem swojej matki Catherine, okazując jej przywiązanie. Jego niezachwiana pewność co do swoich poglądów pozwala mu z łatwością manipulować prawie wszystkimi wokół niego. Jest również skłonny do nepotyzmu, gdy ocenia, że ​​jego krewni są zdolni. Jest to pokazane, gdy mianował swojego rodzeństwa Morgana wiceprezesem TranStar i dyrektorem badań nad Talosem I, a swojego kuzyna Rileya administratorem Pytheasa. <br /><br />

Morgan opisuje Alexa jako bardzo dumnego człowieka, który nie chce przyznać się do swoich niepowodzeń, zwłaszcza w odniesieniu do wybuchu tyfona. Alex wydaje się również być nieco arogancki i zbyt pewny siebie, karcąc Sarah Elazar, kiedy narzekała na ich sprzęt i odmawiając zaakceptowania jakości innych pomysłów niż jego.<br /><br />

Mimo to troszczy się o swoją rodzinę, o czym świadczą jego wiadomości do kuzyna Rileya. Obserwował również uważnie Morgana, kiedy zaczęli wykazywać zmiany osobowości, i wyraził raczej żal niż złość po odkryciu ich decyzji o sabotowaniu Talosa I.
        </p>
    </>
  )
}
