import React, { useEffect, useRef } from 'react';
import { NavLink } from 'react-router-dom';

import styles from "./NavigationBar.module.scss"
// import beep from "../../sounds/beep-hover.mp3"
// import click from "../../sounds/click.mp3"

const NavigationBar = () => {

    // const activeStyle = {
    //     ".active": { backgroundColor: "#32b8eb" },
    //     ".active::before": {
    //         content: '""',
    //         width: "80%",
    //         height: "70px",
    //         zIndex: -1,
    //         position: "absolute",
    //         background: "linear-gradient(0.5turn, #363636, #46bde9)",
    //         left: "50%",
    //         top: "-40px",
    //         transform: "translateX(-50%)"
    //     }
    // }

    // const hoverBeep = new Audio(beep);
    // const clickBeep = new Audio(click);
    // const handleHover = () => hoverBeep.play()
    // const handleClick = () => clickBeep.play()
    
    const navElem = useRef();
    const navElem2 = useRef();
    const navElem3 = useRef();
    const navElem4 = useRef();
    const navElem5 = useRef();
    const navElem6 = useRef();

    let navElems = []
    useEffect(()=>{

        window.addEventListener('keydown', keyNavigate)
        // navElem.current.addEventListener('mouseenter', handleHover)
        // navElem2.current.addEventListener('mouseenter', handleHover)
        // navElem3.current.addEventListener('mouseenter', handleHover)
        // navElem4.current.addEventListener('mouseenter', handleHover)
        // navElem5.current.addEventListener('mouseenter', handleHover)
        // navElem6.current.addEventListener('mouseenter', handleHover)

        navElems.push(navElem.current)
        navElems.push(navElem2.current)
        navElems.push(navElem3.current)
        navElems.push(navElem4.current)
        navElems.push(navElem5.current)
        navElems.push(navElem6.current)
        navElems.splice(6,6)

    }, [])

    let pageNum = 0
    const keyNavigate = e => {
        if(e.key === "q"){
            if(pageNum === 0){
                pageNum = 5
            }else{
                pageNum--
            }
        }else if(e.key === "e"){
            if(pageNum === 5){
                pageNum = 0
            }else{
                pageNum++
            }
        }
        navElems[pageNum].click()
    }
    
    return (
        <ul className={styles.list}>
            <div className={styles.pressQ}>
                <div className={styles.smallRect}>
                    <div className={styles.letter}>
                        Q
                    </div>
                </div>
            </div>
            <NavLink 
            // onClick={handleClick}
            to="/" ref={navElem}>
                O GRZE
            </NavLink>
            <NavLink 
            // onClick={handleClick}
            to="/adversaries" ref={navElem2}>
                PRZECIWNICY
            </NavLink>
            <NavLink 
            // onClick={handleClick}
            to="/weapons" ref={navElem3}>
                BRONIE
            </NavLink>
            <NavLink 
            // onClick={handleClick}
            to="/tech" ref={navElem4}>
                TECHNOLOGIA
            </NavLink>
            <NavLink 
            // onClick={handleClick}
            to="/characters" ref={navElem5}>
                POSTACIE
            </NavLink>
            <NavLink 
            // onClick={handleClick}
            to="/locations" ref={navElem6}>
                LOKACJE
            </NavLink>
            <div className={styles.pressE}>
                <div className={styles.smallRect}>
                    <div className={styles.letter}>
                        E
                    </div>
                </div>
            </div>
        </ul>
    );
}

export default NavigationBar;