import React from 'react'
import { Routes, Route } from 'react-router-dom'
import { Talos } from './wikiPageContent/Talos'
import { LocationsLanding } from './wikiPageContent/LocationsLanding'
import { Arboretum } from './wikiPageContent/Arboretum'
import { Cargo } from './wikiPageContent/Cargo'
import { LivingQuar } from './wikiPageContent/LivingQuar'
import { DeepStorage } from './wikiPageContent/DeepStorage'
import { GUTS } from './wikiPageContent/GUTS'
import { HardwareLabs } from './wikiPageContent/HardwareLabs'
import { LifeSupport } from './wikiPageContent/LifeSupport'
import { NeuromodDivision } from './wikiPageContent/NeuromodDivision'
import { Powerplant } from './wikiPageContent/Powerplant'
import { Psychotronics } from './wikiPageContent/Psychotronics'
import { Pytheas } from './wikiPageContent/Pytheas'
import { ShuttleBay } from './wikiPageContent/ShuttleBay'
import { Bridge } from './wikiPageContent/Bridge'
import { Exterior } from './wikiPageContent/Exterior'
import { Lobby } from './wikiPageContent/Lobby'
import { EscapePods } from './wikiPageContent/EscapePods'

const Description = () => {
  return (
    <>
      <Routes>
        <Route path="/">
          <Route index={true} element={<LocationsLanding/>} />
          <Route path="talos_one" element={<Talos/>} />
          <Route path="arboretum" element={<Arboretum/>} />
          <Route path="cargo_bay" element={<Cargo/>} />
          <Route path="crew_quarters" element={<LivingQuar/>} />
          <Route path="deep_storage" element={<DeepStorage/>} />
          <Route path="guts" element={<GUTS/>} />
          <Route path="hardware_labs" element={<HardwareLabs/>} />
          <Route path="life_support" element={<LifeSupport/>} />
          <Route path="neuromod_division" element={<NeuromodDivision/>} />
          <Route path="power_plant" element={<Powerplant/>} />
          <Route path="psychotronics" element={<Psychotronics/>} />
          <Route path="pytheas" element={<Pytheas/>} />
          <Route path="shuttle_bay" element={<ShuttleBay/>} />
          <Route path="talos_one_bridge" element={<Bridge/>} />
          <Route path="talos_one_exterior" element={<Exterior/>} />
          <Route path="lobby" element={<Lobby/>} />
          <Route path="escape_pods" element={<EscapePods/>} />
        </Route>
      </Routes>
    </>
  )
}

export default Description