import React from 'react'
import styles from './wikiPageContentHori.module.scss'

export const Emitter = () => {

  return (
    <>
        <div className={styles.imageBox}>
            <img className={styles.image} src={require('../../../../../../grafika/qbeam.png')} alt="Qbeam"/>
        </div>
        <h2 className={styles.header}>Ogólne</h2>
        <p className={styles.info}> 
          Q-Beam wystrzeliwuje skoncentrowaną wiązkę niestabilnych cząstek, które ostatecznie powodują eksplozję celu. <br />
          TS-QPB-S11 Quasicząsteczkowy Emiter Wiązki, znany również jako Q-Beam, to eksperymentalna broń znaleziona na Talosie I podczas Prey (2017) i na Pyteas podczas Prey: Mooncrash.
          Q-Beam to prototyp ukierunkowanej broni energetycznej. Strumień lotnych kwazicząstek powoduje gwałtownie kaskadowe wzbudzenie w układach energetycznych materii aż do momentu ich zerwania. Każda triada ogniw Q-Beam, których używa jako amunicji, zawiera domyślnie 100 jednostek energii, chociaż liczbę tę można zwiększyć, zwiększając czas strzelania.
        </p>
          <h3 className={styles.smallHeader}>Broń w głównej grze można znaleźć:</h3>
        <p className={styles.info}>
          <ul className={styles.wikiPageList}>
            <li>W laboratorium wiązek i fal w laboratoriach sprzętowych. Jest podłączony do maszyny w celu przetestowania. Skorzystaj z pobliskiego terminala, aby uzyskać do niego dostęp.</li>
            <li>W pobliżu zwłok Josha Daltona w GUTS.</li>
            <li>W zamrażarce w kuchni stołówki w kwaterach załogi.</li>
            <li>W ładowni B na posterunku dowodzenia, gdzie przebywa Sarah Elazar i ocaleni.</li>
            <li>Na zewnątrz stacji, w miejscu oznaczonym jako „ludzkie zwłoki” w pobliżu wyjścia Psychotroniki za wyrwą, którą należy zapieczętować w ramach zadania pobocznego Million Dollar Caulk Gun (zwłoki to Mariana Arias i są ukryte w małej schowku z Q-Beam unoszącym się obok do niej), ale tego pilnuje technopata.</li>
          </ul>
        </p>
        <h2 className={styles.header}>Ulepszenia</h2>
        <p>
          Q-Beam jest „niestandardową bronią techniczną” i dlatego wymaga neuromodów Lab Tech I i II, aby w pełni ulepszyć zestawy do ulepszania broni.
        </p>
        <h2 className={styles.header}>Działanie</h2>
        <p className={styles.info}>
          Q-Beam wystrzeliwuje ciągłą, podobną do lasera wiązkę i nie wyrządza szkód w konwencjonalnym tego słowa znaczeniu. Zamiast tego ciągły ogień szybko zapełni pasek zdrowia istoty na zielono, zaczynając od lewej i przesuwając się w prawo. Jeśli cała czerwona lub niebieska część paska zdrowia zmieni kolor na zielony, wróg natychmiast zginie. Odbywa się to w odniesieniu do już zadanych obrażeń (tj. zraniony wróg nie będzie wymagał uzupełnienia pustej części paska zdrowia) i kumuluje się z konwencjonalnymi obrażeniami: w rzeczywistości zielona część paska zdrowia istoty nie istnieje , a gracz będzie musiał wyczerpać tylko część czerwoną lub niebieską. Jeśli wróg nie zostanie zabity ogniem Q-Beam, zielona część jego paska „zagoi się” po pewnym czasie, potencjalnie pozostawiając go z taką samą ilością zdrowia, jak na początku. Jeśli HUD jest wyłączony, ilość zieleni na pasku zdrowia można oszacować, patrząc na gęstość lub przezroczystość migoczącej zielonej aury, która staje się bardziej widoczna, im więcej kwazicząstek jest wystrzeliwanych w istotę.
          Największą wadą Q-Beam jest to, że ponieważ nie zadaje obrażeń, nie powoduje też żadnego efektu ogłuszenia, a wrogowie mogą swobodnie odpowiedzieć: gorzej, podczas strzelania ruch Morgana jest drastycznie spowolniony. Aby więc był skuteczny, musi łączyć się z oszałamiającym efektem. Bronie, które ogłuszają, to paralizator Disruptor, działo GLOO i nadajnik Nullwave. Działają również zdolności psychiczne, takie jak Psychoshock, który uniemożliwia większości wrogów użycie ataków dystansowych, oraz Mindjack, który sprawia, że ​​wrogowie nie reagują na broń, dopóki nie jest za późno. Jako broń technologiczna zostanie również tymczasowo unieruchomiona przez ataki elementów elektrycznych, takie jak Voltaic Phantom lub Technopath. <br /> <br />

          Promień ma wysoki DPS, gdy jest używany efektywnie, dzięki czemu idealnie nadaje się do eliminowania silniejszych wrogów, którzy wymagają dużej odległości, aby być bezpiecznym, takich jak Telepata, Technopata, Tkacz i Koszmar. <br /> <br />

          Efektywny zasięg to nie więcej niż 30 metrów. „Czas strzelania” wpływa na szybkość, z jaką wiązka Q faktycznie zużywa amunicję, a ostateczne ulepszenie skutecznie zmniejsza o połowę podstawowe zużycie amunicji. Q-Beam Power zapewnia abstrakcyjny pomiar wyjściowych uszkodzeń (ponieważ jest mierzony w kilowatach zamiast uszkodzeń). „Obsługa” to procent normalnej prędkości chodzenia, z jaką Morgan może się poruszać podczas strzelania Q-Beam, i można ją poprawić za pomocą chipsetu „CRU” Optymalizatora ciężkiego sprzętu. <br />

          Amunicja do Q-Beam jest rzadka i przynosi ogromne korzyści z wyposażenia chipsetu CELL REFURB 00Q-X1, który spowoduje upuszczenie około 100 amunicji Q-Beam przez każdego zniszczonego Operatora i Wieżyczkę. Efekt działa wstecz: każdego operatora lub wieżyczkę, które gracz zniszczył przed zdobyciem chipsetu, można ponownie przeszukać, aby znaleźć amunicję Q-Beam. <br /> <br />

          Plan produkcji ogniw Q-Beam (i samego Q-Beam) jest nagrodą za ukończenie pobocznej misji The Blackbox Project.
        </p>
        <p>Wymagane materiały:
          <img className={styles.materialIcon} alt="Mineral icon" src={require('../../../../../../grafika/Mineral.png')} width="15" height="15"></img> 3,
          <img className={styles.materialIcon} alt="Synthetic icon" src={require('../../../../../../grafika/Synthetic.png')} width="15" height="15"></img> 3
        </p>
    </>
  )
}
